import { BigNumber } from 'ethers';
import { parseUnits } from 'ethers/lib/utils';
import React, { useState } from 'react';
import Loader from 'react-spinners/BeatLoader';

import Grid from '@material-ui/core/Grid';

import CustomInputContainer from '../../../components/CustomInputContainer';
import { ValidateNumber } from '../../../components/CustomInputContainer/RegexValidation';
import useWithdraw from '../../../hooks/callbacks/stabilityPool/useWithdraw';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../../theme';
import Button from '../../../theme/components/Button';
import InfoTip from '../../../theme/components/InfoTip';
import Modal from '../../../theme/components/Modal';
import TextWrapper from '../../../theme/components/TextWrapper';
import ToolTipip from '../../../theme/components/Tooltip';
import { formatToBN, getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';

interface Iprops {
  depositedStake: { isLoading: boolean; value: BigNumber };
  openModal: boolean;
  onClose: () => void;
  trove: string;
  unClaimedReward: { isLoading: boolean; value: BigNumber };
}

const StakeArthModal = (props: Iprops) => {
  const { openModal, onClose, trove, depositedStake, unClaimedReward } = props;

  const [arthValue, setARTHValue] = useState<string>('0');
  const chainId = useGetActiveChainId();
  const [isInputFieldError, setIsInputFieldError] = useState<boolean>(false);
  const troveDetail = troveDetails(trove, chainId);
  const [withdrawing, setWithdrawing] = useState<boolean>(false);

  const withdraw = useWithdraw(
    troveDetail.contractPrefix,
    BigNumber.from(parseUnits(arthValue, 18)),
  );

  const handleClose = () => {
    setARTHValue('0');
    onClose();
  };

  const handleWithdraw = () => {
    setWithdrawing(true);
    withdraw(
      () => {
        setWithdrawing(false);
        window.location.reload();
      },
      () => {
        setWithdrawing(false);
      },
    );
  };

  const onValueChange = async (val: string) => {
    if (val === '') {
      setARTHValue('0');
      return;
    }

    const check: boolean = ValidateNumber(val);
    if (!check) return;
    setARTHValue(check ? val : String(Number(val)));
  };

  return (
    <>
      <Modal
        title={'Withdraw ARTH'}
        open={openModal}
        handleClose={() => handleClose()}
        closeButton
      >
        <CustomInputContainer
          isBalanceLoading={depositedStake.isLoading}
          IBalanceValue={getDisplayBalance(BigNumber.from(depositedStake.value), 18)}
          ILabelValue={'How much ARTH would you like to withdraw?'}
          showBalance={false}
          DefaultValue={arthValue}
          LogoSymbol={'ARTH'}
          SymbolText={'ARTH'}
          dontShowBackgroundContainer={true}
          setText={(t) => onValueChange(t)}
          inputMode={'decimal'}
          tagText={'MAX'}
          hasDropDown={false}
          errorCallback={(flag: boolean) => {
            setIsInputFieldError(flag);
          }}
        />
        <div className="single-line-center-between m-t-4 m-b-32">
          <div />
          <div className="single-line-center-between">
            <TextWrapper text={'Staked ARTH Balance'} Fcolor={'#FFFFFF88'} />
            <div className="tags m-l-4">
              {depositedStake.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : (
                <TextWrapper
                  text={`${Number(
                    getDisplayBalance(depositedStake.value, 18, 3),
                  ).toLocaleString()} ARTH`}
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              )}
            </div>
          </div>
        </div>
        {unClaimedReward.value.gt(0) && (
          <div className="single-line-center-between m-t-12 m-b-8">
            <div className="single-line-center-start">
              <TextWrapper text={'Gain you will recieve'} fontSize={12} />
              <ToolTipip
                toolTipText={`This amount is the ${troveDetail.depositToken.toUpperCase()} earned from ${troveDetail.trove.toUpperCase()} liquidation bonuses.`}
              />
            </div>
            <div className="single-line-center-start">
              {unClaimedReward.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : (
                <TextWrapper
                  text={
                    Number(getDisplayBalance(unClaimedReward.value, 18, 3)).toLocaleString() +
                    ' ' +
                    troveDetail.depositToken.toUpperCase()
                  }
                  fontSize={12}
                  Fcolor={theme.color.green[300]}
                />
              )}
            </div>
          </div>
        )}
        {formatToBN(arthValue).gt(depositedStake.value) && (
          <InfoTip
            type={'Error'}
            msg={'Amount cannot be more than your stake amount'}
            className={'m-b-8'}
          />
        )}
        <Grid container spacing={2}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              variant={'transparent'}
              onClick={() => handleClose()}
              tracking_id={unClaimedReward.value.gt(0) ? `withdraw&claim_S_P` : `withdraw_S_P`}
              tracking_params={{
                collateral: troveDetail.trove,
                action: 'cancel',
                amount: Number(arthValue),
              }}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              disabled={
                isInputFieldError ||
                !Number(arthValue) ||
                formatToBN(arthValue).gt(depositedStake.value)
              }
              onClick={handleWithdraw}
              tracking_id={unClaimedReward.value.gt(0) ? `withdraw&claim_S_P` : `withdraw_S_P`}
              loading={withdrawing}
              tracking_params={{
                collateral: troveDetail.trove,
                action: 'confirm',
                amount: Number(arthValue),
              }}
            >
              {unClaimedReward.value.gt(0) ? `Withdraw and Claim` : `Withdraw`}
            </Button>
          </Grid>
        </Grid>
      </Modal>
    </>
  );
};

export default StakeArthModal;
