import React from 'react';

import { Mixpanel } from '../../../analytics/Mixpanel';
import config from '../../../config';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../index';
import { tracking_params } from '../Button';
import TextWrapper, { TextWrapperProps } from '../TextWrapper';

interface IProps {
  onClick: () => void;
  tracking_id?: string;
  tracking_params?: tracking_params;
  disabled?: boolean;
}

const TextButton = (props: IProps & TextWrapperProps) => {
  const { onClick, tracking_id, tracking_params = {}, disabled = false } = props;
  const chainId = useGetActiveChainId();

  const trackMixpanel = () => {
    const params = {
      networkName: config[chainId]?.networkDisplayName || '',
      ...tracking_params,
    };

    if (tracking_id) {
      Mixpanel.track(`buttonClick:${tracking_id.toLowerCase()}`, params);
    }
  };

  return (
    <div
      className={disabled ? 'not-allowed' : 'pointer'}
      onClick={() => {
        if (tracking_id) trackMixpanel();
        if (onClick) onClick();
      }}
    >
      <TextWrapper
        Fcolor={disabled ? theme.color.transparent[200] : theme.color.primary[300]}
        {...props}
      />
    </div>
  );
};

export default TextButton;
