import { BigNumber } from 'ethers';
import { data } from 'jquery';
import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useWallet } from 'use-wallet';

import { AppState } from '../../../state';
import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { troveDetails } from '../../../utils/tokenDetails';
import useCore from '../../useCore';

type Trove = {
  troveDebt: BigNumber;
  troveColl: BigNumber;
  debt: BigNumber;
  coll: BigNumber;
  stake: BigNumber;
  status: BigNumber | number;
  arrayIndex: BigNumber;
};

type State = {
  isLoading: boolean;
  value: Trove;
};

const defaultTrove: Trove = {
  debt: BigNumber.from(0),
  troveColl: BigNumber.from(0),
  troveDebt: BigNumber.from(0),
  coll: BigNumber.from(0),
  stake: BigNumber.from(0),
  status: 0,
  arrayIndex: BigNumber.from(0),
};

export default (trove: string, ownerAddress: string) => {
  const [value, setValue] = useState<State>({
    isLoading: true,
    value: defaultTrove,
  });

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);

  const hasPendingRewards = useSelector((state: AppState) => state.trove.hasPendingRewards);
  const pendingETHReward = useSelector((state: AppState) => state.trove.pendingETHReward);
  const pendingARTHDebtReward = useSelector(
    (state: AppState) => state.trove.pendingARTHDebtReward,
  );

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: defaultTrove });
      return;
    }

    const contract = core.getTroveManager(troveDetail.contractPrefix, activeChainId);
    let value = await contract.Troves(ownerAddress);
    if (hasPendingRewards[troveDetail.depositToken] && ownerAddress === account) {
      const pendingColl = pendingETHReward[troveDetail.depositToken] || BigNumber.from(0);
      const pendingDebt = pendingARTHDebtReward[troveDetail.depositToken] || BigNumber.from(0);
      value = {
        arrayIndex: value.arrayIndex,
        troveColl: value.coll,
        troveDebt: value.debt,
        coll: value.coll.add(pendingColl),
        debt: value.debt.add(pendingDebt),
        frontEndTag: value.frontEndTag,
        stake: value.stake,
        status: value.status,
      };
    } else {
      value = {
        arrayIndex: value.arrayIndex,
        troveColl: value.coll,
        troveDebt: value.debt,
        coll: value.coll,
        debt: value.debt,
        frontEndTag: value.frontEndTag,
        stake: value.stake,
        status: value.status,
      };
    }

    setValue({ isLoading: false, value: value });
  }, [
    account,
    core,
    troveDetail.contractPrefix,
    troveDetail.depositToken,
    activeChainId,
    hasPendingRewards,
    pendingETHReward,
    pendingARTHDebtReward,
    ownerAddress,
  ]);

  useEffect(() => {
    setValue({ isLoading: true, value: defaultTrove });
  }, [trove]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => {
        window.history.back();
        console.error(`Failed to fetch loan: ${err}`);
      });
    }
  }, [
    core.isUnlocked,
    account,
    core.myAccount,
    blockNumber,
    fetchValue,
    activeChainId,
    ownerAddress,
  ]);

  return value;
};
