window.addEventListener(
  'load',
  function (event) {
    var showWarning = document.getElementById('showWarning');
    var header = document.getElementById('WarningMsg');
    var sidebar = document.getElementById('sidebar_container');
    var main_page = document.getElementById('main_page');
    let showWarningBool = false;
    if (showWarning) {
      showWarningBool = showWarning.innerHTML === 'true';
    }

    const DontShow = () => {
      if (header) {
        header.style.display = 'none';
      }
      if (sidebar) {
        sidebar.style.marginTop = 'calc(112px)';
        sidebar.style.minHeight = 'calc(100vh - 144px)';
      }
      if (main_page) {
        main_page.style.minHeight = 'calc(100vh - 112px)';
        main_page.style.paddingTop = 'calc(112px)';
      }
    };

    const Show = () => {
      if (header) {
        header.style.display = 'block';
      }
      if (sidebar) {
        sidebar.style.marginTop = 'calc(112px + 41px)';
        sidebar.style.minHeight = 'calc(100vh - 144px - 41px)';
      }
      if (main_page) {
        main_page.style.minHeight = 'calc(100vh - 112px - 41px)';
        main_page.style.paddingTop = 'calc(112px + 41px)';
      }
    };

    if (showWarningBool) {
      window.addEventListener(
        'scroll',
        function (event) {
          var top = this.scrollY;
          if (top > 180) {
            DontShow();
          } else {
            // Show();
          }
        },
        false,
      );
    } else {
      // DontShow();
    }
  },
  false,
);
