import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import React, { useEffect, useMemo, useState } from 'react';
import styled from 'styled-components';

import theme from '../../../../theme';
import TextWrapper from '../../../../theme/components/TextWrapper';
import moment from "moment";

function getDaysInMonth(year: number, month: number) {
  return new Date(year, month, 0).getDate();
}

function addDayInDate(date: number, noOfDays: number) {
  return Number(new Date(date + ( 3600 * 1000 * 24 * noOfDays)));
}

export default () => {
  const [data1, setData1] = useState<Array<any>>([]);
  const [data2, setData2] = useState<Array<any>>([]);
  const [loading, setLoading] = useState<boolean>(true);

  // const option = {style: 'currency', currency: 'USD'};
  // const numberFormat = new Intl.NumberFormat('en-US', option);

  useEffect(() => {
    setLoading(true);
    const url = `https://api.arthcoin.com/apy/eth/protocol/graph`;
    const headers = {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET,POST,OPTIONS',
      'Access-Control-Allow-Headers':
        'access-control-allow-headers,access-control-allow-methods,access-control-allow-origin,content-type',
    };

    fetch(url, { headers })
      .then((res) => res.json())
      .then((res: any) => {
        setData1(res.protocolPrice);
        setData2(res.ethPrice);
        setLoading(false);
      })
      .catch((err) => {});
  }, []);

  if (loading && data1 === [] && data2 === []) return <div />;

  const ConvertDateTimeToDate = (data: object) => {
    var Obj: any = {};
    for (const [key, value] of Object.entries(data)) {
      const numberDate = Number(key);
      var newKey = new Date(new Date(numberDate).toDateString()).getTime();
      Obj[newKey] = value;
    }
    return Obj;
  };

  const ConvertToNumberArray = (data: any) => {
    return data.map((data: any) => [Number(data[0]), data[1]]);
  };

  const GetKeysWhichAreCommon = (data1: object, data2: object) => {
    const data1Keys = Object.keys(data1).map((key) => Number(key));
    const data2Keys = Object.keys(data2).map((key) => Number(key));
    const filteredArray = data1Keys.filter((value) => data2Keys.includes(value));
    const filteredData1 = Object.entries(data1).filter((data) =>
      filteredArray.includes(Number(data[0])),
    );
    const filteredData2 = Object.entries(data2).filter((data) =>
      filteredArray.includes(Number(data[0])),
    );
    return {
      data1: filteredData1,
      data2: filteredData2,
    };
  };

  const fillInBetweenDataPoints = (data1: Array<any>): Array<any> => {
    let newArray: Array<any> = [];
    Object.entries(data1).map(([key,data],index) => {
      if (index === 0) {
        newArray.push([Number(data[0]), data[1]])
      } else {
        const currentIndexData = data1[index];
        const prevIndexData = data1[index - 1];
        const start = moment(Number(currentIndexData[0]));
        const end = moment(Number(prevIndexData[0]));
        const dateDiff = start.diff(end, 'days');

        if (dateDiff > 1) {
          const arr = Array.from({length: dateDiff - 1}, (v, i) => i);
          arr.forEach((data) => {
            const addingDate = addDayInDate(Number(prevIndexData[0]), data + 1);
            newArray.push([addingDate, prevIndexData[1]]);
          })
        }
        newArray.push([Number(currentIndexData[0]), currentIndexData[1]])
      }
    })
    return newArray;
  };

  const AddTillDateDays = (data: Array<any>): Array<any> => {
    const lastDate = data[data.length - 1][0];
    const lastValue = data[data.length -1][1];
    const newArray = data;
    const today = new Date(new Date().toDateString()).getTime();
    const start = moment(lastDate);
    const end = moment(today);
    const dateDiff = end.diff(start, 'days');
    const arr = Array.from({length: dateDiff}, (v, i) => i);
    arr.forEach((data) => {
      const addingDate = addDayInDate(lastDate, data + 1);
      newArray.push([addingDate, lastValue]);
    })
    return newArray;
  }

  const ProcessedData = useMemo(() => {
    /*
     1. convert array of array to array of object
     2. convert date time to date
     3. get all the common data points and do it
   */

    if (data1.length > 0 && data2.length > 0) {
      const data1Obj = Object.fromEntries(data1);
      const data2Obj = Object.fromEntries(data2);

      const ProcessedData1: object = ConvertDateTimeToDate(data1Obj);
      const ProcessedData2: object = ConvertDateTimeToDate(data2Obj);
      const FilledDataPoints1: Array<any> = fillInBetweenDataPoints(Object.entries(ProcessedData1).reverse());
      const TillDateGMUPrice: Array<any> = AddTillDateDays(FilledDataPoints1);
      const CommonData:
        { data1: Array<any>, data2: Array<any> } = GetKeysWhichAreCommon(Object.fromEntries(TillDateGMUPrice), ProcessedData2);
      const reversed = {
        data1: CommonData.data1.reverse(),
        data2: CommonData.data2.reverse()
      };
      const FilledDataPoints2 = fillInBetweenDataPoints(reversed.data2);

      return {
        data1: TillDateGMUPrice,
        data2: FilledDataPoints2
      };
    } else {
      return {
        data1: data1,
        data2: data2
      }
    }
  }, [data1, data2]);

  const options = useMemo(() => {
    return {
      chart: {
        type: 'line',
        backgroundColor: 'transparent',
      },
      title: {
        text: '',
      },
      tooltip: {
        pointFormat: '<span>{series.name}</span>: <b>${point.y}</b><br/>',
        valueDecimals: 2,
        // split: true
        shared: true,
      },
      xAxis: {
        type: 'datetime',
        labels: {
          // @ts-ignore
          formatter: function () {
            // @ts-ignore
            return Highcharts.dateFormat('%e %b,%Y', this.value);
          },
        },
      },
      yAxis: [
        {
          // Primary yAxis
          labels: {
            format: '${value}',
            style: {
              color: theme.color.transparent[100],
            },
          },
          title: {
            text: 'Protocol Price',
            style: {
              color: theme.color.transparent[100],
            },
          },
          gridLineColor: theme.color.transparent[500],
        },
        {
          // Secondary yAxis
          labels: {
            format: '${value}',
            style: {
              color: theme.color.transparent[100],
            },
          },
          title: {
            text: 'Eth Price',
            style: {
              color: theme.color.transparent[100],
            },
          },
          opposite: true,
          gridLineColor: theme.color.transparent[500],
        },
      ],
      plotOptions: {
        /*series: {
          states: {
            hover: {
              enabled: false
            }
          },
        },*/
        line: {
          marker: {
            enabled: false,
          },
        },
      },
      credits: {
        enabled: false,
      },
      series: [
        {
          name: 'Protocol Price',
          showInLegend: true,
          color: theme.color.primary[300],
          yAxis: 0,
          data: ProcessedData.data1,
          fillColor: {
            linearGradient: [0, 0, 0, 300],
            stops: [
              [0, Highcharts.getOptions().colors[0]],
              [1, Highcharts.color(theme.color.primary[100]).setOpacity(0).get('rgba')],
            ],
          },
        },
        {
          name: 'ETH Price',
          yAxis: 1,
          showInLegend: true,
          color: theme.color.primary[100],
          data: ProcessedData.data2,
        },
      ],
      legend: {
        itemStyle: {
          color: theme.color.transparent[100],
        },
        itemHoverStyle: {
          color: theme.color.transparent[300],
        },
      },
    };
  }, [ProcessedData.data1, ProcessedData.data2]);

  return (
    <div>
      <Card className={'material-primary m-b-12'}>
        <TextWrapper
          text={'Protocol Price vs ETH Price'}
          fontSize={16}
          align={'center'}
          fontWeight={600}
          className={'m-b-4'}
        />
        <TextWrapper
          text={
            'This chart showcases the protocol price against ETH price. The protocol price should appreciate \
          whenever ETH appreciates and should remain stable otherwise'
          }
          align={'center'}
          Fcolor={theme.color.transparent[100]}
          className={'m-b-24'}
        />
        <HighchartsReact highcharts={Highcharts} options={options} />

      </Card>
    </div>
  );
};

const Card = styled.div`
  padding: 24px 16px;
`;
