import { createAction } from '@reduxjs/toolkit';
import { BigNumber } from 'ethers';

export type BalanceOfType = { bal: BigNumber; who: string; token: string };
export const updateBalanceOf = createAction<BalanceOfType>('token/updateBalanceOf');

export type ApprovalOfType = { bal: BigNumber; from: string; to: string };
export const updateApprovalOf = createAction<ApprovalOfType>('token/updateApprovalOf');
