import * as Sentry from '@sentry/react';
import { useCallback } from 'react';

import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import { truncateMiddle } from '../../../utils/formatBalance';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import useCore from '../../useCore';

export default function () {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const activeChainId = useGetActiveChainId();

  return useCallback(
    async (symbol: string, borrower: string, callback?: () => void): Promise<void> => {
      try {
        const contract = core.getTroveManager(symbol, activeChainId);
        console.log('contract', contract.address);
        const response = await contract.liquidate(borrower);

        addTransaction(response, {
          summary: `Liquidate ${truncateMiddle(
            borrower,
            15,
          )}'s Loan on ${symbol.toUpperCase()}`,
        });

        if (response.status !== 1) {
          Sentry.captureException({
            name: 'useLiquidateTrove',
            response,
            symbol,
            borrower,
          });
        }

        if (callback) callback();
      } catch (e) {
        console.log('e', e);
        Sentry.captureException({
          name: 'useLiquidateTrove',
          ...e,
        });
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [core, addTransaction, addPopup, activeChainId],
  );
}
