import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { BasicNumberState } from '../../../utils/interface';
import useCore from '../../useCore';

export default () => {
  const [value, setValue] = useState<BasicNumberState>({ isLoading: true, value: 0 });

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();
  const chainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: 0 });
      return;
    }

    const contract = core.getUpdateOracleContract(chainId);
    const value = await contract.nextUpkeepTime();
    setValue({ isLoading: false, value: Number(`${value}000`) });
  }, [account, core, chainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => {
        console.error(`Failed to fetch getNextUpTime for: ${err}`);
      });
    }
  }, [core.isUnlocked, account, core.myAccount, blockNumber, fetchValue]);

  return value;
};
