import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import useCore from '../useCore';

import useGetARTHInStabilityPool from './stabilityPool/useGetARTHInStabilityPool';
import useARTHCirculatingSupply from './useARTHCirculatingSupply';

type State = {
  isLoading: boolean;
  value: BigNumber;
  percent: BigNumber;
};

export default (contractPrefix: string) => {
  const [value, setValue] = useState<State>({
    isLoading: true,
    value: BigNumber.from(0),
    percent: BigNumber.from(0),
  });

  const core = useCore();
  const { account } = useWallet();
  const arthCirculatingSupply = useARTHCirculatingSupply();
  const arthInStabilityPool = useGetARTHInStabilityPool(contractPrefix);

  const fetchValue = useCallback(async () => {
    if (!account || arthCirculatingSupply.value.lte(0)) {
      setValue({ isLoading: false, value: BigNumber.from(0), percent: BigNumber.from(0) });
      return;
    }

    if (arthCirculatingSupply.isLoading || arthInStabilityPool.isLoading) {
      return;
    }

    const percent = arthInStabilityPool.value
      .mul(BigNumber.from(10).pow(18))
      .div(arthCirculatingSupply.value);
    setValue({ isLoading: false, value: arthInStabilityPool.value, percent: percent });
  }, [account, arthCirculatingSupply, arthInStabilityPool]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch arth in circulating supply with %: ${err}`),
      );
    }
  }, [core.isUnlocked, account, fetchValue]);

  return value;
};
