import { BigNumber } from 'ethers';
import React from 'react';

import Grid from '@material-ui/core/Grid';

import useWithdrawColl from '../../../hooks/callbacks/borrowerOperations/useWithdrawColl';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import Modal from '../../../theme/components/Modal';
import TextWrapper from '../../../theme/components/TextWrapper';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';

interface Iprops {
  trove: string;
  addedValue: BigNumber;
  troveCollateral: BigNumber;
  openModal: boolean;
  cr: BigNumber;
  newCR: BigNumber;
  debtAmount: BigNumber;
  onClose: () => void;
  onSuccess: () => void;
}

const ConfirmRemoveCollateral = (props: Iprops) => {
  const {
    openModal,
    onClose,
    addedValue,
    trove,
    troveCollateral,
    debtAmount,
    cr,
    newCR,
    onSuccess,
  } = props;

  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);

  const withdrawColl = useWithdrawColl(
    troveDetail.contractPrefix,
    addedValue,
    debtAmount,
    troveCollateral.sub(addedValue),
  );

  const handleWithdrawColl = () => {
    withdrawColl(() => {
      onSuccess();
      onClose();
    });
  };

  return (
    <>
      <Modal
        title={'Confirm Remove Collateral'}
        open={openModal}
        handleClose={() => onClose()}
        closeButton
      >
        <div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper
              text={'Withdrawing Collateral amount'}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(getDisplayBalance(addedValue, 18, 3)).toLocaleString()}
                fontWeight={600}
              />
              <div className="tags m-l-4">
                <TextWrapper
                  text={troveDetail.depositToken.toUpperCase()}
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper
              text={'Current Collateral amount'}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(getDisplayBalance(troveCollateral, 18, 3)).toLocaleString()}
                fontWeight={600}
              />
              <div className="tags m-l-4">
                <TextWrapper
                  text={troveDetail.depositToken.toUpperCase()}
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper text={'New Collateral amount'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(
                  getDisplayBalance(troveCollateral.sub(addedValue), 18, 3),
                ).toLocaleString()}
                fontWeight={600}
              />
              <div className="tags m-l-4">
                <TextWrapper
                  text={troveDetail.depositToken.toUpperCase()}
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper
              text={'Current Collateral Ratio'}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="single-line-center-between">
              <TextWrapper
                text={`${Number(getDisplayBalance(cr, 18, 3)).toLocaleString()} %`}
                fontWeight={600}
              />
            </div>
          </div>
          <div className="single-line-center-between m-b-32">
            <TextWrapper text={'New Collateral Ratio'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={`${Number(getDisplayBalance(newCR, 18, 3)).toLocaleString()} %`}
                fontWeight={600}
              />
            </div>
          </div>
          <Grid container spacing={2} className={'m-t-32'}>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                variant={'transparent'}
                onClick={() => onClose()}
                tracking_id="modify_loan_withdraw_collateral"
                tracking_params={{
                  action: 'confirm',
                  collateral: trove,
                  amount: Number(getDisplayBalance(addedValue, 18, 3)),
                }}
              >
                Cancel
              </Button>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                onClick={handleWithdrawColl}
                tracking_id="modify_loan_withdraw_collateral"
                tracking_params={{
                  action: 'confirm',
                  collateral: trove,
                  amount: Number(getDisplayBalance(addedValue, 18, 3)),
                }}
              >
                Confirm Remove Collateral
              </Button>
            </Grid>
          </Grid>
        </div>
      </Modal>
    </>
  );
};

export default ConfirmRemoveCollateral;
