import React from 'react';
import styled from 'styled-components';

import theme from '../../index';
import { BackgroundAbsolute } from '../CustomComponent';
import IconLoader, { IconLoaderProps } from '../IconLoader';
import TextWrapper from '../TextWrapper';

interface CollateralDropDownProps {
  selectedCollateral: IconLoaderProps['iconName'];
  collateralTypes?: IconLoaderProps['iconName'][];
  collateralText?: string;
  showDropDown?: boolean;
  toggleDropDown?: () => void;
  hasDropDown?: boolean;
  ondropDownValueChange?: (value: IconLoaderProps['iconName']) => void;
  border?: string;
}

const CollateralDropDown = (props: CollateralDropDownProps) => {
  const {
    selectedCollateral,
    collateralTypes,
    collateralText = selectedCollateral,
    showDropDown = false,
    toggleDropDown,
    hasDropDown = false,
    ondropDownValueChange,
    border = '0 6px 6px 0',
  } = props;

  return (
    <MainContainer>
      <Container
        style={{ borderRadius: border }}
        className={'single-line-center-start pointer'}
        onClick={() => {
          if (toggleDropDown) toggleDropDown();
        }}
      >
        <IconLoader
          iconName={selectedCollateral}
          iconType={'tokenSymbol'}
          height={20}
          width={20}
        />
        <TextWrapper
          text={collateralText ? collateralText : selectedCollateral}
          fontWeight={600}
          Fcolor={theme.color.transparent[100]}
          className={'m-l-8'}
        />
        {hasDropDown && (
          <IconLoader
            iconName={showDropDown ? 'ArrowUp' : 'ArrowDown'}
            iconType={'arrow'}
            height={16}
            width={16}
            className={'m-l-8'}
          />
        )}
      </Container>
      {showDropDown && (
        <BackgroundAbsolute
          onClick={() => {
            if (toggleDropDown) toggleDropDown();
          }}
        />
      )}

      {hasDropDown && showDropDown && (
        <CustomDropDownContainer>
          {collateralTypes?.map((item, index) => {
            return (
              <CustomDropDownLi
                onClick={() => {
                  if (ondropDownValueChange) ondropDownValueChange(item);
                }}
                key={index}
              >
                <IconLoader iconName={item} iconType={'tokenSymbol'} height={20} width={20} />
                <CustomDropDownLiText>{item}</CustomDropDownLiText>
              </CustomDropDownLi>
            );
          })}
        </CustomDropDownContainer>
      )}
    </MainContainer>
  );
};

export default CollateralDropDown;

const MainContainer = styled.div`
  position: relative;
`;

const Container = styled.div`
  height: 44px;
  padding: 12px;
  background: #1f1e1e;
`;

const CustomDropDownContainer = styled.div`
  position: absolute;
  top: 50px;
  right: 0;
  z-index: 111;
  background: #1f1e1e;
  border-radius: 6px;
  min-width: max-content;
`;

const CustomDropDownLi = styled.div`
  display: flex;
  flex-direction: row;
  height: 48px;
  padding: 0 12px;
  align-items: center;
  cursor: pointer;
  &:hover {
    background: rgba(62, 62, 62, 0.31);
  }
`;

const CustomDropDownLiText = styled.span`
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.64);
  margin-left: 5px;
`;
