import clsx from 'clsx';
import React, { useEffect, useState } from 'react';
import Loader from 'react-spinners/BeatLoader';

import { makeStyles } from '@material-ui/core/styles';
import { DataGrid } from '@material-ui/data-grid';

import { Mixpanel } from '../../../analytics/Mixpanel';
import config from '../../../config';
import '../../../customCss/Custom-Mahadao-Data-Table.css';
import useLiquidateTrove from '../../../hooks/callbacks/troveManager/useLiquidateTrove';
import useGetStaticLinkedListSize from '../../../hooks/state/sortedTroves/useGetStaticLinkedListSize';
import useGetAllTroves from '../../../hooks/state/useGetAllTroves';
import useTokenDecimals from '../../../hooks/useTokenDecimals';
import { PAGINATION_PAGE_SIZE } from '../../../protocol/constants';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import HtmlTooltip from '../../../theme/components/HtmlTooltip';
import IconLoader from '../../../theme/components/IconLoader';
import TextWrapper from '../../../theme/components/TextWrapper';
import { getDisplayBalance, truncateMiddle } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';

export function SortedDescendingIcon() {
  return <IconLoader iconName={'ArrowUp'} iconType="arrow" />;
}

export function SortedAscendingIcon() {
  return <IconLoader iconName={'ArrowDown'} iconType="arrow" />;
}

const useStyles = makeStyles({
  root: {
    '& .super-app-theme--header': {
      cursor: 'pointer',
    },
    '& .super-app.negative': {
      color: '#FA4C69',
      fontWeight: '600',
    },
    '& .super-app.positive': {
      color: '#20C974',
      fontWeight: '600',
    },
    '& .super-app.loss': {
      color: '#FA4C69',
      fontWeight: '600',
    },
    '& .super-app.profit': {
      color: '#20C974',
      fontWeight: '600',
    },
  },
  gridStyle: {
    color: 'white',
  },
});

type Props = {
  selectedTrove: string;
  doRefresh: boolean;
  setDoRefresh: () => void;
};

const AllLoansTable = ({ setDoRefresh, doRefresh, selectedTrove }: Props) => {
  const chainId = useGetActiveChainId();
  const troveDetail = troveDetails(selectedTrove, chainId);
  const [pageNo, setPageNo] = useState<number>(0);
  const noOfSize = useGetStaticLinkedListSize(troveDetail?.contractPrefix, doRefresh);

  const troves = useGetAllTroves(troveDetail?.contractPrefix, pageNo, doRefresh);
  const liquidateTrove = useLiquidateTrove();

  const depositTokenDecimals = useTokenDecimals(troveDetail?.depositToken);

  const classes = useStyles();

  useEffect(() => {
    setPageNo(0);
  }, [selectedTrove]);

  const handleLiquidate = (symbol: string, borrower: string) => {
    liquidateTrove(symbol, borrower, () => setDoRefresh());
  };

  const columns = [
    {
      field: 'owner',
      headerName: 'OWNER',
      flex: 0.23,
      sortable: false,
      renderCell: (params: any) => {
        return (
          <div className="single-line-center-start">
            {truncateMiddle(params.value, 15)}
            <IconLoader
              iconName={'ArrowLink'}
              iconType={'arrow'}
              onClick={() =>
                window.open(`${config[chainId].etherscanUrl}/address/${params.row.owner}`)
              }
            />
          </div>
        );
      },
    },
    {
      field: 'deposit',
      headerName: 'COLLATERAL',
      flex: 0.23,
      sortable: false,
      renderCell: (params: any) => {
        const depositToken = troveDetail.depositToken;
        return (
          <div
            className={'start-center'}
            onClick={() => {
              window.open(`/#/loan/details/${params.row.owner}/${params.row.token}`);
            }}
          >
            {Number(getDisplayBalance(params.value, depositTokenDecimals, 3)).toLocaleString()}{' '}
            {depositToken}
          </div>
        );
      },
    },
    {
      field: 'debt',
      headerName: 'DEBT(ARTH) ',
      flex: 0.23,
      sortable: false,
      renderCell: (params: any) => {
        return (
          <div
            className={'start-center'}
            onClick={() => {
              window.open(`/#/loan/details/${params.row.owner}/${params.row.token}`);
            }}
          >
            {Number(getDisplayBalance(params.value, 18, 3)).toLocaleString()} ARTH
          </div>
        );
      },
      renderHeader: (params: any) => {
        return (
          <div className="single-line-center-start">
            <IconLoader iconName={'ARTH'} iconType="tokenSymbol" width={20} className="m-r-4" />
            {params.field}
          </div>
        );
      },
    },
    {
      field: 'collateralRatio',
      headerName: 'COLLATERAL RATIO',
      flex: 0.23,
      sortable: false,
      sortingOrder: ['asc', 'desc'],
      cellClassName: (params: any) =>
        clsx('super-app', {
          negative: params.row.isLiquidateable,
          positive: !params.row.isLiquidateable,
        }),
      renderCell: (params: any) => {
        return (
          <div
            className={'start-center'}
            onClick={() => {
              window.open(`/#/loan/details/${params.row.owner}/${params.row.token}`);
            }}
          >
            {Number(params.value.toString()).toLocaleString()}%
          </div>
        );
      },
    },
    {
      field: 'delete',
      headerName: 'LIQUIDATE',
      flex: 0.1,
      sortable: false,
      renderCell: (params: any) => {
        return (
          <div
            className={'start-center'}
            style={{ display: 'flex', justifyContent: 'center', width: '100%', zIndex: 100 }}
            onClick={() => {
              if (params.row.isLiquidateable) {
                Mixpanel.track(`buttonClick:all_loans_liquidate`, {
                  networkName: config[chainId]?.networkDisplayName,
                  collateral: params.row.token,
                });
                handleLiquidate(params.row.token, params.row.owner);
              }
            }}
          >
            <HtmlTooltip
              title={
                params.row.isLiquidateable ? 'Liquidate' : 'Collateral ratio not low enough'
              }
            >
              <IconLoader iconName={params.row.isLiquidateable ? 'Delete' : 'DeleteFaded'} />
            </HtmlTooltip>
          </div>
        );
      },
    },
  ];

  const LoadingOverlayComponent = () => {
    return (
      <div className="noResultContainer">
        <div className="internal">
          <Loader color={'#ffffff'} loading={true} size={10} margin={2} />
        </div>
      </div>
    );
  };

  const noResultsComponent = () => {
    return (
      <div className="noResultContainer">
        <div className="internal">
          <TextWrapper
            text={'There are no opens loans yet.'}
            fontSize={24}
            fontWeight={'bold'}
            className="m-b-8"
          />
        </div>
      </div>
    );
  };

  return (
    <React.Fragment>
      <div className={classes.root} style={{ position: 'relative' }}>
        <DataGrid
          className={classes.gridStyle}
          getRowId={(rows) => {
            return rows.id;
          }}
          pagination
          rows={troves.value}
          columns={columns}
          pageSize={PAGINATION_PAGE_SIZE}
          rowCount={Number(noOfSize.value.toString())}
          paginationMode="server"
          disableMultipleSelection={true}
          onRowClick={(newSelection) => {
            // window.open(`/#/loan/details/${newSelection.row.owner}/${newSelection.row.token}`);
          }}
          rowHeight={84}
          loading={troves.isLoading || noOfSize.isLoading}
          onPageChange={(newPage) => {
            setPageNo(Number(newPage.page) || 0);
          }}
          autoHeight={true}
          disableColumnMenu={true}
          components={{
            LoadingOverlay: LoadingOverlayComponent,
            NoRowsOverlay: noResultsComponent,
            ColumnSortedDescendingIcon: SortedDescendingIcon,
            ColumnSortedAscendingIcon: SortedAscendingIcon,
          }}
          disableColumnSelector={true}
          disableSelectionOnClick={true}
        />
      </div>
    </React.Fragment>
  );
};

export default AllLoansTable;
