import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useWallet } from 'use-wallet';

import { AppState } from '../../state';
import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default () => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const systemDebt = useSelector((app: AppState) => app.analytics.entireSystemDebt);
  const { account } = useWallet();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    // const collaterals = core.getCollateralTypes(activeChainId);
    const collaterals = core.getAllTroves(activeChainId);

    const debts = collaterals.map((c: string) => systemDebt[c]);

    if (debts.findIndex((f) => f === undefined) >= 0)
      return setValue({ isLoading: true, value: BigNumber.from(0) });

    setValue({ isLoading: false, value: debts.reduce((a, b) => a.add(b), BigNumber.from(0)) });
  }, [account, core, systemDebt, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch protocol debt %: ${err}`));
    }
  }, [core.isUnlocked, account, fetchValue]);

  return value;
};
