import React, { createContext, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { useWallet } from 'use-wallet';

import config from '../../config';
import BasisCash from '../../protocol';
import { useGetActiveChainId } from '../../state/chains/hooks';

export interface BasisCashContext {
  core: BasisCash;
}

export const Context = createContext<BasisCashContext>({ core: null });

interface IProps {
  children: any;
}

export const BasisCashProvider = (props: IProps) => {
  const { children } = props;
  const chainId = useGetActiveChainId();
  const { ethereum, account } = useWallet();
  const [core, setCore] = useState<BasisCash>();
  const dispatch = useDispatch();

  useEffect(() => {
    if (!core && config) {
      const newCore = new BasisCash(config, chainId);
      if (account) {
        newCore.unlockWallet(ethereum, account, dispatch, chainId);
      }
      setCore(newCore);
    } else if (account) {
      core.unlockWallet(ethereum, account, dispatch, chainId);
    }
  }, [account, core, dispatch, ethereum, chainId]);

  return <Context.Provider value={{ core }}>{children}</Context.Provider>;
};
