import React, { useMemo } from "react";

import { farmingPools } from '../../config/farmingPools';
import useCore from '../../hooks/useCore';
import { useGetActiveChainId } from '../../state/chains/hooks';
import PageHeader from '../../theme/components/PageHeader';

import DataTable from './component/DataTable';
import useGetV3StakingAPR from "../../hooks/state/farming/useGetV3StakingAPR";
import UnclaimedCollateral from "../AddLoan/components/UnclaimedCollateral";
import config from "../../config";

const UniswapV3Gauges = () => {
  const core = useCore();
  const gauges = farmingPools;
  const apr = useGetV3StakingAPR();
  const chainId = useGetActiveChainId();

  const dataWithApr = useMemo(() => {
    return gauges.map(data => {
      var minApr = '-';
      var maxApr = '-';
      if (!apr.isLoading && apr.data[data.apiKey]) {
        minApr = apr.data[data.apiKey].min;
        maxApr = apr.data[data.apiKey].max;
      }
      return { ...data, minApr: minApr, maxApr: maxApr }
    })

  }, [apr.data, apr.isLoading, gauges])


  return (
    <div className={'custom-container'}>
      <PageHeader
        title={'FARMING'}
        subTitle={'Earn rewards by staking your tokens and helping the protocol grow.'}
        className={'m-b-40'}
        showLearnMore
        learnMoreLink="https://docs.mahadao.com/governance/pool-incentives/uniswap-v3-staking"
      />
      <div>
        {(chainId === 56 || chainId === 137) && <div className={"m-b-24"}>
          <UnclaimedCollateral contractPrefix={config[chainId].defaultCollateral} />
        </div>}
        <DataTable data={dataWithApr} apr={apr}/>
        {/*<div className={'m-t-24'}>
          <TextWrapper
            text={<div>
              To register your own pool for gauges and pool incentives, create a governance proposal <a
                href={'https://discuss.mahadao.com/c/governance/gauge-proposals/54'}
                className={'links'}>here</a>
            </div>}
            align={'center'}
          />
        </div>*/}
      </div>
    </div>
  );
};

export default UniswapV3Gauges;
