import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { BNZERO } from '../../../protocol/constants';
import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { BasicState } from '../../../utils/interface';
import useCore from '../../useCore';

export default (contractPrefix: string) => {
  const [value, setValue] = useState<BasicState>({ isLoading: true, value: BNZERO });

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();
  const activeChainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BNZERO });
      return;
    }

    const contract = core.getSortedTroves(contractPrefix, activeChainId);
    const value = await contract.getSize();
    setValue({ isLoading: false, value: value });
  }, [account, core, contractPrefix, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch linked list size: ${err}`));
    }
  }, [core.isUnlocked, blockNumber, account, fetchValue]);

  return value;
};
