import React from 'react';
import styled from 'styled-components';

import { Mixpanel } from '../../analytics/Mixpanel';
import config from '../../config';
import { useGetActiveChainId } from '../../state/chains/hooks';
import theme from '../../theme';
import TextWrapper from '../../theme/components/TextWrapper';

const Footer = () => {
  const chainId = useGetActiveChainId();

  const trackButtons = (value: string) => {
    Mixpanel.track(`buttonClick:footer_${value}`, {
      networkName: config[chainId]?.networkDisplayName || '',
    });
  };

  return (
    <FooterContainer className="single-line-center-center">
      <div
        onClick={() => {
          trackButtons('website');
          window.open('https://mahadao.com');
        }}
        className="p-l-8 p-r-8"
      >
        <TextWrapper
          text={'Website'}
          className="pointer"
          Fcolor={theme.color.transparent[100]}
        />
      </div>
      <div
        onClick={() => {
          trackButtons('twitter');
          window.open('https://twitter.com/TheMahaDAO');
        }}
        className="p-l-8 p-r-8"
      >
        <TextWrapper
          text={'Twitter'}
          className="pointer"
          Fcolor={theme.color.transparent[100]}
        />
      </div>
      <div
        onClick={() => {
          trackButtons('github');
          window.open('https://github.com/mahadao');
        }}
        className="p-l-8 p-r-8"
      >
        <TextWrapper
          text={'Github'}
          className="pointer"
          Fcolor={theme.color.transparent[100]}
        />
      </div>
      <div
        onClick={() => {
          trackButtons('discord');
          window.open('https://discord.com/invite/mahadao');
        }}
        className="p-l-8 p-r-8"
      >
        <TextWrapper
          text={'Discord'}
          className="pointer"
          Fcolor={theme.color.transparent[100]}
        />
      </div>
      <div
        onClick={() => {
          trackButtons('buyMaha');
          window.open(
            'https://app.uniswap.org/#/swap?outputCurrency=0xb4d930279552397bba2ee473229f89ec245bc365',
          );
        }}
        className="p-l-8 p-r-8"
      >
        <TextWrapper
          text={'Buy MAHA'}
          className="pointer"
          Fcolor={theme.color.transparent[100]}
        />
      </div>
    </FooterContainer>
  );
};

export default Footer;

const FooterContainer = styled.div`
  position: absolute;
  padding: 0 16px;
  bottom: 24px;
  flex-wrap: wrap;
`;
