import { BigNumber } from 'ethers';
import React from 'react';

import Grid from '@material-ui/core/Grid';

import useWithdrawARTH from '../../../hooks/callbacks/borrowerOperations/useWithdrawARTH';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import Modal from '../../../theme/components/Modal';
import TextWrapper from '../../../theme/components/TextWrapper';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';

interface Iprops {
  currentDebt: BigNumber;
  addedDebt: BigNumber;
  borrowingFee: BigNumber;
  borrowingFeeRate: BigNumber;
  cr: BigNumber;
  newCR: BigNumber;
  troveCollateral: BigNumber;
  openModal: boolean;
  onClose: () => void;
  trove: string;
  onSuccess: () => void;
}

const ConfirmGenerateARTH = (props: Iprops) => {
  const {
    currentDebt,
    addedDebt,
    borrowingFee,
    borrowingFeeRate,
    cr,
    newCR,
    troveCollateral,
    openModal,
    onClose,
    trove,
    onSuccess,
  } = props;

  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);

  const withdrawARTH = useWithdrawARTH(
    troveDetail.contractPrefix,
    borrowingFeeRate,
    addedDebt.sub(borrowingFee),
    currentDebt.add(addedDebt),
    troveCollateral,
  );

  const handleWithdraw = () => {
    withdrawARTH(() => {
      onSuccess();
      onClose();
    });
  };

  return (
    <>
      <Modal
        title={'Confirm Generate ARTH'}
        open={openModal}
        handleClose={() => onClose()}
        closeButton
      >
        <div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper text={'ARTH minted'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(getDisplayBalance(addedDebt, 18, 3)).toLocaleString()}
                fontWeight={600}
              />
              <div className="tags m-l-4">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper text={'Current debt amount'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(getDisplayBalance(currentDebt, 18, 3)).toLocaleString()}
                fontWeight={600}
              />
              <div className="tags m-l-4">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper text={'New debt amount'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(
                  getDisplayBalance(currentDebt.add(addedDebt), 18, 3),
                ).toLocaleString()}
                fontWeight={600}
              />
              <div className="tags m-l-4">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper text={'One-time fee'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={
                  Number(getDisplayBalance(borrowingFee, 18, 3)).toLocaleString() +
                  ' (' +
                  Number(getDisplayBalance(borrowingFeeRate, 16, 3)).toLocaleString() +
                  '%)'
                }
                fontWeight={600}
              />
              <div className="tags m-l-4">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper
              text={'Current collateral ratio'}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="single-line-center-between">
              <TextWrapper
                text={`${Number(getDisplayBalance(cr, 18, 3)).toLocaleString()} %`}
                fontWeight={600}
              />
            </div>
          </div>
          <div className="single-line-center-between m-b-32">
            <TextWrapper text={'New collateral ratio'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={`${Number(getDisplayBalance(newCR, 18, 3)).toLocaleString()} %`}
                fontWeight={600}
              />
            </div>
          </div>
          <Grid container spacing={2}>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                variant={'transparent'}
                onClick={() => onClose()}
                tracking_id="modify_loan_generate_ARTH"
                tracking_params={{
                  action: 'cancel',
                  collateral: trove,
                  amount: Number(getDisplayBalance(addedDebt, 18, 3)),
                }}
              >
                Cancel
              </Button>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                onClick={handleWithdraw}
                tracking_id="modify_loan_generate_ARTH"
                tracking_params={{
                  action: 'confirm',
                  collateral: trove,
                  amount: Number(getDisplayBalance(addedDebt, 18, 3)),
                }}
              >
                Confirm Generate ARTH
              </Button>
            </Grid>
          </Grid>
        </div>
      </Modal>
    </>
  );
};

export default ConfirmGenerateARTH;
