import * as Sentry from '@sentry/react';
import { useCallback } from 'react';

import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import useCore from '../../useCore';

export default function (contractPrefix: string) {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetActiveChainId();

  return useCallback(
    async (callback?: () => void): Promise<void> => {
      try {
        const contract = core.getBorrowerOperations(contractPrefix, chainId);

        const gasLimit = await contract.estimateGas.closeTrove();
        const response = await contract.closeTrove({ gasLimit });

        addTransaction(response, {
          summary: `Close Loan on ${contractPrefix.toUpperCase()}`,
        });

        if (response.status !== 1) {
          Sentry.captureException({
            name: 'useCloseTrove',
            response,
            contractPrefix,
          });
        }

        if (callback) callback();
      } catch (e) {
        Sentry.captureException({
          ...e,
          name: 'useCloseTrove',
        });
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [core, contractPrefix, chainId, addTransaction, addPopup],
  );
}
