import { BigNumber } from '@ethersproject/bignumber';
import { parseUnits } from 'ethers/lib/utils';
import React, { useEffect, useMemo, useState } from 'react';
import Loader from 'react-spinners/BeatLoader';
import styled from 'styled-components';

import Grid from '@material-ui/core/Grid';

import CustomInputContainer from '../../../components/CustomInputContainer';
import { ValidateNumber } from '../../../components/CustomInputContainer/RegexValidation';
import useApprove, { ApprovalState } from '../../../hooks/callbacks/useApprove';
import useMAHAPrice from '../../../hooks/state/controller/useMAHAPrice';
import useGetStabilityFeeAndToken from '../../../hooks/state/governance/useGetStabilityFee';
import useGetRedemptionFeeRateWithDecay from '../../../hooks/state/troveManager/useGetRedemptionFeeRateWithDecay';
import useCollateralPriceFeed from '../../../hooks/state/useCollateralPriceFeed';
import useGetNativeTokenBalance from '../../../hooks/state/useGetNativeTokenBalance';
import useTokenBalance from '../../../hooks/state/useTokenBalance';
import useCore from '../../../hooks/useCore';
import { DECIMALS_18 } from '../../../protocol/constants';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import IconLoader from '../../../theme/components/IconLoader';
import SuccessModal from '../../../theme/components/SuccesModal';
import TextWrapper from '../../../theme/components/TextWrapper';
import ToolTip from '../../../theme/components/Tooltip';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';
import ConfirmRedeemModal from '../modal/ConfirmRedeemModal';

type Props = {
  trove: string;
};

const RedeemTabContent = ({ trove }: Props) => {
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);

  const [arthValue, setArthValue] = useState<string>('0');
  const [collateralValue, setCollateralValue] = useState<string>('0');
  const [openConfirmationModal, setOpenConfirmationModal] = useState<boolean>(false);

  const [isInputFieldError, setIsInputFieldError] = useState<boolean>(false);
  const [successCollectModal, setSuccessCollectModal] = useState<boolean>(false);

  const core = useCore();
  const chainId = useGetActiveChainId();

  const mahaPrice = useMAHAPrice();
  const arthBalance = useTokenBalance(core.tokens('ARTH', activeChainId));
  const collateralGMUPrice = useCollateralPriceFeed(
    troveDetail.depositToken,
    troveDetail.contractPrefix,
  );
  const stabilityFeeRate = useGetStabilityFeeAndToken(troveDetail.contractPrefix);
  const collateralBalance = useGetNativeTokenBalance();
  const redemptionRate = useGetRedemptionFeeRateWithDecay(troveDetail.depositToken);

  const redemptionFee = useMemo(() => {
    if (redemptionRate.isLoading) return { isLoading: true, value: BigNumber.from(0) };

    return {
      isLoading: false,
      value: BigNumber.from(parseUnits(collateralValue, 18))
        .mul(redemptionRate.value)
        .div(DECIMALS_18),
    };
  }, [redemptionRate, collateralValue]);

  const stabilityFee = useMemo(() => {
    if (stabilityFeeRate.isLoading || mahaPrice.isLoading)
      return { isLoading: true, value: BigNumber.from(0) };

    if (stabilityFeeRate.value.eq(0)) {
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };
    }

    const stabilityFeeInARTH = BigNumber.from(parseUnits(arthValue, 18))
      .mul(stabilityFeeRate.value)
      .div(DECIMALS_18);

    return {
      isLoading: false,
      value: stabilityFeeInARTH.mul(DECIMALS_18).div(mahaPrice.value),
    };
  }, [stabilityFeeRate, mahaPrice, arthValue]);

  const [mahaApprovalStatus, approveMAHA] = useApprove(
    core.tokens('MAHA', activeChainId),
    core.getGovernance(troveDetail.contractPrefix, chainId).address,
  );

  const isMAHAApproved = useMemo(() => mahaApprovalStatus === ApprovalState.APPROVED, [
    mahaApprovalStatus,
  ]);
  const isMAHAApproving = useMemo(() => mahaApprovalStatus === ApprovalState.PENDING, [
    mahaApprovalStatus,
  ]);

  useEffect(() => window.scrollTo(0, 0), []);

  const onCollateralValueChange = async (val: string) => {
    if (val === '' || collateralGMUPrice.isLoading || collateralGMUPrice.value.lte(0)) {
      setCollateralValue('0');
      setArthValue('0');
      return;
    }

    const check: boolean = ValidateNumber(val);
    if (!check) return;
    setCollateralValue(check ? val : String(Number(val)));

    const valueInNumber: number = Number(val);
    if (!valueInNumber) return;

    const bnCollateralAmount = BigNumber.from(parseUnits(`${valueInNumber}`, 18));

    const totalCollateralValue = collateralGMUPrice.value
      .mul(bnCollateralAmount)
      .div(DECIMALS_18);

    setArthValue(getDisplayBalance(totalCollateralValue, 18, 18));
  };

  const onARTHValueChange = async (val: string) => {
    if (val === '' || collateralGMUPrice.isLoading || collateralGMUPrice.value.lte(0)) {
      setCollateralValue('0');
      setArthValue('0');
      return;
    }

    let check: boolean = ValidateNumber(val);
    if (!check) return;
    setArthValue(check ? val : String(Number(val)));

    const valueInNumber: number = Number(val);
    if (!valueInNumber) return;

    const bnArthValue = BigNumber.from(parseUnits(`${valueInNumber}`, 18));

    const finalCollateralValue = bnArthValue.mul(DECIMALS_18).div(collateralGMUPrice.value);

    setCollateralValue(getDisplayBalance(finalCollateralValue, 18, 18));
  };

  const resetFields = () => {
    setArthValue('0');
    setCollateralValue('0');
  };

  return (
    <>
      <LeftTopCard className={'custom-mahadao-container'}>
        <CustomInputContainer
          ILabelValue={`Amount you want to redeem?`}
          IBalanceValue={`${getDisplayBalance(arthBalance.value, 18, 3)}`}
          isBalanceLoading={arthBalance.isLoading}
          ILabelInfoValue={''}
          DefaultValue={arthValue.toString()}
          LogoSymbol={'ARTH'}
          disabled={arthBalance.isLoading}
          hasDropDown={false}
          SymbolText={'ARTH'}
          inputMode={'decimal'}
          setText={onARTHValueChange}
          tagText={'MAX'}
          errorCallback={(flag: boolean) => {
            setIsInputFieldError(flag);
          }}
          tokenDecimals={18}
        />
        <div className="text-center m-t-12 m-b-12">
          <IconLoader iconName={'ArrowTailDown'} iconType={'arrow'} />
        </div>
        <CustomInputContainer
          ILabelValue={'You will receive'}
          IBalanceValue={`${getDisplayBalance(collateralBalance.value, 18, 3)}`}
          isBalanceLoading={collateralBalance.isLoading}
          DefaultValue={collateralValue.toString()}
          LogoSymbol={troveDetail.depositToken}
          setText={onCollateralValueChange}
          disabled={collateralBalance.isLoading || collateralGMUPrice.isLoading}
          SymbolText={troveDetail.depositToken}
          tokenDecimals={18}
        />
        <div className="single-line-center-between m-t-20 m-b-8">
          <div className="single-line-center-start">
            <TextWrapper text={'Redemption fee'} fontSize={12} />
            <ToolTip
              toolTipText={`The redemption fee is charged as a percentage of the redeemed ${troveDetail.depositToken.toUpperCase()}. The redemption fee is set algorithimatically based on demand and starts at 0.5%.`}
            />
          </div>
          <div className="single-line-center-start">
            {redemptionFee.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={Number(getDisplayBalance(redemptionFee.value, 18, 6)).toLocaleString(
                  'en-US',
                  {
                    maximumFractionDigits: 4,
                    minimumFractionDigits: 2,
                  },
                )}
                fontSize={12}
              />
            )}
            <div className="tags m-l-4">
              <TextWrapper text={troveDetail.depositToken.toUpperCase()} fontSize={12} />
            </div>
          </div>
        </div>
        <div className="single-line-center-between m-b-8">
          <div className="single-line-center-start">
            <TextWrapper text={'MAHA fee'} fontSize={12} />
          </div>
          <div className="single-line-center-start">
            {stabilityFee.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={Number(getDisplayBalance(stabilityFee.value, 18, 6)).toLocaleString(
                  'en-US',
                  {
                    maximumFractionDigits: 4,
                    minimumFractionDigits: 2,
                  },
                )}
                fontSize={12}
              />
            )}
            <div className="tags m-l-4">
              <TextWrapper text={'MAHA'} fontSize={12} />
            </div>
          </div>
        </div>
        <div className="m-t-32">
          <Grid container spacing={2}>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                loading={isMAHAApproving}
                disabled={
                  isMAHAApproved ||
                  stabilityFee.value.lte(0) ||
                  !Number(arthValue) ||
                  !Number(collateralValue) ||
                  isInputFieldError
                }
                onClick={approveMAHA}
                tracking_id={isMAHAApproved ? `Redeem_Approved_MAHA` : `Redeem_Approve_MAHA`}
                tracking_params={{ collateral: troveDetail.trove, action: 'confirm' }}
              >
                {isMAHAApproved ? `Approved MAHA` : `Approve MAHA`}
              </Button>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                disabled={
                  !Number(arthValue) ||
                  !Number(collateralValue) ||
                  isInputFieldError ||
                  (stabilityFee.value.gt(0) && !isMAHAApproved)
                }
                onClick={() => setOpenConfirmationModal(true)}
                tracking_id={'Redeem_ARTH'}
                tracking_params={{
                  collateral: troveDetail.trove,
                  action: 'initial',
                  amount: Number(arthValue),
                }}
              >
                Redeem ARTH
              </Button>
            </Grid>
          </Grid>
        </div>
      </LeftTopCard>
      <ConfirmRedeemModal
        redeemFee={redemptionFee.value}
        stabilityFee={stabilityFee.value}
        stabilityFeeRate={stabilityFeeRate.value}
        stabilityFeeToken={'MAHA'}
        arthValue={arthValue}
        trove={trove}
        redemptionRate={redemptionRate.value}
        openModal={openConfirmationModal}
        onClose={() => setOpenConfirmationModal(false)}
        collateralValue={collateralValue}
        onSuccess={() => {
          setSuccessCollectModal(true);
          setOpenConfirmationModal(false);
        }}
      />
      <SuccessModal
        modalOpen={successCollectModal}
        setModalOpen={() => {
          resetFields();
          setSuccessCollectModal(false);
        }}
        title={'Your transaction is being mined on blockchain'}
        subTitle={''}
        buttonText={'Close'}
      />
    </>
  );
};

const LeftTopCard = styled.div`
  padding: 32px;
  @media (max-width: 600px) {
    padding: 32px 16px;
  }
`;

export default RedeemTabContent;
