import { BigNumber } from 'ethers';
import Numeral from 'numeral';
import React, { useMemo } from 'react';
import Loader from 'react-spinners/BeatLoader';

import useGetIsFrontendRegistered from '../../hooks/state/borrowerOperations/useGetIsFrontendRegistered';
import useGetStabilityFeeAndToken from '../../hooks/state/governance/useGetStabilityFee';
import useLinkedListSize from '../../hooks/state/sortedTroves/useGetLinkedListSize';
import useGetBorrowingFeeRateWithDecay from '../../hooks/state/troveManager/useGetBorrowingFeeRateWithDecay';
import useGetEntireSystemCollateral from '../../hooks/state/troveManager/useGetEntireSystemCollateral';
import useGetEntireSystemDebt from '../../hooks/state/troveManager/useGetEntireSystemDebt';
import useGetIsRecoveryMode from '../../hooks/state/troveManager/useGetIsRecoveryMode';
import useGetRedemptionFeeRateWithDecay from '../../hooks/state/troveManager/useGetRedemptionFeeRateWithDecay';
import useGetTotalCollateralRatio from '../../hooks/state/troveManager/useGetTotalCollateralRatio';
import useTotalSupply from '../../hooks/state/useARTHCirculatingSupply';
import useCollateralPriceFeed from '../../hooks/state/useCollateralPriceFeed';
import useGetARTHInStabilityPoolWithPercent from '../../hooks/state/useGetARTHInStabilityPoolWithPercent';
import useGetCollateralWithGMUValue from '../../hooks/state/useGetCollateralWithGMUValue';
import useGetUSDValue from '../../hooks/state/useGetUSDValue';
import useCore from '../../hooks/useCore';
import useTokenDecimals from '../../hooks/useTokenDecimals';
import { DECIMALS_18 } from '../../protocol/constants';
import { useGetActiveChainId } from '../../state/chains/hooks';
import theme from '../../theme';
import DataField from '../../theme/components/DataField';
import TextWrapper from '../../theme/components/TextWrapper';
import ToolTip from '../../theme/components/Tooltip';
import { formatToBN, getDisplayBalance } from '../../utils/formatBalance';
import { troveDetails } from '../../utils/tokenDetails';

type Props = {
  trove: string;
};

const LoanInfo = ({ trove }: Props) => {
  const core = useCore();
  const totalSupply = useTotalSupply();
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);
  const tvl = useGetCollateralWithGMUValue(
    troveDetail.depositToken,
    troveDetail.contractPrefix,
  );
  const isRecoveryMode = useGetIsRecoveryMode(trove);
  const vaultLinkedListSize = useLinkedListSize(troveDetail.contractPrefix);
  const stabiltyFee = useGetStabilityFeeAndToken(troveDetail.contractPrefix);
  const entireSystemDebt = useGetEntireSystemDebt(troveDetail.contractPrefix);
  const collateralGMUPrice = useCollateralPriceFeed(
    troveDetail.depositToken,
    troveDetail.contractPrefix,
  );
  const totalCollteralRatio = useGetTotalCollateralRatio(trove);
  const isFrontEndRegistered = useGetIsFrontendRegistered(troveDetail.contractPrefix);
  const borrowingFeeRate = useGetBorrowingFeeRateWithDecay(troveDetail.contractPrefix);
  const redemptionFeeRate = useGetRedemptionFeeRateWithDecay(troveDetail.contractPrefix);
  const entireSystemCollateral = useGetEntireSystemCollateral(troveDetail.contractPrefix);
  const amountOfARTHInStabilityPool = useGetARTHInStabilityPoolWithPercent(
    troveDetail.contractPrefix,
  );
  const tokenDecimals = useTokenDecimals(troveDetail.depositToken);
  const arthUSDValue = {
    isLoading: false,
    value: formatToBN(2),
  };

  const recoveryModelPriceThreshold = useMemo(() => {
    if (
      collateralGMUPrice.isLoading ||
      entireSystemCollateral.isLoading ||
      entireSystemDebt.isLoading
    )
      return { isLoading: true, value: BigNumber.from(0) };

    if (collateralGMUPrice.value.lte(0) || entireSystemDebt.value.lte(0))
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };

    if (!isRecoveryMode.value) {
      const thresholdCR = DECIMALS_18.mul(troveDetail.crParameters.ccr).div(1000);

      return {
        isLoading: false,
        value: thresholdCR
          .mul(entireSystemDebt.value)
          .div(entireSystemCollateral.value)
          .div(100),
        // .div(BigNumber.from(10).pow(18 - tokenDecimals)),
      };
    }

    const thresholdCR = DECIMALS_18.mul(troveDetail.crParameters.ccr).div(10000);
    return {
      isLoading: false,
      value: thresholdCR.mul(entireSystemDebt.value).div(entireSystemCollateral.value).div(100),
    };
  }, [
    collateralGMUPrice.isLoading,
    collateralGMUPrice.value,
    entireSystemCollateral.isLoading,
    entireSystemCollateral.value,
    entireSystemDebt.isLoading,
    entireSystemDebt.value,
    isRecoveryMode.value,
    troveDetail.crParameters.ccr,
  ]);

  return (
    <div className="material-primary">
      <TextWrapper className={'m-b-12'} text={'Protocol Statistics'} fontWeight={600} />

      <div className={'m-b-8'}>
        <DataField
          label={'TVL'}
          isValueLoading={tvl.isLoading}
          value={`${
            Numeral(getDisplayBalance(tvl.value, 18, 6)).format('0,0.000a') +
            ` ${troveDetail.trove.toUpperCase()}`
          }`}
          valueFontWeight={600}
          className={'m-b-2'}
        />
        <DataField
          isValueLoading={tvl.isLoading || arthUSDValue.isLoading}
          value={`$${Numeral(
            getDisplayBalance(tvl.gmuValue.mul(arthUSDValue.value), 18 * 2, 6),
          ).format('0,0.000')}`}
          valueFontColor={theme.color.transparent[100]}
        />
      </div>

      <div className={'m-b-8'}>
        <DataField
          label={`${troveDetail.trove.toUpperCase()} Price`}
          isValueLoading={collateralGMUPrice.isLoading}
          value={`${
            Numeral(getDisplayBalance(collateralGMUPrice.value, 18, 6)).format('0,0.000') +
            ' ARTH'
          }`}
          valueFontWeight={600}
          className={'m-b-2'}
        />
        <DataField
          isValueLoading={collateralGMUPrice.isLoading || arthUSDValue.isLoading}
          value={`$${Numeral(
            getDisplayBalance(collateralGMUPrice.value.mul(arthUSDValue.value), 18 * 2, 6),
          ).format('0,0.000')}`}
          valueFontColor={theme.color.transparent[100]}
        />
      </div>

      <div className="single-line-center-between m-b-8">
        <div className="single-line-center-between">
          <TextWrapper
            text={`Loans with ${troveDetail.trove.toUpperCase()}`}
            Fcolor={'#FFFFFF88'}
          />
          <ToolTip
            toolTipText={`The total No. of active Loans for ${troveDetail.trove.toUpperCase()} in the Protocol.`}
          />
        </div>
        {vaultLinkedListSize.isLoading ? (
          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
        ) : (
          <TextWrapper
            text={vaultLinkedListSize.value.toString() + ' loans'}
            fontWeight={600}
          />
        )}
      </div>

      <div className="single-line-center-between m-b-8">
        <div className="single-line-center-between">
          <TextWrapper text={'ARTH Supply'} Fcolor={'#FFFFFF88'} />
          <ToolTip toolTipText={`The total ARTH minted by the Protocol.`} />
        </div>
        {totalSupply.isLoading ? (
          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
        ) : (
          <TextWrapper
            text={
              Numeral(getDisplayBalance(totalSupply.value, 18, 6)).format('0,0.000a') + ' ARTH'
            }
            fontWeight={600}
          />
        )}
      </div>

      <div className="single-line-center-between m-b-8">
        <div className="single-line-center-between">
          <TextWrapper text={'ARTH Debt'} Fcolor={'#FFFFFF88'} />
          <ToolTip
            toolTipText={`The total Debt minted by the loans for ${troveDetail.trove.toUpperCase()}.`}
          />
        </div>
        {entireSystemDebt.isLoading ? (
          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
        ) : (
          <TextWrapper
            text={
              Numeral(getDisplayBalance(entireSystemDebt.value, 18, 6)).format('0,0.000a') +
              ' ARTH'
            }
            fontWeight={600}
          />
        )}
      </div>

      <div className="single-line-center-between m-b-8">
        <div className="single-line-center-between">
          <TextWrapper text={'ARTH in Stability Pool'} Fcolor={'#FFFFFF88'} />
          <ToolTip
            toolTipText={`The total ARTH currently held in the Stability Pool, expressed as an amount and a fraction of the ARTH Circulating Supply.`}
          />
        </div>
        {amountOfARTHInStabilityPool.isLoading ? (
          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
        ) : (
          <TextWrapper
            text={
              Numeral(getDisplayBalance(amountOfARTHInStabilityPool.value, 18, 6)).format(
                '0,0.000a',
              ) +
              ' ARTH (' +
              Numeral(getDisplayBalance(amountOfARTHInStabilityPool.percent, 18, 3)).format(
                '0,0.000%',
              ) +
              ')'
            }
            fontWeight={600}
          />
        )}
      </div>

      <div className="single-line-center-between m-b-8">
        <div className="single-line-center-between">
          <TextWrapper text={'Total Collateral Ratio'} Fcolor={'#FFFFFF88'} />
          <ToolTip
            toolTipText={`The ratio of the GMU value of the entire system collateral at the current ARTH/${troveDetail.trove.toUpperCase()} price, to the entire system debt.`}
          />
        </div>
        {totalCollteralRatio.isLoading ? (
          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
        ) : (
          <TextWrapper
            text={
              vaultLinkedListSize.value.gt(0)
                ? Numeral(getDisplayBalance(totalCollteralRatio.value, 18, 6)).format(
                    '0,0.000%',
                  )
                : '0%'
            }
            fontWeight={600}
          />
        )}
      </div>

      <div className="single-line-center-between m-b-8">
        <div className="single-line-center-between">
          <TextWrapper text={'In Recovery Mode?'} Fcolor={'#FFFFFF88'} />
          <ToolTip
            toolTipText={`Recovery Mode is activated when the Total Collateral Ratio (TCR) falls below 150%. When active,
              your loan can be liquidated if its collateral ratio is below the TCR. The maximum collateral
              you can lose from liquidation is capped at 110% of your loan's debt. Operations are also restricted
              that would negatively impact the TCR.`}
          />
        </div>
        {isRecoveryMode.isLoading ? (
          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
        ) : (
          <TextWrapper text={isRecoveryMode.value ? 'Yes' : 'No'} fontWeight={600} />
        )}
      </div>

      <div className={'m-b-32'}>
        <DataField
          label={!isRecoveryMode.value ? 'Recovery Mode Price' : `Normal Mode Price Threshold`}
          isValueLoading={recoveryModelPriceThreshold.isLoading}
          labelToolTip={
            !isRecoveryMode.value
              ? `The GMU value of ${troveDetail.trove.toUpperCase()} below which the Total Collateral Ratio will drop below 150% and the system will enter Recovery Mode.`
              : `The GMU value of ${troveDetail.trove.toUpperCase()} above which the Total Collateral Ratio will go above 150% and the system will enter Normal Mode.`
          }
          value={`~${
            Numeral(getDisplayBalance(recoveryModelPriceThreshold.value, 18, 6)).format(
              '0,0.000',
            ) + ' ARTH'
          }`}
          valueFontWeight={600}
          className={'m-b-2'}
        />
        <DataField
          isValueLoading={recoveryModelPriceThreshold.isLoading || arthUSDValue.isLoading}
          value={`$${Numeral(
            getDisplayBalance(
              recoveryModelPriceThreshold.value.mul(arthUSDValue.value),
              18 * 2,
              6,
            ),
          ).format('0,0.000')}`}
          valueFontColor={theme.color.transparent[100]}
        />
      </div>

      <TextWrapper className={'m-b-12'} text={'Fees'} fontWeight={600} />

      <div className="single-line-center-between m-b-8">
        <div className="single-line-center-between">
          <TextWrapper text={'One-time borrowing fee'} Fcolor={'#FFFFFF88'} />
          <ToolTip
            toolTipText={`The one-time borrowing fee is a one-off fee charged as a percentage of the borrowed amount (in ARTH) and is part of a loan's debt. The fee varies between 0.5% and 5% depending on ARTH redemption volumes.`}
          />
        </div>
        {borrowingFeeRate.isLoading || isRecoveryMode.isLoading ? (
          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
        ) : (
          <TextWrapper
            text={
              isRecoveryMode.value
                ? '0%'
                : Numeral(getDisplayBalance(borrowingFeeRate.value, 18, 6)).format('0,0.000%')
            }
            fontWeight={600}
          />
        )}
      </div>

      <div className="single-line-center-between m-b-8">
        <div className="single-line-center-between">
          <TextWrapper text={'Redemption fee'} Fcolor={'#FFFFFF88'} />
          <ToolTip
            toolTipText={`The redemption fee is charged as a percentage of the redeemed ${troveDetail.depositToken.toUpperCase()}. The redemption fee is set algorithimatically based on demand and starts at 0.5%.`}
          />
        </div>
        {redemptionFeeRate.isLoading ? (
          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
        ) : (
          <TextWrapper
            text={Numeral(getDisplayBalance(redemptionFeeRate.value, 18, 6)).format('0,0.000%')}
            fontWeight={600}
          />
        )}
      </div>

      <div className="single-line-center-between m-b-32">
        <div className="single-line-center-between">
          <TextWrapper text={'MAHA fee'} Fcolor={'#FFFFFF88'} />
          <ToolTip
            toolTipText={`The MAHA fee is an extra fee charged during redemption as a percentage of the redeemed ${troveDetail.trove.toUpperCase()} in MAHA. This fee is controlled by governance.`}
          />
        </div>
        {stabiltyFee.isLoading ? (
          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
        ) : (
          <TextWrapper
            text={Numeral(getDisplayBalance(stabiltyFee.value, 18, 6)).format('0,0.000%')}
            fontWeight={600}
          />
        )}
      </div>

      <TextWrapper text={'Frontend'} fontWeight={600} className="m-b-16" />

      <div className="single-line-center-between m-b-8">
        <div className="single-line-center-between">
          <TextWrapper text={'Share of one-time fee'} Fcolor={'#FFFFFF88'} />
          <ToolTip
            toolTipText={`A rate by the Frontend Operator that determines the fraction of borrowing fee that will be paid out to the Frontend operated by using this Frontend. Remaining share of borrowing fee is paid out to the Ecosystem Fund.`}
          />
        </div>
        {isFrontEndRegistered.isLoading ? (
          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
        ) : (
          <TextWrapper
            text={isFrontEndRegistered.isRegistered ? '50.000%' : '0.000%'}
            fontWeight={600}
          />
        )}
      </div>
    </div>
  );
};

export default LoanInfo;
