import { createAction } from '@reduxjs/toolkit';
import { BigNumber } from 'ethers';

export type DefaultPriceType = { val: BigNumber; collateral: string };

export const updateGMUPrice = createAction<DefaultPriceType>('analytics/updateGMUPrice');

export const getEntireSystemColl = createAction<DefaultPriceType>(
  'analytics/getEntireSystemColl',
);

export const getEntireSystemDebt = createAction<DefaultPriceType>(
  'analytics/getEntireSystemDebt',
);

export const getBorrowingRateWithDecay = createAction<DefaultPriceType>(
  'analytics/getBorrowingRateWithDecay',
);

export const getRedemptionRateWithDecay = createAction<DefaultPriceType>(
  'analytics/getRedemptionRateWithDecay',
);

export const getBorrowingRate = createAction<DefaultPriceType>('analytics/getBorrowingRate');

export const getRedemptionRate = createAction<DefaultPriceType>('analytics/getRedemptionRate');
