import { Contract, ethers } from 'ethers';
import Web3 from 'web3';

import * as tokenState from '../state/token/controller';
import * as troveState from '../state/trove/controller';
import { decimalify } from '../utils/Decimal';
import { Trove } from '../utils/Trove';
import { getDefaultProvider } from '../utils/provider';

import ERC20 from './ERC20';
import Multicall from './Mulitcall';
import {
  Configuration,
  FarmingConfigurations,
  PoolsStaticInfo,
  StrategiesStaticInfo,
  TokeniseStaticInfo,
  TroveStaticInfo,
  configKeys,
} from './config';
import ABIS from './deployments/abi';

/**
 * An API module of ARTH contracts.
 * All contract-interacting domain logic should be defined in here.
 */
export class Protocol {
  myAccount: string;

  web3: Web3;

  _signer?: ethers.Signer;

  _config: {
    [chainId: number]: Configuration;
  };

  _contracts: {
    [chainId: number]: { [name: string]: Contract };
  };

  provider: ethers.providers.BaseProvider;

  _tokens: {
    [chainId: number]: { [name: string]: ERC20 };
  };

  tradingPairs: {
    [name: string]: ERC20[];
  };

  assetLockContracts: {
    [name: string]: Array<Contract | ERC20>;
  };

  multicall: { [chainId: number]: Multicall };

  _activeNetwork: number;

  constructor(cfg: { [chainId: number]: Configuration }, chainId: number) {
    this._activeNetwork = chainId;
    this._contracts = {
      56: {},
      137: {},
    };
    this._tokens = {
      56: {},
      137: {},
    };
    this._tokens = {
      56: {},
      137: {},
    };

    // @ts-ignore
    this.multicall = { 56: {}, 137: {} };
    for (const [chainIdString, config] of Object.entries(cfg)) {
      const chainId = Number(chainIdString);
      const { deployments } = config;
      this.provider = getDefaultProvider(config);
      const networkConfig: { [name: string]: Contract } = {};
      const tokens: { [name: string]: ERC20 } = {};

      for (const [name, deployment] of Object.entries(deployments)) {
        if (!deployment.abi) continue;
        //to push all erc20 tokens in tokens array
        if (cfg[chainId].supportedTokens.includes(name)) {
          tokens[name] = new ERC20(
            deployments[name].address,
            this.provider,
            name,
            cfg[chainId].decimalOverrides[name] || 18,
          );
        }

        //to push all others as contracts
        networkConfig[name] = new Contract(
          deployment.address,
          ABIS[deployment.abi],
          this.provider,
        );
      }
      this._contracts[chainId] = networkConfig;
      this._tokens[chainId] = tokens;
      this.multicall[chainId] = new Multicall(
        cfg[chainId].defaultProvider,
        deployments.Multicall.address,
      );
    }

    this._config = cfg;
  }

  /**
   * @param provider From an unlocked wallet. (e.g. Metamask)
   * @param account An address of unlocked wallet account.
   */
  unlockWallet(provider: any, account: string, dispatch: any, chainId: number) {
    // @ts-ignore
    const newProvider = new ethers.providers.Web3Provider(provider);
    this.config('chainId', this._activeNetwork);
    this.web3 = new Web3(provider);
    this.provider = newProvider;
    this._signer = newProvider.getSigner(0);
    this.myAccount = account;

    for (const [chainId, contracts] of Object.entries(this._contracts)) {
      for (const [name, contract] of Object.entries(contracts)) {
        this._contracts[Number(chainId)][name] = contract.connect(this._signer);
      }
    }

    for (const tokens of Object.values(this._tokens)) {
      for (const token of Object.values(tokens)) {
        if (token && token.address) token.connect(this._signer);
      }
    }

    tokenState.initUser(this, dispatch, chainId);
    troveState.initUser(this, dispatch, chainId);
  }

  get isUnlocked(): boolean {
    return !!this.myAccount;
  }

  getContractAt = (abiName: string, address: string) => {
    const contract = new Contract(address, ABIS[abiName], this.provider);
    return contract.connect(this._signer);
  };

  signer = () => {
    return this._signer;
  };

  getBlockTimestamp = () => {
    return Math.floor(Date.now() / 1000);
  };

  getAllTroves = (chainId: number, account?: string): string[] => {
    // @ts-ignore
    const data: TroveStaticInfo = this.config('troveStaticInfo', chainId);
    return [data.contractPrefix];
    /*const filteredData = data.filter((data) => {
      if (!data.isDeprecated) {
        return data;
      } else {
        // if (this.isUserWhiteListed(chainId, account)) {
        return data;
        // }
      }
    });
    if (account) {
      return filteredData.map((value) => value.contractPrefix);
    } else {
      return data.map((value) => value.contractPrefix);
    }*/
  };

  getAllStrategies = (chainId: number, account?: string): StrategiesStaticInfo[] => {
    // @ts-ignore
    const data: Array<StrategiesStaticInfo> = this.config('strategiesStaticInfo', chainId);
    return data;
  };

  getAllTokeniseInfo = (chainId: number): Array<TokeniseStaticInfo> => {
    // @ts-ignore
    const data: Array<TokeniseStaticInfo> = this.config('tokeniseStaticInfo', chainId);
    return data;
  };

  getTradingCollateralTypes = (chainId: number): string[] => {
    // @ts-ignore
    return this.config('tradingCollaterals', chainId);
  };

  getPool = (chainId: number): PoolsStaticInfo => {
    // @ts-ignore
    return this.config('poolsStaticInfo', chainId);
  };

  getLiquidityPools = (chainId: number): PoolsStaticInfo[] => {
    // @ts-ignore
    return this.config('liquidityPoolsStaticInfo', chainId);
  };

  getFarmingPools = (chainId: number): FarmingConfigurations[] => {
    // @ts-ignore
    return this.config('farmingConfigs', chainId);
  };

  getFees = (chainId: number): FarmingConfigurations[] => {
    // @ts-ignore
    return this.config('farmingConfigs', chainId);
  };

  getTotal = async (contractPrefix: string, chainId: number): Promise<Trove> => {
    const [activePool, defaultPool] = await Promise.all([
      this.getActivePoolTrove(contractPrefix, chainId),
      this.getDefaultPoolTrove(contractPrefix, chainId),
    ]);

    return activePool.add(defaultPool);
  };

  getActivePoolTrove = async (contractPrefix: string, chainId: number): Promise<Trove> => {
    const activePool = this.getActivePool(contractPrefix, chainId);

    const [activeCollateral, activeDebt] = await Promise.all(
      [activePool.getETH(), activePool.getARTHDebt()].map((getBigNumber) =>
        getBigNumber.then(decimalify),
      ),
    );

    return new Trove(activeCollateral, activeDebt);
  };

  async getDefaultPoolTrove(contractPrefix: string, chainId: number): Promise<Trove> {
    const defaultPool = this.getDefaultPool(contractPrefix, chainId);

    const [liquidatedCollateral, closedDebt] = await Promise.all(
      [defaultPool.getETH(), defaultPool.getARTHDebt()].map((getBigNumber) =>
        getBigNumber.then(decimalify),
      ),
    );

    return new Trove(liquidatedCollateral, closedDebt);
  }

  /* gasOptions(gas: BigNumber): Overrides {
    // @ts-ignore
    const multiplied = Math.floor(gas.toNumber() * this.config('gasLimitMultiplier'));
    return {
      gasLimit: BigNumber.from(multiplied),
    };
  }*/

  updateActiveNetwork(chainId: number, dispatch: any) {
    this._activeNetwork = chainId;
    tokenState.initUser(this, dispatch, chainId);
    troveState.initUser(this, dispatch, chainId);
  }

  tokens(id: string, chainId: number): ERC20 {
    return this._tokens[chainId][id];
  }

  config(id: configKeys, chainId: number) {
    return this._config[chainId][id];
  }

  contracts(name: string, chainId: number) {
    return this._contracts[chainId][name];
  }

  getCRVTradeTokenIndex(token: string, chainId: number): number {
    return this._config[chainId]['crvTradeTokenIndexMapping'][token] || 0;
  }

  getMultiTroveGetter(contractPrefix: string, chainId: number) {
    return this.contracts(`${contractPrefix}MultiTroveGetter`, chainId);
    // return this.contracts[`${token}MultiTroveGetter`];
  }

  getGovernance(contractPrefix: string, chainId: number) {
    return this.contracts(`${contractPrefix}Governance`, chainId);
  }

  getLeverageAccountRegistry(chainId: number) {
    return this.contracts(`LeverageAccountRegistry`, chainId);
  }

  getHintHelper(contractPrefix: string, chainId: number) {
    return this.contracts(`${contractPrefix}HintHelpers`, chainId);
  }

  getTroveManager(contractPrefix: string, chainId: number) {
    return this.contracts(`${contractPrefix}TroveManager`, chainId);
  }

  getBorrowerOperations(token: string, chainId: number) {
    return this.contracts(`${token}BorrowerOperations`, chainId);
  }

  getCollSurplusPool(token: string, chainId: number) {
    return this.contracts(`${token}CollSurplusPool`, chainId);
  }

  getPriceFeed(contractPrefix: string, chainId: number) {
    return this.contracts(`${contractPrefix}PriceFeed`, chainId);
  }

  getActivePool(contractPrefix: string, chainId: number) {
    return this.contracts(`${contractPrefix}ActivePool`, chainId);
  }

  getDefaultPool(contractPrefix: string, chainId: number) {
    return this.contracts(`${contractPrefix}DefaultPool`, chainId);
  }

  getSortedTroves(contractPrefix: string, chainId: number) {
    return this.contracts(`${contractPrefix}SortedTroves`, chainId);
  }

  getStabilityPool(contractPrefix: string, chainId: number) {
    return this.contracts(`${contractPrefix}StabilityPool`, chainId);
  }

  getUpdateOracleContract(chainId: number) {
    return this.contracts(`MAHAKeeper`, chainId);
  }

  getController(contractPrefix: string, chainId: number) {
    if (chainId === 56 || chainId === 97) {
      return null;
    } else {
      return this.contracts(`${contractPrefix}Controller`, chainId);
    }
  }
}
