import { createReducer } from '@reduxjs/toolkit';
import { BigNumber } from 'ethers';

import * as Actions from './actions';

export interface AnalyticsState {
  hasPendingRewards: {
    [collateral: string]: boolean;
  };
  pendingARTHDebtReward: {
    [collateral: string]: BigNumber;
  };
  pendingETHReward: {
    [collateral: string]: BigNumber;
  };
}

export const initialState: AnalyticsState = {
  hasPendingRewards: {},
  pendingARTHDebtReward: {},
  pendingETHReward: {},
};

export default createReducer(initialState, (builder) =>
  builder
    .addCase(
      Actions.getPendingARTHDebtReward,
      (t, { payload }: { payload: Actions.DefaultBNType }) => {
        t.pendingARTHDebtReward = t.pendingARTHDebtReward || {};
        t.pendingARTHDebtReward[payload.collateral] = payload.val;
      },
    )
    .addCase(
      Actions.getPendingETHReward,
      (t, { payload }: { payload: Actions.DefaultBNType }) => {
        t.pendingETHReward = t.pendingETHReward || {};
        t.pendingETHReward[payload.collateral] = payload.val;
      },
    )
    .addCase(
      Actions.hasPendingRewards,
      (t, { payload }: { payload: Actions.DefaultBoolType }) => {
        t.hasPendingRewards = t.hasPendingRewards || {};
        t.hasPendingRewards[payload.collateral] = payload.val;
      },
    ),
);
