import { BigNumber } from 'ethers';
import { parseUnits } from 'ethers/lib/utils';
import React, { useMemo, useState } from 'react';
import Loader from 'react-spinners/BeatLoader';
import { useWallet } from 'use-wallet';

import CustomInputContainer from '../../components/CustomInputContainer';
import { ValidateNumber } from '../../components/CustomInputContainer/RegexValidation';
import DepositModal from '../../components/DepositModal';
import useGetIsRecoveryMode from '../../hooks/state/troveManager/useGetIsRecoveryMode';
import useGetTrove from '../../hooks/state/troveManager/useGetTrove';
import useCollateralPriceFeed from '../../hooks/state/useCollateralPriceFeed';
import useGetNativeTokenBalance from '../../hooks/state/useGetNativeTokenBalance';
import { BNZERO, DECIMALS_18 } from '../../protocol/constants';
import { useGetActiveChainId } from '../../state/chains/hooks';
import theme from '../../theme';
import Button from '../../theme/components/Button';
import InfoTip from '../../theme/components/InfoTip';
import SuccessModal from '../../theme/components/SuccesModal';
import TextWrapper from '../../theme/components/TextWrapper';
import ToolTip from '../../theme/components/Tooltip';
import { crParameters, getDisplaycrParameters } from '../../utils/crParameters';
import { formatToBN, getDisplayBalance } from '../../utils/formatBalance';
import { troveDetails } from '../../utils/tokenDetails';

import ConfirmAddCollateral from './modal/ConfirmAddCollateral';
import ConfirmRemoveCollateral from './modal/ConfirmRemoveCollateral';

interface Iprops {
  trove: string;
}

const CollateralModifyLoan = (props: Iprops) => {
  const { trove } = props;

  const [depositModal, setdepositModal] = useState<boolean>(false);
  const [addCollateralValue, setAddCollateralValue] = useState<string>('0');
  const [isInputFieldError, setIsInputFieldError] = useState<boolean>(false);
  const [withdrawCollateralValue, setWithdrawCollateralValue] = useState<string>('0');
  const [openAddConfirmationModal, setOpenAddConfirmationModal] = useState<boolean>(false);
  const [isWithdrawInputFieldError, setWithdrawIsInputFieldError] = useState<boolean>(false);
  const [openRemoveConfirmationModal, setOpenRemoveConfirmationModal] = useState<boolean>(
    false,
  );
  const [openSuccessModal, setOpenSuccessModal] = useState<boolean>(false);

  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);
  const { account } = useWallet();
  const getTrove = useGetTrove(trove, account);
  const collateralGMUPrice = useCollateralPriceFeed(
    troveDetail.depositToken,
    troveDetail.contractPrefix,
  );
  const collateralBalance = useGetNativeTokenBalance();
  const isRecoveryMode = useGetIsRecoveryMode(troveDetail.contractPrefix);
  const collateralPrice = useCollateralPriceFeed(
    troveDetail.depositToken,
    troveDetail.contractPrefix,
  );

  const collateralRatio = useMemo(() => {
    if (collateralGMUPrice.isLoading || getTrove.isLoading)
      return { isLoading: true, value: BigNumber.from(0) };

    if (getTrove.value.debt.lte(0))
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };

    const ratio = getTrove.value.coll
      .mul(collateralGMUPrice.value)
      .mul(100)
      .div(getTrove.value.debt);
    return {
      isLoading: false,
      value: ratio,
    };
  }, [getTrove, collateralGMUPrice]);

  const modifiedCollatreal = useMemo(() => {
    if (collateralGMUPrice.isLoading || getTrove.isLoading)
      return { isLoading: true, value: BigNumber.from(0) };

    if (getTrove.value.debt.lte(0) || getTrove.value.coll.lte(0))
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };

    const addCollateralBN = BigNumber.from(parseUnits(addCollateralValue, 18));
    const removeCollateralBN = BigNumber.from(parseUnits(withdrawCollateralValue, 18));
    if (removeCollateralBN.gt(getTrove.value.coll)) {
      return { isLoading: false, value: BigNumber.from(0) };
    }

    const bnCollateral = getTrove.value.coll.add(addCollateralBN).sub(removeCollateralBN);
    const ratio = bnCollateral.mul(collateralGMUPrice.value).mul(100).div(getTrove.value.debt);
    return {
      isLoading: false,
      value: ratio,
    };
  }, [getTrove, addCollateralValue, withdrawCollateralValue, collateralGMUPrice]);

  const isModifiedCollateralRatioBelowMin = useMemo(() => {
    if (isRecoveryMode.isLoading || modifiedCollatreal.isLoading) return false;
    const crData = crParameters(trove, activeChainId);
    return !isRecoveryMode.value
      ? modifiedCollatreal.value.lte(DECIMALS_18.mul(crData.mcr).div(1000))
      : modifiedCollatreal.value.lte(DECIMALS_18.mul(crData.ccr).div(1000));
  }, [
    isRecoveryMode.isLoading,
    isRecoveryMode.value,
    modifiedCollatreal.isLoading,
    modifiedCollatreal.value,
    trove,
    activeChainId,
  ]);

  const isCollateralRatioBelowMin = useMemo(() => {
    if (isRecoveryMode.isLoading || collateralRatio.isLoading) return false;
    const crData = crParameters(trove, activeChainId);
    return !isRecoveryMode.value
      ? collateralRatio.value.lte(DECIMALS_18.mul(crData.mcr).div(1000))
      : collateralRatio.value.lte(DECIMALS_18.mul(crData.ccr).div(1000));
  }, [
    activeChainId,
    collateralRatio.isLoading,
    collateralRatio.value,
    isRecoveryMode.isLoading,
    isRecoveryMode.value,
    trove,
  ]);

  const getApproveAndAddCollButton = () => {
    return (
      <Button
        disabled={!Number(addCollateralValue) || isInputFieldError}
        onClick={() => setOpenAddConfirmationModal(true)}
        tracking_id="modify_loan_add_collateral"
        tracking_params={{
          action: 'initial',
          collateral: trove,
          amount: Number(addCollateralValue),
        }}
      >
        Add collateral
      </Button>
    );
  };

  const onAddCollateralChange = (val: string) => {
    if (val === '' || collateralGMUPrice.value.lte(0) || collateralGMUPrice.isLoading) {
      setAddCollateralValue('0');
      return;
    }

    const check: boolean = ValidateNumber(val);
    if (!check) return;
    setWithdrawCollateralValue('0');
    setAddCollateralValue(check ? val : String(Number(val)));
  };

  const onWithdrawCollateralChange = (val: string) => {
    if (val === '' || collateralGMUPrice.value.lte(0) || collateralGMUPrice.isLoading) {
      setWithdrawCollateralValue('0');
      return;
    }

    const check: boolean = ValidateNumber(val);
    if (!check) return;
    setAddCollateralValue('0');
    setWithdrawCollateralValue(check ? val : String(Number(val)));
  };

  const resetFields = () => {
    setAddCollateralValue('0');
    setWithdrawCollateralValue('0');
  };

  const avail_to_withdraw = useMemo(() => {
    if (getTrove.isLoading || collateralPrice.isLoading || isRecoveryMode.isLoading)
      return {
        isLoading: true,
        value: BNZERO,
      };
    const expectedCollateral = BigNumber.from(110)
      .mul(getTrove.value.troveDebt)
      .mul(DECIMALS_18)
      .div(collateralPrice.value)
      .div(100);

    const data = isRecoveryMode.value
      ? BigNumber.from(0)
      : expectedCollateral.lt(getTrove.value.troveColl)
      ? getTrove.value.troveColl.sub(expectedCollateral)
      : BigNumber.from(0);

    return {
      isLoading: false,
      value: data,
    };
  }, [
    collateralPrice.isLoading,
    collateralPrice.value,
    getTrove.isLoading,
    getTrove.value.troveColl,
    getTrove.value.troveDebt,
    isRecoveryMode,
  ]);

  const isAmountMoreThanMaxWithdrawal = useMemo(() => {
    const withdrawCollateralBN = formatToBN(withdrawCollateralValue, 18);
    return withdrawCollateralBN.gt(avail_to_withdraw.value);
  }, [withdrawCollateralValue, avail_to_withdraw.value]);

  return (
    <div>
      <ConfirmAddCollateral
        cr={collateralRatio.value}
        newCR={modifiedCollatreal.value}
        debtAmount={getTrove.value.debt}
        addedValue={BigNumber.from(parseUnits(addCollateralValue, 18))}
        troveCollateral={getTrove.value.coll}
        trove={trove}
        openModal={openAddConfirmationModal}
        onClose={() => setOpenAddConfirmationModal(false)}
        onSuccess={() => {
          setOpenAddConfirmationModal(false);
          setOpenSuccessModal(true);
        }}
      />
      <ConfirmRemoveCollateral
        cr={collateralRatio.value}
        newCR={modifiedCollatreal.value}
        debtAmount={getTrove.value.debt}
        addedValue={BigNumber.from(parseUnits(withdrawCollateralValue, 18))}
        troveCollateral={getTrove.value.coll}
        trove={trove}
        openModal={openRemoveConfirmationModal}
        onClose={() => setOpenRemoveConfirmationModal(false)}
        onSuccess={() => {
          setOpenRemoveConfirmationModal(false);
          setOpenSuccessModal(true);
        }}
      />
      <SuccessModal
        modalOpen={openSuccessModal}
        setModalOpen={() => {
          resetFields();
          setOpenSuccessModal(false);
        }}
        title={'Your transaction is being mined on blockchain'}
        buttonText={'Close'}
      />
      {isCollateralRatioBelowMin && (
        <div className="m-b-24">
          <InfoTip
            type={'Warning'}
            msg={
              !isRecoveryMode.value
                ? `Collateral ratio is below ${
                    getDisplaycrParameters(trove, activeChainId).mcr
                  }% and can be liquidated.
                 Add ${trove.toUpperCase()} or decrease debt by repaying some of the borrowed ARTH.`
                : `System is currently in Recovery Mode. Collateral ratio is below
                ${
                  getDisplaycrParameters(trove, activeChainId).ccr
                }% and can be liquidated. Add ${troveDetail.depositToken.toUpperCase()}
                or decrease debt by repaying some of the borrowed ARTH.`
            }
          />
        </div>
      )}
      {isModifiedCollateralRatioBelowMin &&
      (Number(addCollateralValue) || Number(withdrawCollateralValue)) ? (
        <div className="m-b-24">
          <InfoTip
            type={'Warning'}
            msg={
              !isRecoveryMode.value
                ? `The modified loan has Collateral ratio is below
                  ${getDisplaycrParameters(trove, activeChainId).mcr}% and can be liquidated.
                  Add ${trove.toUpperCase()} or decrease debt by repaying some of the borrowed ARTH.`
                : `System is currently in Recovery Mode. The modified loan has Collateral ratio is below
                  ${
                    getDisplaycrParameters(trove, activeChainId).ccr
                  }% and can be liquidated. Add
                  ${troveDetail.depositToken.toUpperCase()} or decrease debt by repaying some of the borrowed ARTH.`
            }
          />
        </div>
      ) : null}
      <div className="p-b-32 bottom-divider">
        <CustomInputContainer
          ILabelValue={'Add Collateral'}
          IBalanceValue={`${getDisplayBalance(collateralBalance.value, 18, 3)}`}
          isBalanceLoading={collateralBalance.isLoading}
          ILabelInfoValue={''}
          disabled={collateralBalance.isLoading || getTrove.isLoading}
          DefaultValue={addCollateralValue.toString()}
          LogoSymbol={troveDetail.depositToken}
          hasDropDown={false}
          SymbolText={troveDetail.depositToken}
          inputMode={'decimal'}
          setText={(val: string) => {
            onAddCollateralChange(val);
          }}
          tagText={'MAX'}
          tokenDecimals={18}
          errorCallback={(flag: boolean) => {
            setIsInputFieldError(flag);
          }}
        />
        <div className="m-t-32">
          <InfoTip
            type={'Warning'}
            msg={'Please make sure you have gas fee while adding collateral'}
            className={'m-b-8'}
          />
          {getApproveAndAddCollButton()}
        </div>
      </div>
      <div className="p-t-20">
        <CustomInputContainer
          ILabelValue={'Withdraw Collateral'}
          showBalance={false}
          IBalanceValue={`${getDisplayBalance(avail_to_withdraw.value, 18, 18)}`}
          isBalanceLoading={
            collateralBalance.isLoading || getTrove.isLoading || avail_to_withdraw.isLoading
          }
          DefaultValue={withdrawCollateralValue.toString()}
          ILabelInfoValue={''}
          LogoSymbol={troveDetail.depositToken}
          disabled={isRecoveryMode.value || getTrove.isLoading || isCollateralRatioBelowMin}
          hasDropDown={false}
          tagText={'MAX'}
          SymbolText={troveDetail.depositToken}
          setText={(val: string) => {
            onWithdrawCollateralChange(val);
          }}
          tokenDecimals={18}
          errorCallback={(flag: boolean) => {
            // setWithdrawIsInputFieldError(flag);
          }}
        />
        <div className="single-line-center-between m-t-12 m-b-16">
          <div className="single-line-center-start">
            <TextWrapper text={`Max Withdrawal collateral`} Fcolor={'#FFFFFF88'} />
            <div className="tags m-l-4">
              {avail_to_withdraw.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : (
                <TextWrapper
                  text={
                    getDisplayBalance(avail_to_withdraw.value, 18, 6) +
                    ` ${troveDetail.depositToken.toUpperCase()}`
                  }
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              )}
            </div>
          </div>
          <div className="single-line-center-end">
            <TextWrapper text={`Current collateral`} Fcolor={'#FFFFFF88'} />
            <div className="tags m-l-4">
              {getTrove.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : (
                <TextWrapper
                  text={
                    Number(getDisplayBalance(getTrove.value.coll, 18, 3)).toLocaleString() +
                    ` ${troveDetail.depositToken.toUpperCase()}`
                  }
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              )}
            </div>
          </div>
        </div>
        <div className="m-t-32">
          {isAmountMoreThanMaxWithdrawal && (
            <InfoTip
              type={'Error'}
              msg={'Amount cannot be more than your available withdrawal amount'}
              className={'m-b-8'}
            />
          )}
          <Button
            disabled={
              isCollateralRatioBelowMin ||
              isModifiedCollateralRatioBelowMin ||
              !Number(withdrawCollateralValue) ||
              isWithdrawInputFieldError ||
              isAmountMoreThanMaxWithdrawal
            }
            onClick={() => setOpenRemoveConfirmationModal(true)}
            tracking_id="modify_loan_withdraw_collateral"
            tracking_params={{
              action: 'initial',
              collateral: trove,
              amount: Number(withdrawCollateralValue),
            }}
          >
            Withdraw Collateral
          </Button>
        </div>
      </div>
      <div className="m-t-20">
        <div className="single-line-center-between">
          <div className="single-line-center-start">
            <TextWrapper text={'Current Collateral Ratio'} fontSize={12} />
            <ToolTip
              toolTipText={`The ratio between the ARTH value of the collateral and the debt (in ARTH) you are depositing.
               While the Minimum Collateral Ratio is ${
                 getDisplaycrParameters(trove, activeChainId).mcr
               }% during normal
                operation, it is recommended to keep the Collateral Ratio always above
                ${
                  getDisplaycrParameters(trove, activeChainId).ccr
                }% to avoid liquidation under Recovery Mode.
                 A Collateral Ratio above 200% or 250% is recommended for additional safety.`}
            />
          </div>
          <div className="single-line-center-start">
            {collateralRatio.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={`${Number(
                  getDisplayBalance(collateralRatio.value, 18, 3),
                ).toLocaleString()} %`}
                fontSize={12}
              />
            )}
          </div>
        </div>
        <div className="single-line-center-between">
          <div className="single-line-center-start">
            <TextWrapper text={'New Collateral Ratio'} fontSize={12} />
            <ToolTip
              toolTipText={`The ratio between the ARTH value of the collateral and the debt (in ARTH) as a result of
              your loan modification. While the Minimum Collateral Ratio is ${
                getDisplaycrParameters(trove, activeChainId).mcr
              }% during normal operation, it is
              recommended to keep the Collateral Ratio always above ${
                getDisplaycrParameters(trove, activeChainId).ccr
              }% to avoid liquidation under Recovery Mode.
              A Collateral Ratio above 200% or 250% is recommended for additional safety.`}
            />
          </div>
          <div className="single-line-center-start">
            {modifiedCollatreal.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={
                  modifiedCollatreal.value.eq(collateralRatio.value)
                    ? '0 %'
                    : `${Number(
                        getDisplayBalance(modifiedCollatreal.value, 18, 3),
                      ).toLocaleString()} %`
                }
                fontSize={12}
                Fcolor={isModifiedCollateralRatioBelowMin ? theme.color.red[300] : 'white'}
              />
            )}
          </div>
        </div>
      </div>
      {depositModal && (
        <DepositModal onCancel={() => setdepositModal(false)} onDeposit={() => {}} />
      )}
    </div>
  );
};

export default CollateralModifyLoan;
