import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { useGetActiveChainId } from '../../../state/chains/hooks';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (contractPrefix: string) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const { account } = useWallet();
  const activeChainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const contract = core.getCollSurplusPool(contractPrefix, activeChainId);
    const value = await contract.getCollateral(account);
    setValue({ isLoading: false, value });
  }, [account, core, contractPrefix, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch frontend registration status %: ${err}`),
      );
    }
  }, [core.isUnlocked, account, fetchValue]);

  return value;
};
