import config from '../config';

export const crParameters = (trove: string, chainId: number) => {
  return config[chainId].troveStaticInfo.crParameters;
};

export const getDisplaycrParameters = (trove: string, chainId: number, type = 'trove') => {
  const ratio = crParameters(trove, chainId);
  const ccr = Math.ceil(ratio.ccr / 1000);
  const mcr = Math.ceil(ratio.mcr / 1000);
  return {
    mcr: mcr,
    ccr: ccr,
  };
};
