import { createAction } from '@reduxjs/toolkit';
import { BigNumber } from 'ethers';

export type DefaultBNType = { val: BigNumber; for: string; collateral: string };
export type DefaultBoolType = { val: boolean; for: string; collateral: string };

export const getPendingARTHDebtReward = createAction<DefaultBNType>(
  'trove/getPendingARTHDebtReward',
);
export const getPendingETHReward = createAction<DefaultBNType>('trove/getPendingETHReward');
export const hasPendingRewards = createAction<DefaultBoolType>('trove/hasPendingRewards');
