import { BigNumber } from 'ethers';
import React from 'react';

import Grid from '@material-ui/core/Grid';

import useRepayARTH from '../../../hooks/callbacks/borrowerOperations/useRepayARTH';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import Modal from '../../../theme/components/Modal';
import TextWrapper from '../../../theme/components/TextWrapper';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';

interface Iprops {
  trove: string;
  currentDebt: BigNumber;
  removedDebt: BigNumber;
  cr: BigNumber;
  newCR: BigNumber;
  troveCollateral: BigNumber;
  openModal: boolean;
  onClose: () => void;
  symbol: string;
  onSuccess: () => void;
}

const ConfirmPaybackARTH = (props: Iprops) => {
  const {
    trove,
    currentDebt,
    removedDebt,
    cr,
    newCR,
    troveCollateral,
    openModal,
    onClose,
    symbol,
    onSuccess,
  } = props;

  const chainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, chainId);

  const repayARTH = useRepayARTH(
    troveDetail.contractPrefix,
    removedDebt,
    currentDebt.sub(removedDebt),
    troveCollateral,
  );

  const handleRepayARTH = () => {
    repayARTH(() => {
      onSuccess();
      onClose();
    });
  };

  return (
    <>
      <Modal
        title={'Confirm Payback ARTH'}
        open={openModal}
        handleClose={() => onClose()}
        closeButton
      >
        <div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper text={'Payback ARTH amount'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(getDisplayBalance(removedDebt, 18, 3)).toLocaleString()}
                fontWeight={600}
              />
              <div className="tags m-l-4">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper text={'Current Debt amount'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(getDisplayBalance(currentDebt, 18, 3)).toLocaleString()}
                fontWeight={600}
              />
              <div className="tags m-l-4">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper text={'New Debt amount'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(
                  getDisplayBalance(currentDebt.sub(removedDebt), 18, 3),
                ).toLocaleString()}
                fontWeight={600}
              />
              <div className="tags m-l-4">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper
              text={'Current Collateral Ratio'}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="single-line-center-between">
              <TextWrapper
                text={`${Number(getDisplayBalance(cr, 18, 3)).toLocaleString()} %`}
                fontWeight={600}
              />
            </div>
          </div>
          <div className="single-line-center-between m-b-32">
            <TextWrapper text={'New Collateral Ratio'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={`${Number(getDisplayBalance(newCR, 18, 3)).toLocaleString()} %`}
                fontWeight={600}
              />
            </div>
          </div>
          <Grid container spacing={2} className={'m-t-32'}>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                variant={'transparent'}
                onClick={() => onClose()}
                tracking_id="modify_loan_payback_ARTH"
                tracking_params={{
                  action: 'cancel',
                  collateral: symbol,
                  amount: Number(getDisplayBalance(removedDebt, 18, 3)),
                }}
              >
                Cancel
              </Button>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                onClick={handleRepayARTH}
                tracking_id="modify_loan_payback_ARTH"
                tracking_params={{
                  action: 'confirm',
                  collateral: symbol,
                  amount: Number(getDisplayBalance(removedDebt, 18, 3)),
                }}
              >
                Confirm Payback ARTH
              </Button>
            </Grid>
          </Grid>
        </div>
      </Modal>
    </>
  );
};

export default ConfirmPaybackARTH;
