import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { useGetActiveChainId } from '../../../state/chains/hooks';
import { troveDetails } from '../../../utils/tokenDetails';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber | number;
};

export default (trove: string, doRefresh: boolean) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: 0 });

  const core = useCore();
  const { account } = useWallet();
  const activeChainId = useGetActiveChainId();

  const troveDetail = troveDetails(trove, activeChainId);

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: 0 });
      return;
    }

    const contract = core.getSortedTroves(troveDetail.contractPrefix, activeChainId);
    const value = await contract.getSize();
    setValue({ isLoading: false, value: value });
  }, [core, trove, account]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch linked list size: ${err}`));
    }
  }, [core.isUnlocked, account, doRefresh, fetchValue]);

  return value;
};
