import Numeral from 'numeral';
import React, { useState } from 'react';

import { Grid } from '@material-ui/core';

import useETHGain from '../../../hooks/state/stabilityPool/useETHGain';
import useGetDepositedAmount from '../../../hooks/state/stabilityPool/useGetDepositedAmount';
import useGetMAHARewards from '../../../hooks/state/stabilityPool/useGetMAHARewards';
import useGetPoolDeposit from '../../../hooks/state/stabilityPool/useGetPoolDeposit';
import useGetPoolShare from '../../../hooks/state/stabilityPool/useGetPoolShare';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../../theme';
import Button from '../../../theme/components/Button';
import DataField from '../../../theme/components/DataField';
import TextButton from '../../../theme/components/TextButton';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';
import WithdrawARTHModal from '../modal/WithdrawARTHModal';

interface IProps {
  trove: string;
}

const UsersData = (props: IProps) => {
  const { trove } = props;
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);
  const ethGain = useETHGain(trove);
  const mahaGain = useGetMAHARewards(trove);
  const poolShare = useGetPoolShare(trove);
  const depositedStake = useGetDepositedAmount(trove);

  const [openConfirmWithdrawModal, setOpenConfirmWithdrawModal] = useState<boolean>(false);

  return (
    <div className={'material-primary'}>
      <WithdrawARTHModal
        unClaimedReward={ethGain}
        depositedStake={depositedStake}
        trove={trove}
        openModal={openConfirmWithdrawModal}
        onClose={() => setOpenConfirmWithdrawModal(false)}
      />
      <DataField
        label={'Your Pool Share'}
        isValueLoading={poolShare.isLoading}
        value={`${poolShare.value.toString() + '%'}`}
        labelFontColor={theme.color.transparent[100]}
        labelFontWeight={600}
        className={'m-b-16'}
      />
      <DataField
        label={'Your Locked Stake'}
        isValueLoading={depositedStake.isLoading}
        value={
          Number(getDisplayBalance(depositedStake.value, 18, 3)).toLocaleString() + ' ARTH'
        }
        labelFontColor={theme.color.transparent[100]}
        labelFontWeight={600}
        className={'m-b-16'}
      />
      <DataField
        label={'Unclaimed Rewards'}
        isValueLoading={ethGain.isLoading}
        value={
          ' ' +
          Number(getDisplayBalance(ethGain.value, 18, 6)).toLocaleString() +
          ' ' +
          troveDetail.depositToken.toUpperCase()
        }
        labelFontColor={theme.color.transparent[100]}
        labelFontWeight={600}
        className={'m-b-2'}
      />
      <DataField
        label={''}
        isValueLoading={mahaGain.isLoading}
        value={Number(getDisplayBalance(mahaGain.value, 18, 6)).toLocaleString() + ' MAHA'}
        labelFontColor={theme.color.transparent[100]}
        labelFontWeight={600}
        className={'m-b-32'}
      />
      <Button
        onClick={() =>
          depositedStake.value.gt(0) ? setOpenConfirmWithdrawModal(true) : () => {}
        }
        text={ethGain.value.gt(0) || mahaGain.value.gt(0) ? 'Withdraw and Claim' : 'Withdraw'}
        tracking_id={
          ethGain.value.gt(0) || mahaGain.value.gt(0) ? `withdraw&claim_S_P` : `withdraw_S_P`
        }
        tracking_params={{
          collateral: troveDetail.trove,
          action: 'initial',
          amount: Number(getDisplayBalance(depositedStake.value, 18, 3)),
        }}
        disabled={depositedStake.value.lte(0)}
      />
      {/*<Grid container spacing={2}>
        <Grid item lg={6} md={6} sm={12} xs={12}>
        </Grid>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <Button
            onClick={() =>
              depositedStake.value.gt(0)
                ? setOpenConfirmWithdrawModal(true)
                : () => { }
            }
            text={(ethGain.value.gt(0) || mahaGain.value.gt(0))? 'Withdraw and Claim': 'Withdraw'}
            tracking_id={(ethGain.value.gt(0) || mahaGain.value.gt(0)) ? `withdraw&claim_S_P` : `withdraw_S_P`}
            tracking_params={{
              collateral: troveDetail.trove,
              action: 'initial',
              amount: Number(getDisplayBalance(depositedStake.value, 18, 3)),
            }}
            disabled={depositedStake.value.lte(0)}
          />
        </Grid>
      </Grid>*/}
    </div>
  );
};

export default UsersData;
