import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { useGetActiveChainId } from '../../../state/chains/hooks';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  isRegistered: boolean;
};

export default (contractPrefix: string) => {
  const [value, setValue] = useState<State>({ isLoading: true, isRegistered: false });

  const core = useCore();
  const { account } = useWallet();
  const activeChainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, isRegistered: false });
      return;
    }

    const contract = core.getBorrowerOperations(contractPrefix, activeChainId);
    const flag = await contract.frontEnds(core.config('frontEndAddress', activeChainId));
    setValue({ isLoading: false, isRegistered: flag });
  }, [core, contractPrefix, account]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch frontend registration status %: ${err}`),
      );
    }
  }, [core.isUnlocked, account, fetchValue]);

  return value;
};
