import * as Sentry from '@sentry/react';
import { BigNumber } from 'ethers';
import { useCallback } from 'react';

import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import { getDisplayBalance } from '../../../utils/formatBalance';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import useCore from '../../useCore';

export default function (contractPrefix: string, amount: BigNumber) {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetActiveChainId();

  return useCallback(
    async (onSuccess: () => void, onError: () => void): Promise<void> => {
      const contract = core.getStabilityPool(contractPrefix, chainId);

      try {
        const response = await contract.withdrawFromSP(amount);

        if (response.status !== 1) {
          Sentry.captureException({
            name: 'useWithdraw',
            response,
            contractPrefix,
            amount,
          });
        }

        addTransaction(response, {
          summary: `Withdraw ${Number(getDisplayBalance(amount, 18, 3)).toLocaleString()} ARTH`,
        });

        const tx = await response.wait();

        if (tx.status === 1) onSuccess();
        if (tx.status !== 1) onError();
      } catch (e) {
        Sentry.captureException({
          name: 'useWithdraw',
          ...e,
        });
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e.stack,
          },
        });
        onError();
      }
    },
    [core, contractPrefix, chainId, amount, addTransaction, addPopup],
  );
}
