import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useWallet } from 'use-wallet';

import { DECIMALS_18 } from '../../protocol/constants';
import { AppState } from '../../state';
import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default () => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const activeChainId = useGetActiveChainId();
  // const gmuPrices = useSelector((app: AppState) => app.analytics.gmuPrice);
  const entireSystemColl = useSelector((app: AppState) => app.analytics.entireSystemColl);
  const { account } = useWallet();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const collaterals = core.getAllTroves(activeChainId);
    const entireCollateral = collaterals.map((c: string) => entireSystemColl[c]);

    setValue({
      isLoading: false,
      value: entireCollateral.reduce((a, b) => a.add(b), BigNumber.from(0)),
    });
  }, [account, core, entireSystemColl, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch entire protocol collateral %: ${err}`),
      );
    }
  }, [core.isUnlocked, account, fetchValue, activeChainId]);

  return value;
};
