import { BigNumber } from '@ethersproject/bignumber';
import { parseUnits } from 'ethers/lib/utils';
import React, { useState } from 'react';

import Grid from '@material-ui/core/Grid';

import useRedeemCollateral from '../../../hooks/callbacks/troveManager/useRedeemCollateral';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import Modal from '../../../theme/components/Modal';
import TextWrapper from '../../../theme/components/TextWrapper';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';

interface Iprops {
  arthValue: string;
  openModal: boolean;
  onClose: () => void;
  redemptionRate: BigNumber;
  collateralValue: string;
  trove: string;
  redeemFee: BigNumber;
  stabilityFee: BigNumber;
  stabilityFeeRate: BigNumber;
  stabilityFeeToken: string;
  onSuccess: () => void;
}

const ConfirmRedeemModal = (props: Iprops) => {
  const {
    openModal,
    onClose,
    trove,
    redemptionRate,
    arthValue,
    collateralValue,
    redeemFee,
    stabilityFee,
    stabilityFeeRate,
    stabilityFeeToken,
    onSuccess,
  } = props;

  const chainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, chainId);
  const [loading, setLoading] = useState<boolean>(false);

  const redeemCollateral = useRedeemCollateral(
    trove,
    BigNumber.from(parseUnits(arthValue, 18)),
    redemptionRate,
  );

  const handleRedemption = () => {
    setLoading(true);
    redeemCollateral(
      () => {
        setLoading(false);
        onClose();
        onSuccess();
      },
      () => {
        setLoading(false);
        onClose();
      },
    );
  };

  return (
    <>
      <Modal
        title={'Confirm Redemption'}
        open={openModal}
        handleClose={() => onClose()}
        closeButton
      >
        <div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper text={'Your ARTH amount'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(arthValue).toLocaleString()}
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
              />
              <div className="tags m-l-4">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-12">
            <TextWrapper text={'MAHA Fee'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={
                  Number(getDisplayBalance(stabilityFee, 18, 3)).toLocaleString('en-US', {
                    maximumFractionDigits: 4,
                    minimumFractionDigits: 2,
                  }) +
                  ' (' +
                  Number(getDisplayBalance(stabilityFeeRate, 16, 3)).toLocaleString() +
                  '%)'
                }
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
              />
              <div className="tags m-l-4">
                <TextWrapper text={stabilityFeeToken} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-24">
            <TextWrapper text={'Redeem Fee'} fontWeight={600} Fcolor={'#FFFFFF88'} />
            <div className="single-line-center-between">
              <TextWrapper
                text={
                  Number(getDisplayBalance(redeemFee, 18, 6)).toLocaleString() +
                  ' (' +
                  Number(getDisplayBalance(redemptionRate, 16, 3)).toLocaleString() +
                  '%)'
                }
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
              />
              <div className="tags m-l-4">
                <TextWrapper text={trove.toUpperCase()} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="bottom-divider m-b-24" />
          <div className="single-line-center-between m-b-32">
            <TextWrapper
              text={`You will receive ${troveDetail.depositToken.toUpperCase()}`}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(
                  getDisplayBalance(
                    BigNumber.from(parseUnits(collateralValue, 18)).sub(redeemFee),
                    18,
                    3,
                  ),
                ).toLocaleString()}
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
              />
              <div className="tags m-l-4">
                <TextWrapper
                  text={troveDetail.depositToken.toUpperCase()}
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              </div>
            </div>
          </div>
          <Grid container spacing={2}>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                variant={'transparent'}
                onClick={() => onClose()}
                tracking_id={'Redeem_ARTH'}
                tracking_params={{
                  collateral: troveDetail.trove,
                  action: 'cancel',
                  amount: Number(arthValue),
                }}
              >
                Back
              </Button>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                loading={loading}
                onClick={handleRedemption}
                tracking_id={'Redeem_ARTH'}
                tracking_params={{
                  collateral: troveDetail.trove,
                  action: 'confirm',
                  amount: Number(arthValue),
                }}
              >
                Redeem ARTH
              </Button>
            </Grid>
          </Grid>
        </div>
      </Modal>
    </>
  );
};

export default ConfirmRedeemModal;
