import { BigNumber } from 'ethers';
import { useSelector } from 'react-redux';

import { AppState } from '../../../state';

export default (token: string) => {
  const borrowingRateWithDecay = useSelector(
    (app: AppState) => app.analytics.borrowingRateWithDecay,
  );

  return { isLoading: false, value: borrowingRateWithDecay[token] || BigNumber.from(0) };
};
