import { useCallback, useEffect, useState } from 'react';

import { useGetActiveChainId } from '../state/chains/hooks';

import useCore from './useCore';

const useTokenDecimals = (token: string) => {
  const core = useCore();
  const [decimals, setDecimals] = useState(18);
  const activeChainId = useGetActiveChainId();

  const fetchdecimals = useCallback(async () => {
    if (token === core._config[activeChainId].blockchainToken) return 18;
    setDecimals(core.tokens(token, activeChainId).decimal);
  }, [core, token, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchdecimals().catch((err) =>
        console.error(`Failed to fetch token decimals for ${token}: ${err.stack} `),
      );
    }
  }, [core.isUnlocked, fetchdecimals, token]);

  return decimals;
};

export default useTokenDecimals;
