import { Dispatch } from '@reduxjs/toolkit';
import { BigNumber } from 'ethers';

import { IMulticallInput } from '../../protocol/Mulitcall';
import { Protocol } from '../../protocol/Protocol';

import * as Actions from './actions';

export const initUser = (core: Protocol, dispatch: Dispatch, chainId: number) => {
  const _registerVariable = (
    fn: string[],
    to: string,
    reduxAction: any,
    convertResult: any,
  ) => {
    const input: IMulticallInput = {
      key: `TROVE:${fn}:${to}`,
      target: to,
      call: fn,
      convertResult,
    };

    core.multicall[chainId].on(input.key, (val) => {
      dispatch(reduxAction(val));
    });

    return input;
  };

  const registerTroveManagerCalls = () => {
    const calls: IMulticallInput[] = [];
    const collaterals = core.getAllTroves(chainId);
    // const collaterals = core.getCollateralTypes(chainId);

    const who = core.myAccount;

    collaterals.forEach((collateral) => {
      const tm = core.getTroveManager(collateral, chainId);

      calls.push(
        _registerVariable(
          ['getPendingETHReward(address)(uint256)', who],
          tm.address,
          Actions.getPendingETHReward,
          (val: BigNumber) => ({ val, who, collateral }),
        ),
        _registerVariable(
          ['getPendingARTHDebtReward(address)(uint256)', who],
          tm.address,
          Actions.getPendingARTHDebtReward,
          (val: BigNumber) => ({ val, who, collateral }),
        ),
        _registerVariable(
          ['hasPendingRewards(address)(bool)', who],
          tm.address,
          Actions.hasPendingRewards,
          (val: boolean) => ({ val, who, collateral }),
        ),
      );
    });

    return calls;
  };

  const calls = registerTroveManagerCalls();
  core.multicall[chainId].addCalls(calls);
};
