import { useCallback, useEffect, useState } from 'react';

import useCore from '../../useCore';

export interface IAPR {
  isLoading: boolean;
  data: IAPRData;
}

export interface IAPRData {
  [key: string]: {
    max: string;
    min: string;
  };
}

const useGetV3StakingAPR = () => {
  const [data, setData] = useState<IAPR>({ isLoading: true, data: {} });
  const core = useCore();

  const fetchAPY = useCallback(async () => {
    const url = `https://api.arthcoin.com/apy/guageV3Apy`;
    const headers = {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET,POST,OPTIONS',
      'Access-Control-Allow-Headers':
        'access-control-allow-headers,access-control-allow-methods,access-control-allow-origin,content-type',
    };

    fetch(url, { headers })
      .then((res) => res.json())
      .then((res: IAPRData) => {
        setData({ isLoading: false, data: res });
      })
      .catch((err) => {
        setData({ isLoading: false, data: {} });
      });
  }, []);

  useEffect(() => {
    if (core) {
      setData({ isLoading: true, data: {} });
      fetchAPY().catch((err) => {
        setData({ isLoading: false, data: {} });
        console.error(`Failed to fetch FarmingAPR Values: ${err.stack}`);
      });
    }
  }, [core, fetchAPY]);

  return data;
};

export default useGetV3StakingAPR;
