import { BigNumber } from 'ethers';
import { parseUnits } from 'ethers/lib/utils';
import React, { useMemo, useState } from 'react';

import { Grid } from '@material-ui/core';

import useTokenizationWithdraw from '../../../hooks/callbacks/tokenization/useTokenizationWithdraw';
import useTokenBalance from '../../../hooks/state/useTokenBalance';
import useCore from '../../../hooks/useCore';
import { TokeniseStaticInfo } from '../../../protocol/config';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import CollateralDropDown from '../../../theme/components/CollateralDropDown';
import IconLoader from '../../../theme/components/IconLoader';
import Input from '../../../theme/components/Input';
import InputContainer from '../../../theme/components/InputContainer';
import Modal from '../../../theme/components/Modal';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { IModalProps } from '../../../utils/interface';

// import useGetARTHUSDGonsInfo from '../../../hooks/state/useGetARTHUSDGonsInfo';

const getARTHUSDOut = (
  input: BigNumber,
  gonsPerFragment: BigNumber,
  gonsPrecision: BigNumber,
): BigNumber => input.mul(gonsPerFragment).div(gonsPrecision);

const getARTHInForARTHUSD = (
  output: BigNumber,
  gonsPerFragment: BigNumber,
  gonsPrecision: BigNumber,
): BigNumber => output.mul(gonsPrecision).div(gonsPerFragment);

interface IProps {
  tokenziseInfo: TokeniseStaticInfo;
}

const Withdraw = (props: IModalProps & IProps) => {
  const { open, onClose, tokenziseInfo } = props;
  const core = useCore();
  const chainId = useGetActiveChainId();

  const [depositTokenSupply, setDepositTokenSupply] = useState<string>('');
  const [tokeniseSupply, setTokeniseSupply] = useState<string>('');

  const arthUSDGonsInfo = {
    // useGetARTHUSDGonsInfo();
    isLoading: false,
    gonsPerFramgment: BigNumber.from(2),
    gonsPrecision: BigNumber.from(1),
  };

  const depositToken = core.tokens(tokenziseInfo.depositToken, chainId);
  const tokenizeToken = core.tokens(tokenziseInfo.tokeniseToken, chainId);

  const depositTokenBalance = useTokenBalance(core.tokens(tokenziseInfo.depositToken, chainId));
  const tokeniseBalance = useTokenBalance(core.tokens(tokenziseInfo.tokeniseToken, chainId));

  const withdrawAction = useTokenizationWithdraw(
    props.tokenziseInfo.tokeniseToken,
    BigNumber.from(
      parseUnits(
        props.tokenziseInfo.tokeniseToken === 'ARTH.usd'
          ? depositTokenSupply || '0'
          : tokeniseSupply || '0',
        core.tokens(
          props.tokenziseInfo.tokeniseToken === 'ARTH.usd'
            ? props.tokenziseInfo.depositToken
            : props.tokenziseInfo.tokeniseToken,
          chainId,
        ).decimal,
      ),
    ),
    core.tokens(props.tokenziseInfo.depositToken, chainId),
  );

  const disableButton = useMemo(() => {
    return (
      arthUSDGonsInfo.isLoading ||
      depositTokenBalance.isLoading ||
      tokeniseBalance.isLoading ||
      !Number(depositTokenSupply) ||
      !Number(tokeniseSupply) ||
      BigNumber.from(parseUnits(tokeniseSupply, depositToken.decimal)).gt(tokeniseBalance.value)
    );
  }, [
    arthUSDGonsInfo.isLoading,
    depositTokenBalance.isLoading,
    tokeniseBalance.isLoading,
    tokeniseBalance.value,
    depositTokenSupply,
    tokeniseSupply,
    depositToken.decimal,
  ]);

  const onOutputChange = (val: string) => {
    if (
      !Number(val) ||
      (props.tokenziseInfo.tokeniseToken === 'ARTH.usd' && arthUSDGonsInfo.gonsPrecision.lte(0))
    ) {
      setDepositTokenSupply('');
      setTokeniseSupply('');
      return;
    }

    if (props.tokenziseInfo.tokeniseToken === 'ARTH.usd') {
      const output: BigNumber = getARTHUSDOut(
        BigNumber.from(parseUnits(val, 18)),
        arthUSDGonsInfo.gonsPerFramgment,
        arthUSDGonsInfo.gonsPrecision,
      );

      setDepositTokenSupply(val);
      setTokeniseSupply(getDisplayBalance(output, depositToken.decimal));
      return;
    }

    setDepositTokenSupply(val);
    setTokeniseSupply(val);
  };

  const onInputChange = (val: string) => {
    if (
      !Number(val) ||
      (props.tokenziseInfo.tokeniseToken === 'ARTH.usd' &&
        arthUSDGonsInfo.gonsPerFramgment.lte(0))
    ) {
      setDepositTokenSupply('');
      setTokeniseSupply('');
      return;
    }

    if (props.tokenziseInfo.tokeniseToken === 'ARTH.usd') {
      const output: BigNumber = getARTHInForARTHUSD(
        BigNumber.from(parseUnits(val, 18)),
        arthUSDGonsInfo.gonsPerFramgment,
        arthUSDGonsInfo.gonsPrecision,
      );

      setDepositTokenSupply(getDisplayBalance(output, depositToken.decimal));
      setTokeniseSupply(val);
      return;
    }

    setDepositTokenSupply(val);
    setTokeniseSupply(val);
  };

  return (
    <Modal
      title={`Redeem ${props.tokenziseInfo.depositToken}`}
      open={open}
      handleClose={onClose}
      closeButton
    >
      <div>
        <div className="m-b-8">
          <InputContainer
            label={'How much tokens do you need to redeem?'}
            dataLabel={'Balance: '}
            dataValue={getDisplayBalance(tokeniseBalance.value, tokenizeToken.decimal)}
          >
            <div className="single-line-center-between">
              <Input
                value={tokeniseSupply}
                setValue={onInputChange}
                maxTag={true}
                onMaxClick={() => {
                  onInputChange(
                    getDisplayBalance(tokeniseBalance.value, tokenizeToken.decimal),
                  );
                }}
              />
              <CollateralDropDown
                selectedCollateral={tokenziseInfo.tokeniseToken}
                collateralText={tokenziseInfo.tokeniseToken}
              />
            </div>
          </InputContainer>
        </div>
        <div className="text-center m-t-12 m-b-12">
          <IconLoader iconName={'ArrowTailDown'} iconType={'arrow'} />
        </div>
        <div className="m-b-8">
          <InputContainer
            label={`How much will you receive?`}
            dataLabel={'Balance: '}
            dataValue={getDisplayBalance(depositTokenBalance.value, depositToken.decimal)}
          >
            <div className="single-line-center-between">
              <Input value={depositTokenSupply} setValue={onOutputChange} />
              <CollateralDropDown selectedCollateral={tokenziseInfo.depositToken} />
            </div>
          </InputContainer>
        </div>
        <div className={'m-t-32'}>
          <Grid container spacing={2}>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                variant={'outlined'}
                text={'Cancel'}
                onClick={onClose}
                tracking_id={'tokenize_redeem'}
                tracking_params={{
                  collateral: tokenziseInfo.tokeniseToken,
                  action: 'cancel',
                }}
              />
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                text={'Withdraw'}
                disabled={disableButton}
                onClick={withdrawAction}
                tracking_id={'tokenize_redeem'}
                tracking_params={{
                  collateral: tokenziseInfo.tokeniseToken,
                  action: 'confirm',
                }}
              />
            </Grid>
          </Grid>
        </div>
      </div>
    </Modal>
  );
};

export default Withdraw;
