import { BigNumber } from '@ethersproject/bignumber';
import { useCallback, useEffect, useState } from 'react';

import { useGetActiveChainId } from '../../../state/chains/hooks';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default () => {
  const [customState, setCustomState] = useState<State>({
    isLoading: true,
    value: BigNumber.from(0),
  });
  const core = useCore();
  const activeChainId = useGetActiveChainId();

  const fetchCashPrice = useCallback(async () => {
    const controller = core.contracts('UniswapPairOracle_ARTH_MAHA', activeChainId);
    const decimals = BigNumber.from(10).pow(18);

    if (!controller) {
      setCustomState({
        isLoading: false,
        value: BigNumber.from(0),
      });
    } else {
      setCustomState({
        isLoading: false,
        value: await controller.consult(core.tokens('MAHA', activeChainId).address, decimals),
      });
    }
  }, [activeChainId, core]);

  useEffect(() => {
    fetchCashPrice().catch((err) =>
      console.error(`Failed to fetch uniswap price: ${err.stack}`),
    );
  }, [fetchCashPrice]);

  return customState;
};
