import detectEthereumProvider from '@metamask/detect-provider';
import { BigNumber, utils } from 'ethers';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import styled from 'styled-components';
import { useWallet } from 'use-wallet';

import ChooseWallet from '../../../components/TopBar/components/modal/WalletInfo/ChooseWallet';
import config, { getSupportedChains } from '../../../config';
import useCore from '../../../hooks/useCore';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../index';
import Button from '../Button';
import { ButtonProps } from '../Button';
import TextWrapper from '../TextWrapper';
import WrongNetworkModal from '../WrongNetworkModal';

const ActionButton = (props: ButtonProps) => {
  const core = useCore();

  const [showWalletOption, setShowWalletOption] = useState<boolean>(false);
  const [wrongNetwork, setWrongNetwork] = React.useState<boolean>(false);
  const [showWrongNeworkModal, setShowWrongNeworkModal] = useState<boolean>(false);
  const chainId = useGetActiveChainId();

  const { account, ethereum }: { account: any; ethereum?: any } = useWallet();

  const switchMetamaskChain = () => {
    // @ts-ignore
    if (window.ethereum) {
      // @ts-ignore
      window.ethereum
        .request({
          method: 'wallet_switchEthereumChain',
          params: [
            { chainId: utils.hexStripZeros(BigNumber.from(config.chainId).toHexString()) },
          ],
        })
        .then(() => {
          window.location.reload();
        })
        .catch((error: any) => {
          if (error.code === 4902) addNetworkToMetamask();
        });
    }
  };

  const addNetworkToMetamask = () => {
    if (!ethereum?.request) return;
    if (ethereum) {
      ethereum
        .request({
          method: 'wallet_addEthereumChain',
          params: [
            {
              chainId: utils.hexStripZeros(BigNumber.from(config.chainId).toHexString()),
              chainName: config.networkName,
              rpcUrls: [config[chainId].defaultProvider],
              iconUrls: [],
              blockExplorerUrls: [config.etherscanUrl],
              nativeCurrency: {
                name: config.blockchainTokenName,
                symbol: config.blockchainToken,
                decimals: config.blockchainTokenDecimals,
              },
            },
          ],
        })
        .then(() => {
          window.location.reload();
        })
        .catch((error: any) => {
          if (error.code === 4001) {
            // EIP-1193 userRejectedRequest error.
            console.log('We cannot encrypt anything without the key.');
          }
        });
    }
  };

  const processNetwork = useCallback(async () => {
    const provider: any = await detectEthereumProvider();

    if (provider) {
      const chainId = Number(await provider.request({ method: 'eth_chainId' }));
      setWrongNetwork(!getSupportedChains().includes(chainId));
    }
  }, [core]);

  useEffect(() => {
    processNetwork();
  }, [core, account]);

  return (
    <div>
      <ChooseWallet openModal={showWalletOption} onClose={() => setShowWalletOption(false)} />
      <WrongNetworkModal
        open={showWrongNeworkModal}
        onClose={() => setShowWrongNeworkModal(false)}
      />
      {wrongNetwork ? (
        <Button
          text={props.text}
          tracking_id={'connect_wallet_before_button'}
          onClick={() => setShowWrongNeworkModal(true)}
        />
      ) : !account ? (
        <Button
          text={props.text}
          tracking_id={'connect_wallet_before_button'}
          onClick={() => {
            setShowWalletOption(true);
          }}
        />
      ) : (
        <Button {...props} />
      )}
    </div>
  );
};

export default ActionButton;

const WrongNetworkButton = styled.div`
  padding: 10px 22px;
  width: 100%;
  background: ${theme.color.red[300]};
  border-radius: 6px;
`;
