import { BigNumber } from '@ethersproject/bignumber';
import { parseUnits } from 'ethers/lib/utils';
import React from 'react';

import useTokenizationDeposit from '../../../hooks/callbacks/tokenization/useTokenizationDeposit';
import useApprove, { ApprovalState } from '../../../hooks/callbacks/useApprove';
import useCore from '../../../hooks/useCore';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';

interface IProps {
  onCancel: () => void;
  depositToken: string;
  wrappedToken: string;
  val: string;
  disableButton: boolean;
}

export default (props: IProps) => {
  const chainId = useGetActiveChainId();

  const core = useCore();
  const depositToken = core.tokens(props.depositToken, chainId);
  const wrappedToken = core.tokens(props.wrappedToken, chainId);

  const [approveStatus, approve] = useApprove(depositToken, wrappedToken?.address || '');

  const depositAction = useTokenizationDeposit(
    props.wrappedToken,
    BigNumber.from(parseUnits(props.val, depositToken.decimal)),
    depositToken,
  );

  const handleDeposit = () => {
    depositAction(() => {
      props.onCancel();
    });
  };

  const isApproved = approveStatus === ApprovalState.APPROVED;
  const isApproving = approveStatus === ApprovalState.PENDING;

  return !isApproved ? (
    <Button
      loading={isApproving}
      text={isApproving ? 'Approving' : 'Approve'}
      size={'lg'}
      onClick={approve}
      disabled={isApproved || props.disableButton}
      tracking_id={'tokenize_mint_apporve'}
      tracking_params={{
        collateral: depositToken.symbol,
      }}
    />
  ) : (
    <Button
      disabled={props.disableButton}
      text={'Deposit'}
      size={'lg'}
      onClick={handleDeposit}
      tracking_id={'tokenize_mint'}
      tracking_params={{
        collateral: depositToken.symbol,
        action: 'confirm',
      }}
      // tracking_id={'farming_deposit'}
      // tracking_params={{
      //   action: 'confirm',
      //   collateral: props.poolInfo.name,
      //   amount: Number(val),
      // }}
    />
  );
};
