import { BigNumber } from '@ethersproject/bignumber';
import { parseUnits } from 'ethers/lib/utils';
import React, { useMemo, useState } from 'react';

import Grid from '@material-ui/core/Grid';

import { ValidateNumber } from '../../components/CustomInputContainer/RegexValidation';
import useRedeemOldARTH from '../../hooks/callbacks/RedeemPoly/useRedeemOldARTH';
import useApprove, { ApprovalState } from '../../hooks/callbacks/useApprove';
import useTokenBalance from '../../hooks/state/useTokenBalance';
import useCore from '../../hooks/useCore';
import { useGetActiveChainId } from '../../state/chains/hooks';
import Button from '../../theme/components/Button';
import CollateralDropDown from '../../theme/components/CollateralDropDown';
import IconLoader from '../../theme/components/IconLoader';
import Input from '../../theme/components/Input';
import InputContainer from '../../theme/components/InputContainer';
import PageHeader from '../../theme/components/PageHeader';
import States from '../../theme/components/States';
import { getDisplayBalance } from '../../utils/formatBalance';

import RedeemDesc from './components/RedeemDesc';

const RedeemARTH = () => {
  const core = useCore();
  const chainId = useGetActiveChainId();
  const [arthValue, setArthValue] = useState<string>('');
  const [collateralValue, setCollateralValue] = useState<string>('');

  const [toggleDropDown, setToggleDropDown] = useState<boolean>(false);
  const [selectedCollateral, setSelectedcollateral] = useState<string>('USDC');
  const collateralValues = ['USDC'];

  const arthBalance = useTokenBalance(core.tokens('ARTH', chainId));
  const collateraltoken = core.tokens(selectedCollateral, chainId);
  const collateralBalance = useTokenBalance(collateraltoken);

  const [arthApprovalStatus, approveARTH] = useApprove(
    core.tokens('ARTH', chainId),
    core.contracts('ARTHRedeemer', chainId).address,
  );

  const isARTHApproved = useMemo(() => arthApprovalStatus === ApprovalState.APPROVED, [
    arthApprovalStatus,
  ]);
  const isARTHApproving = useMemo(() => arthApprovalStatus === ApprovalState.PENDING, [
    arthApprovalStatus,
  ]);

  const isArthGreaterThanBalance = useMemo(() => {
    return BigNumber.from(parseUnits(`${arthValue === '' ? '0' : arthValue}`, 18)).gt(
      arthBalance.value,
    );
  }, [arthBalance, arthValue]);

  const onARTHValueChange = async (val: string) => {
    if (val === '') {
      setCollateralValue('');
      setArthValue('');
      return;
    }

    let check: boolean = ValidateNumber(val);
    if (!check) return;
    setArthValue(check ? val : String(Number(val)));

    const valueInNumber: number = Number(val);
    if (!valueInNumber) return;

    const finalCollateralValue = valueInNumber * 2;

    setCollateralValue(finalCollateralValue.toString());
  };

  const redeemAction = useRedeemOldARTH(
    BigNumber.from(parseUnits(`${arthValue === '' ? '0' : arthValue}`, 18)),
    collateralValue,
  );

  const handleRedeem = () => {
    redeemAction(
      () => {},
      () => {},
    );
  };

  return (
    <div className={'custom-container'}>
      <PageHeader
        title={'Redeem'}
        subTitle={'Redeem ARTH for USDC at a 1:2 ratio'}
        className={'m-b-40'}
      />
      <div>
        <Grid container className="m-t-24" spacing={2}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <div className={'material-secondary'}>
              <InputContainer
                label={'Amount you want to redeem'}
                dataLabel={'Balance: '}
                dataValue={`${getDisplayBalance(arthBalance.value, 18, 3)}`}
                dataValueLoading={arthBalance.isLoading}
              >
                <States
                  state={isArthGreaterThanBalance ? 'error' : 'default'}
                  msg={
                    isArthGreaterThanBalance ? 'Amount cannot be greater than your balance' : ''
                  }
                >
                  <div className={'single-line-center-between'}>
                    <Input
                      value={arthValue}
                      setValue={onARTHValueChange}
                      maxTag={true}
                      onMaxClick={() =>
                        onARTHValueChange(`${getDisplayBalance(arthBalance.value, 18, 3)}`)
                      }
                    />
                    <CollateralDropDown selectedCollateral={'ARTH'} />
                  </div>
                </States>
              </InputContainer>
              <div className="text-center m-t-12 m-b-12">
                <IconLoader iconName={'ArrowTailDown'} iconType={'arrow'} />
              </div>
              <InputContainer
                label={'You will receive'}
                dataLabel={'Balance: '}
                dataValue={`${getDisplayBalance(
                  collateralBalance.value,
                  collateraltoken.decimal,
                  3,
                )}`}
              >
                <div className={'single-line-center-between'}>
                  <Input
                    value={collateralValue}
                    setValue={setCollateralValue}
                    disabled={true}
                  />
                  <CollateralDropDown
                    selectedCollateral={selectedCollateral}
                    showDropDown={toggleDropDown}
                    hasDropDown={false}
                    toggleDropDown={() => setToggleDropDown(!toggleDropDown)}
                    collateralTypes={collateralValues}
                    ondropDownValueChange={setSelectedcollateral}
                  />
                </div>
              </InputContainer>
              <div className={'m-t-32'}>
                <Grid container className="m-t-24" spacing={2}>
                  <Grid item lg={6} md={6} sm={12} xs={12}>
                    <Button
                      loading={isARTHApproving}
                      disabled={isARTHApproved}
                      onClick={approveARTH}
                      tracking_id={
                        isARTHApproved ? `Redeem_Approved_ARTH` : `Redeem_Approve_ARTH`
                      }
                      tracking_params={{ collateral: 'USDC', action: 'confirm' }}
                    >
                      {isARTHApproved ? `ARTH Approved` : `Approve ARTH`}
                    </Button>
                  </Grid>
                  <Grid item lg={6} md={6} sm={12} xs={12}>
                    <Button
                      text={'Redeem'}
                      disabled={
                        isARTHApproving ||
                        !isARTHApproved ||
                        arthValue === '' ||
                        isArthGreaterThanBalance
                      }
                      onClick={handleRedeem}
                    />
                  </Grid>
                </Grid>
              </div>
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default RedeemARTH;
