import * as Sentry from '@sentry/react';
import { BigNumber } from 'ethers';
import Numeral from 'numeral';
import { useCallback } from 'react';

import { DECIMALS_18 } from '../../../protocol/constants';
import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import { getDisplayBalance } from '../../../utils/formatBalance';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import { findHintsForNominalCollateralRatio } from '../../state/hinthelpers/helper';
import useCore from '../../useCore';

export default function (
  contractPrefix: string,
  addCollAmount: BigNumber, // Collateral to add in trove.
  debtAmount: BigNumber, // Total debt after the modification.
  collateralAmount: BigNumber, // Total collateral after the modification.
) {
  const core = useCore();
  const addPopup = useAddPopup();
  const activeChainId = useGetActiveChainId();
  const addTransaction = useTransactionAdder();

  return useCallback(
    async (callback?: () => void): Promise<void> => {
      try {
        const contract = core.getBorrowerOperations(contractPrefix, activeChainId);

        const [
          partialRedemptionUpperHint,
          partialRedemptionLowerHint,
        ]: string[] = await findHintsForNominalCollateralRatio(
          collateralAmount.mul(DECIMALS_18.mul(100)).div(debtAmount),
          core.getSortedTroves(contractPrefix, activeChainId),
          core.getHintHelper(contractPrefix, activeChainId),
          core.getTroveManager(contractPrefix, activeChainId),
          core.myAccount,
        );

        const gasLimit = (
          await contract.estimateGas.addColl(
            partialRedemptionUpperHint,
            partialRedemptionLowerHint,
            { value: addCollAmount },
          )
        )
          .mul(120)
          .div(100);

        const response = await contract.addColl(
          partialRedemptionUpperHint,
          partialRedemptionLowerHint,
          { value: addCollAmount, gasLimit },
        );

        addTransaction(response, {
          summary: `Add ${Numeral(getDisplayBalance(addCollAmount, 18, 3)).format(
            '0,0.00a',
          )} ${contractPrefix.toUpperCase()} to Loan.`,
        });

        if (response.status !== 1) {
          Sentry.captureException({
            name: 'useAddColl',
            response,
            contractPrefix,
            addCollAmount,
            debtAmount,
            collateralAmount,
          });
        }

        if (callback) callback();
      } catch (e) {
        Sentry.captureException({
          ...e,
          name: 'useAddColl',
        });
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [
      contractPrefix,
      core,
      addTransaction,
      addPopup,
      addCollAmount,
      debtAmount,
      collateralAmount,
      activeChainId,
    ],
  );
}
