import * as Sentry from '@sentry/react';
import { BigNumber } from 'ethers';
import { useCallback } from 'react';

import { DECIMALS_18 } from '../../../protocol/constants';
import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import { getDisplayBalance } from '../../../utils/formatBalance';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import { findHintsForNominalCollateralRatio } from '../../state/hinthelpers/helper';
import useCore from '../../useCore';

export default function (
  contractPrefix: string,
  borrowingFee: BigNumber,
  debtAmount: BigNumber,
  collateralAmount: BigNumber,
) {
  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();

  return useCallback(
    async (callback?: () => void, errorHandler?: () => void): Promise<void> => {
      try {
        const contract = core.getBorrowerOperations(contractPrefix, activeChainId);

        const [
          partialRedemptionUpperHint,
          partialRedemptionLowerHint,
        ]: string[] = await findHintsForNominalCollateralRatio(
          collateralAmount.mul(DECIMALS_18.mul(100)).div(debtAmount),
          core.getSortedTroves(contractPrefix, activeChainId),
          core.getHintHelper(contractPrefix, activeChainId),
          core.getTroveManager(contractPrefix, activeChainId),
          core.myAccount,
        );

        const gasLimit = await contract.estimateGas.openTrove(
          borrowingFee.add(BigNumber.from(10).pow(18).mul(1).div(100)).toString(), // Slippage in max fee.
          debtAmount,
          partialRedemptionUpperHint,
          partialRedemptionLowerHint,
          core.config('frontEndAddress', activeChainId),
          { value: collateralAmount },
        );

        const tx = await contract.openTrove(
          borrowingFee.add(BigNumber.from(10).pow(18).mul(1).div(100)).toString(), // Slippage in max fee.
          debtAmount,
          partialRedemptionUpperHint,
          partialRedemptionLowerHint,
          core.config('frontEndAddress', activeChainId),
          { value: collateralAmount, gasLimit: gasLimit.mul(110).div(100) },
        );

        addTransaction(tx, {
          summary: `Borrow ${Number(
            getDisplayBalance(debtAmount, 18, 3),
          ).toLocaleString()} ARTH using ${contractPrefix.toUpperCase()} as collateral.`,
        });

        const response = await tx.wait();

        if (errorHandler && response.status !== 1) {
          Sentry.captureException({
            name: 'useOpenTrove',
            response,
            contractPrefix,
            borrowingFee,
            debtAmount,
            collateralAmount,
          });
          errorHandler();
        }
        if (callback && response.status === 1) callback();
      } catch (e) {
        Sentry.captureException({
          ...e,
          name: 'useOpenTrove',
        });
        if (errorHandler) {
          errorHandler();
        }

        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [
      core,
      contractPrefix,
      activeChainId,
      collateralAmount,
      debtAmount,
      borrowingFee,
      addTransaction,
      addPopup,
    ],
  );
}
