import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

import Depreciated from './components/Depreciated';
import config from './config';
import { useGetChainId } from "./utils/NetworksCustomHooks";
import NoPageFound from "./components/NoPageFound";
import Page from './theme/components/Page/Page';
import AddLoan from './views/AddLoan';
import Faucet from './views/Faucet';
import Loan from './views/Loan';
import LoanDetails from './views/LoanDetails';
import ModifyLoan from './views/ModifyLoan';
import Price from './views/Price';
import Redeem from './views/Redeem';
import RedeemARTH from './views/RedeemARTH';
import StabilityPool from './views/StabilityPool';
import Tokenization from './views/Tokenization';
import UniswapV3Gauges from './views/UniswapV3Gauges';
import { useNetwork } from "wagmi";
import WrongNetwork from "./components/WrongNetwork";

const Navigation = () => {
  const chainId = useGetChainId();
  const {chain} = useNetwork();
  let defaultUrl = '/loans/all-loans';

  if (chain === undefined || chain.unsupported) {
    return (
      <Switch>
        <Route exact path="*">
          <Page>
            <WrongNetwork />
          </Page>
        </Route>
      </Switch>
    )
  }

  if(chainId === 1 || chainId === 5) {
    return (
      <Switch>
        <Route exact path="/">
          <Redirect to={defaultUrl} />
        </Route>
        <Route path="/price">
          <Page>
            <Price />
          </Page>
        </Route>
        <Route path="/loans/:mode">
          <Page><Loan /></Page>
        </Route>
        <Route path="/loan/add/:id">
          <Page>
            <AddLoan />
          </Page>
        </Route>
        <Route path="/loan/details/:ownerAddress/:trove">
          <Page>
            <LoanDetails />
          </Page>
        </Route>
        <Route path="/loan/modify/:trove">
          <Page>
            <ModifyLoan />
          </Page>
        </Route>
        <Route path="/redeem/:id">
          <Page>
            <Redeem />
          </Page>
        </Route>
        <Route path="/stability-pool/:id">
          <Page>
            <StabilityPool />
          </Page>
        </Route>
        <Route path="/farming">
          <Page>
            <UniswapV3Gauges />
          </Page>
        </Route>
        <Route exact path="*">
          <Page>
            <NoPageFound />
          </Page>
        </Route>
      </Switch>
    )
  }

  if (chainId === 137) {
    let defaultUrl = '/farming';
    return (
      <Switch>
        <Route exact path="/">
          <Redirect to={defaultUrl} />
        </Route>
        <Route path="/stability-pool/:id">
          <Page>
            <StabilityPool />
          </Page>
        </Route>
        <Route path="/redeem">
          <Page>
            <RedeemARTH />
          </Page>
        </Route>
        <Route path="/farming">
          <Page>
            <UniswapV3Gauges />
          </Page>
        </Route>
        <Route path="/tokenization">
          <Page>
            <Tokenization />
          </Page>
        </Route>
        <Route exact path="*">
          <Redirect to={defaultUrl} />
        </Route>
      </Switch>
    )
  }

  if (chainId === 56) {
    let defaultUrl = '/farming';
    return (
      <Switch>
        <Route exact path="/">
          <Redirect to={defaultUrl} />
        </Route>
        <Route path="/stability-pool/:id">
          <Page>
            <StabilityPool />
          </Page>
        </Route>
        <Route path="/farming">
          <Page>
            <UniswapV3Gauges />
          </Page>
        </Route>
        <Route path="/tokenization">
          <Page>
            <Tokenization />
          </Page>
        </Route>
        <Route exact path="*">
          <Redirect to={defaultUrl} />
        </Route>
      </Switch>
    )
  }

  return (
    <Switch>
      <Route exact path="/">
        <Redirect to={defaultUrl} />
      </Route>
      <Route path="/price">
        <Page>
          <Price />
        </Page>
      </Route>
      <Route path="/loans/:mode">
        <Page>{chainId !== 137 ? <Loan /> : <Depreciated />}</Page>
      </Route>
      <Route path="/loan/add/:id">
        <Page>
          {chainId === 1 || chainId === 5 ? <AddLoan /> : <Depreciated />}
        </Page>
      </Route>
      <Route path="/loan/details/:ownerAddress/:trove">
        <Page>
          <LoanDetails />
        </Page>
      </Route>
      <Route path="/loan/modify/:trove">
        <Page>
          <ModifyLoan />
        </Page>
      </Route>
      <Route path="/redeem/:id">
        {chainId === 137 ? (
          <Redirect to={'/redeem'} />
        ) : (
          <Page>
            <Redeem />
          </Page>
        )}
      </Route>
      <Route path="/stability-pool/:id">
        <Page>{chainId === 1 ? <StabilityPool /> : <Depreciated />}</Page>
      </Route>
      {chainId !== 1 && (
        <Route path="/tokenization">
          <Page>
            <Tokenization />
          </Page>
        </Route>
      )}
      <Route path="/redeem">
        <Page>
          {chainId === 137 ? (
            <RedeemARTH />
          ) : (
            <Redirect to={`/redeem/${config[chainId].defaultCollateral}`} />
          )}
        </Page>
      </Route>
      <Route path="/faucet">
        {chainId === 5 || chainId === 80001 ? (
          <Page>
            <Faucet />
          </Page>
        ) : (
          <Redirect to={defaultUrl} />
        )}
      </Route>
      <Route path="/farming">
        <Page>
          <UniswapV3Gauges />
        </Page>
      </Route>
      <Route exact path="*">
        <Page>
          <NoPageFound />
        </Page>
      </Route>
    </Switch>
  );
};

export default Navigation;
