import 'aos/dist/aos.css';
import { SnackbarProvider } from 'notistack';
import React, { useEffect } from 'react';
import { Provider, useDispatch } from 'react-redux';
import { HashRouter as Router } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import { UseWalletProvider, useWallet } from 'use-wallet';

import Navigation from './Navigation';
import { isProduction } from './analytics/Mixpanel';
import Disclaimer from './components/Disclaimer';
import NoMetamaskNotice from './components/NoMetamaskNotice';
import Popups from './components/Popups';
import TopBar from './components/TopBar';
import config, { ConfigChains, getSupportedChains } from "./config";
import BasisCashProvider from './contexts/BasisCashProvider';
import ModalsProvider from './contexts/Modals';
import useCore from './hooks/useCore';
import store from './state';
import Updaters from './state/Updaters';
import * as analyticsState from './state/analytics/controller';
import { useGetActiveChainId, useGetUpdateActiveChainId } from './state/chains/hooks';
import theme from './theme';
import './theme.css';
import '@rainbow-me/rainbowkit/styles.css';

import {
  darkTheme,
  getDefaultWallets,
  RainbowKitProvider
} from "@rainbow-me/rainbowkit";
import {
  chain,
  configureChains,
  createClient, defaultChains,
  WagmiConfig
} from "wagmi";
import { publicProvider } from 'wagmi/providers/public';
import { alchemyProvider } from 'wagmi/providers/alchemy'
import ChainUpdater from "./components/ChainUpdater";
import {myCustomTheme} from "./utils/rainbowKitCustomTheme";


const Providers: React.FC = ({ children }) => {
  return (
    <ThemeProvider theme={theme}>
      <Provider store={store}>
        <WalletProvider>
          <RainbowProvider>
            {children}
          </RainbowProvider>
        </WalletProvider>
      </Provider>
    </ThemeProvider>
  );
};

const WalletProvider: React.FC = ({ children }) => {
  return (
    <UseWalletProvider
      connectors={{
        injected: {
          chainId: getSupportedChains(),
        },
        walletconnect: {
          bridge: 'https://bridge.walletconnect.org',
          pollingInterval: 12000,
          rpc: {
            137: 'https://polygon-rpc.com/',
            56: 'https://bsc-dataseed1.defibit.io/',
            1: 'https://shy-silent-grass.quiknode.pro/94c847bf5de231bb794ddf50954f49eae04b1f7c/',
          },
        },
      }}
    >
      <Updaters />
      <BasisCashProvider>
        <AppContent>{children}</AppContent>
      </BasisCashProvider>
    </UseWalletProvider>
  );
};

const RainbowProvider: React.FC = ({children}) => {
  const { chains, provider } = configureChains(ConfigChains, [
    // publicProvider(),
      alchemyProvider({ apiKey: 'yFIea42iOou-1yrxEjr4tcxVXODp0uNs' }),
      publicProvider()
  ]);

  const { connectors } = getDefaultWallets({
    appName: 'arth.mahadao.com',
    chains
  });

  const wagmiClient = createClient({
    autoConnect: true,
    connectors,
    provider
  })

  return (
    <WagmiConfig client={wagmiClient}>
      <RainbowKitProvider chains={chains}
                          theme={myCustomTheme}
                          showRecentTransactions={true}>
        {children}
      </RainbowKitProvider>
    </WagmiConfig>
  )
}

const App: React.FC = () => {
  const makeUnPassive = (ev: any) => {
    ev.preventDefault();
  };

  useEffect(() => {
    document.body.addEventListener('touchmove', makeUnPassive, { passive: true });
    return () => document.body.removeEventListener('touchmove', makeUnPassive);
  }, []);

  return (
    <Providers>
      <Router>
        <TopBar />
        <ChainUpdater/>
        <Disclaimer />
        <Navigation />
      </Router>
    </Providers>
  );
};

const AppContent: React.FC = ({ children }) => {
  const core = useCore();
  const { ethereum } = useWallet();
  const dispatch = useDispatch();
  const chainId = useGetActiveChainId();
  const setChainId = useGetUpdateActiveChainId();

  useEffect(() => {
    // @ts-ignore
    if (ethereum)
      // @ts-ignore
      ethereum.on('chainChanged', (chainId) => {
        console.log('chain changed', chainId);
        setChainId(chainId);
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [ethereum]);

  useEffect(() => {
    if (core) {
      analyticsState.init(core, dispatch, chainId);
    }
  }, [core, dispatch, chainId]);

  // if (!window.ethereum) return <NoMetamaskNotice />;
  if (!core) return <div />;

  return (
    <ModalsProvider>
      <SnackbarProvider
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        maxSnack={2}
        autoHideDuration={2500}
      >
        <>
          <Popups />
          {children}
        </>
      </SnackbarProvider>
    </ModalsProvider>
  );
};

// @ts-ignore
export default App;

if (isProduction) console.log = function () {};
