import { BigNumber } from 'ethers';
import { parseUnits } from 'ethers/lib/utils';
import React, { useMemo, useState } from 'react';
import Loader from 'react-spinners/BeatLoader';
import styled from 'styled-components';
import { useWallet } from 'use-wallet';

import Grid from '@material-ui/core/Grid';

import config from '../../../config';
import useGetTrove from '../../../hooks/state/troveManager/useGetTrove';
import useCollateralPriceFeed from '../../../hooks/state/useCollateralPriceFeed';
import useTokenDecimals from '../../../hooks/useTokenDecimals';
import { DECIMALS_18 } from '../../../protocol/constants';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../../theme';
import Button from '../../../theme/components/Button';
import DataField from '../../../theme/components/DataField';
import IconLoader from '../../../theme/components/IconLoader';
import SuccessModal from '../../../theme/components/SuccesModal';
import TextButton from '../../../theme/components/TextButton';
import TextWrapper from '../../../theme/components/TextWrapper';
import ToolTip from '../../../theme/components/Tooltip';
import { formatToBN, getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';
import ConfirmCloseLoanModal from '../modal/ConfirmCloseLoanModal';

interface Iprops {
  trove: string;
  ownerAddress: string;
  onModifyLoan: () => void;
}

const OpenedLaon = (props: Iprops) => {
  const { trove, onModifyLoan, ownerAddress } = props;
  const [openConfirmationModal, setOpenConfirmationModal] = useState<boolean>(false);
  const [closeLoanSuccessModal, setCloseLoanSuccessModal] = useState<boolean>(false);
  const chainId = useGetActiveChainId();
  const { account } = useWallet();
  const troveDetail = troveDetails(trove, chainId);
  const collateralGMUPrice = useCollateralPriceFeed(
    troveDetail.depositToken,
    troveDetail.contractPrefix,
  );
  const getTrove = useGetTrove(troveDetail.contractPrefix, ownerAddress);
  const arthUSDValue = {
    isLoading: false,
    value: formatToBN(2),
  };

  const depositTokenDecimals = useTokenDecimals(troveDetail.depositToken);

  const collateralRatio = useMemo(() => {
    if (collateralGMUPrice.isLoading) return { isLoading: true, value: BigNumber.from(0) };

    const debtAmountBN = getTrove.value?.debt || BigNumber.from(0);
    const collateralValueBN = getTrove.value?.coll || BigNumber.from(1);

    if (debtAmountBN.lte(0)) return { isLoading: false, vaue: BigNumber.from(0) };
    const ratio = collateralValueBN.mul(collateralGMUPrice.value).mul(100).div(debtAmountBN);

    return {
      isLoading: false,
      value: ratio,
    };
  }, [getTrove, collateralGMUPrice]);

  const liquidationPrice = useMemo(() => {
    if (collateralGMUPrice.isLoading || getTrove.isLoading)
      return { isLoading: true, value: BigNumber.from(0) };

    const collateralAmountBN = BigNumber.from(getTrove.value.coll);
    const liquidationCR = DECIMALS_18.mul(1099999).div(10000);

    if (collateralGMUPrice.value.lte(0) || collateralAmountBN.lte(0))
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };

    return {
      isLoading: false,
      value: liquidationCR.mul(getTrove.value.debt).div(collateralAmountBN).div(100),
    };
  }, [collateralGMUPrice.isLoading, collateralGMUPrice.value, getTrove.value]);

  return (
    <div>
      <ConfirmCloseLoanModal
        getTrove={getTrove.value}
        trove={trove}
        openModal={openConfirmationModal}
        onClose={() => setOpenConfirmationModal(false)}
        onSuccess={() => setCloseLoanSuccessModal(true)}
      />
      <div className={'custom-mahadao-container'}>
        <div className={'custom-mahadao-container-content'}>
          {ownerAddress !== account && (
            <div className={'m-b-32'}>
              <TextWrapper text={'Owner Address'} align={'center'} />
              <TextButton
                onClick={() =>
                  window.open(`${config[chainId].etherscanUrl}/address/${ownerAddress}`)
                }
                text={`${ownerAddress}`}
                align={'center'}
              />
            </div>
          )}
          <LaonInfoLine>
            <div className="single-line-center-between">
              <div className="single-line-center-start">
                <TextWrapper text={'Total Collateral'} fontSize={18} Fcolor={'#FFFFFF88'} />
              </div>
              <div className="single-line-center-start">
                {getTrove.isLoading ? (
                  <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
                ) : (
                  <TextWrapper
                    text={Number(
                      getDisplayBalance(getTrove.value.coll, depositTokenDecimals, 3),
                    ).toLocaleString()}
                    fontSize={18}
                    Fcolor={'#FFFFFF'}
                    fontWeight={600}
                  />
                )}
                <div className="tags m-l-4">
                  <TextWrapper
                    text={troveDetail.depositToken.toUpperCase()}
                    fontSize={18}
                    Fcolor={'#FFFFFF88'}
                    fontWeight={600}
                  />
                </div>
              </div>
            </div>
          </LaonInfoLine>
          <div className="text-center m-t-12 m-b-12">
            <IconLoader iconName={'ArrowTailDown'} iconType={'arrow'} />
          </div>
          <LaonInfoLine>
            <div className="single-line-center-between">
              <div className="single-line-center-start">
                <TextWrapper text={'Total Debt'} fontSize={18} Fcolor={'#FFFFFF88'} />
              </div>
              <div className="single-line-center-start">
                {getTrove.isLoading ? (
                  <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
                ) : (
                  <TextWrapper
                    text={Number(
                      getDisplayBalance(getTrove.value.debt, 18, 3),
                    ).toLocaleString()}
                    fontSize={18}
                    Fcolor={'#FFFFFF'}
                    fontWeight={600}
                  />
                )}
                <div className="tags m-l-4">
                  <TextWrapper
                    text={'ARTH'}
                    fontSize={18}
                    Fcolor={'#FFFFFF88'}
                    fontWeight={600}
                  />
                </div>
              </div>
            </div>
          </LaonInfoLine>
          <div className="m-t-20">
            <div className="single-line-center-between m-b-8">
              <div className="single-line-center-start">
                <TextWrapper text={'Collateral Ratio'} fontSize={12} />
                <ToolTip
                  toolTipText={
                    'The ratio between the ARTH value of the collateral and the debt (in ARTH) you are depositing. While the Minimum Collateral Ratio is 110% during normal operation, it is recommended to keep the Collateral Ratio always above 150% to avoid liquidation under Recovery Mode. A Collateral Ratio above 200% or 250% is recommended for additional safety.'
                  }
                />
              </div>
              <div className="single-line-center-start">
                {collateralRatio.isLoading ? (
                  <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
                ) : (
                  <TextWrapper
                    text={
                      Number(getDisplayBalance(collateralRatio.value, 18, 3)).toLocaleString() +
                      '%'
                    }
                    fontSize={12}
                    Fcolor={theme.color.green[300]}
                  />
                )}
              </div>
            </div>
            <div className="single-line-center-between m-b-8">
              <div className="single-line-center-start">
                <TextWrapper text={'Liquidation Reserve'} fontSize={12} />
                <ToolTip
                  toolTipText={
                    'An amount set aside to cover the liquidator’s gas costs if your loan needs to be liquidated. The amount increases your debt and is refunded if you close your loan by fully paying off its net debt.'
                  }
                />
              </div>
              <div className="single-line-center-start">
                <TextWrapper
                  text={
                    Number(
                      getDisplayBalance(troveDetail.liquidationReserve, 18, 3),
                    ).toLocaleString() + ' ARTH'
                  }
                  fontSize={12}
                  Fcolor={theme.color.green[300]}
                />
              </div>
            </div>
            <DataField
              label={'Liquidation Price'}
              isValueLoading={liquidationPrice.isLoading}
              value={`${Number(
                getDisplayBalance(liquidationPrice.value.mul(arthUSDValue.value), 18 * 2, 3),
              ).toLocaleString()} USD/${troveDetail.depositToken.toUpperCase()}`}
              labelFontColor={'white'}
              labelFontSize={12}
              valueFontSize={12}
              valueFontColor={theme.color.green[300]}
            />
          </div>
          {ownerAddress === account && (
            <div className={'m-t-32'}>
              <Grid container spacing={2}>
                <Grid item lg={6} md={6} sm={12} xs={12}>
                  <Button
                    onClick={() => setOpenConfirmationModal(true)}
                    tracking_id="close_loan"
                    tracking_params={{
                      collateral: trove,
                      action: 'initial',
                      amount: Number(getDisplayBalance(getTrove.value.coll, 18, 3)),
                      other: {
                        debt: Number(getDisplayBalance(getTrove.value.debt, 18, 3)),
                      },
                    }}
                    disabled={getTrove.value.coll.lte(0) || getTrove.value.debt.lte(0)}
                  >
                    Close Loan
                  </Button>
                </Grid>
                <Grid item lg={6} md={6} sm={12} xs={12}>
                  <Button
                    onClick={() => onModifyLoan()}
                    tracking_id="modify_loan"
                    tracking_params={{ collateral: trove }}
                    disabled={getTrove.value.coll.lte(0) || getTrove.value.debt.lte(0)}
                  >
                    Modify Loan
                  </Button>
                </Grid>
              </Grid>
            </div>
          )}
        </div>
      </div>

      <SuccessModal
        modalOpen={closeLoanSuccessModal}
        setModalOpen={() => {
          window.location.href = '/#/loans/overview';
          setCloseLoanSuccessModal(false);
        }}
        title={'Your transaction is being mined on blockchain'}
        buttonText={'Close'}
      />
    </div>
  );
};

export default OpenedLaon;

const LaonInfoLine = styled.div`
  background: rgba(255, 255, 255, 0.08);
  border-radius: 6px;
  padding: 14px 12px;
`;
