import detectEthereumProvider from '@metamask/detect-provider';
import React, { useCallback, useEffect, useState } from 'react';
import ReactGA from 'react-ga';
import { useDispatch } from 'react-redux';
import { useMediaQuery } from 'react-responsive';
import { useLocation } from "react-router-dom";
import styled from 'styled-components';
import { useWallet } from 'use-wallet';

import { Mixpanel } from '../../analytics/Mixpanel';
import useGaTracker from '../../analytics/useGATracker.js';
import config, { getSupportedChains } from '../../config';
import useCore from '../../hooks/useCore';
import '../../scripts/headerWarning.js';
import {
  useGetActiveChainId,
  useGetUpdateActiveChainId,
  useUpdateAvailableChains,
} from '../../state/chains/hooks';
import Button from '../../theme/components/Button';
import IconLoader from '../../theme/components/IconLoader';
import TextWrapper from '../../theme/components/TextWrapper';
import NetworkChange from '../NetworkChange';

import AccountButton from './components/AccountButton';
import MobileNav from './components/MobileNav';
import DesktopProjectRoutes from './components/modal/ProjectRoutes/DesktopProjectRoute';
import MobileProjectRoutes from './components/modal/ProjectRoutes/MobileProjectRoute';
import DesktopTransactionInfo from './components/modal/Transaction/DesktopTransactionInfo';
import { ConnectButton } from '@rainbow-me/rainbowkit';

export let showWarningGlobal = false;

const TopBar: React.FC = () => {
  useGaTracker();
  const core = useCore();
  const { account, connect, chainId } = useWallet();
  const activeChainId = useGetActiveChainId();
  const setAvailableChains = useUpdateAvailableChains();
  const dispatch = useDispatch();

  const [showTxModal, setShowTxModal] = useState<boolean>(false);
  const [showProjectModal, setShowProjectModal] = useState<boolean>(false);
  const [showMobileMenu, toggleMobileMenu] = useState<boolean>(false);
  const [showWarning, setShowWarning] = React.useState<boolean>(false);
  const isMobile = useMediaQuery({ maxWidth: '800px' });

  /*const processNetwork = useCallback(async () => {
    const provider: any = await detectEthereumProvider();

    if (provider) {
      const chainId = Number(await provider.request({ method: 'eth_chainId' }));
      if (getAvailableChains.includes(chainId)) {
        setChainId(chainId);
      } else {
        setShowWarning(true);
      }
    }
  }, [getAvailableChains, setChainId]);*/

  useEffect(() => {
    if (core) {
      core.updateActiveNetwork(activeChainId, dispatch);
    }
  }, [activeChainId, core, dispatch]);

  const location = useLocation();
  React.useEffect(() => {
    Mixpanel.track(`ScreenView:${location.pathname}`, {
      networkName: config[chainId]?.networkDisplayName || '',
    });
    ReactGA.pageview(location.pathname);
  }, [chainId, location.pathname]);

  useEffect(() => {
    const shouldBeDisconnected = localStorage.getItem('disconnectWallet');
    if (!shouldBeDisconnected && !!!account) connect('injected');

    if (account) {
      Mixpanel.identify(account);
      Mixpanel.people.set({ walletId: account });
    }

    // processNetwork();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, connect, core]);

  /*useEffect(() => {
    showWarningGlobal = showWarning;
  }, [showWarning]);*/

  useEffect(() => {
    setAvailableChains(getSupportedChains());
  }, [setAvailableChains]);

  return (
    <TopBarContainer>
      {/*<div id={'showWarning'} style={{ display: 'none' }}>
        {showWarning ? 'true' : 'false'}
      </div>*/}
      {/*<UpdateChainId/>*/}
      <DesktopTransactionInfo openModal={showTxModal} onDismiss={() => setShowTxModal(false)} />

      {isMobile ? (
        <MobileProjectRoutes
          openModal={showProjectModal}
          onDismiss={() => setShowProjectModal(false)}
        />
      ) : (
        <DesktopProjectRoutes
          openModal={showProjectModal}
          onDismiss={() => setShowProjectModal(false)}
        />
      )}
      <StyledTopBar>
        <StyledTopBarInner>
          <HideonPhone>
            <div className="single-line-center-between">
              <div className={'single-line-center-start'}>
                <IconLoader
                  iconName={'ARTHlg'}
                  iconType={'brandLogo'}
                  onClick={() => window.open('https://mahadao.com/arth', '_self')}
                />
                <div className={'tags m-l-8 m-t-4'}>
                  <TextWrapper text={'BETA'} fontSize={12} align={'center'} />
                </div>
              </div>
              <div className="single-line-center-start">
                <IconLoader
                  className={'pointer m-r-24'}
                  iconName={'BentoMenu'}
                  iconType={'misc'}
                  onClick={() => {
                    setShowProjectModal(true);
                  }}
                />
                <div className="m-r-12">
                  <Button
                    text={'Get MAHA'}
                    onClick={() =>
                      window.open(
                        'https://app.uniswap.org/#/swap?outputCurrency=0xb4d930279552397bba2ee473229f89ec245bc365',
                      )
                    }
                    tracking_id={'get_MAHA'}
                    tracking_params={{ position: 'header' }}
                  />
                </div>
                {!!account && (
                  <IconLoader
                    iconName={'Transaction'}
                    className={'pointer m-r-12'}
                    onClick={() => setShowTxModal(true)}
                  />
                )}
                <ConnectButton />
              </div>
            </div>
          </HideonPhone>
          <HideOnBigScreen>
            <div className="single-line-center-between">
              <IconLoader
                iconName={!showMobileMenu ? 'Menu' : 'Cross'}
                onClick={() => toggleMobileMenu(!showMobileMenu)}
                className={'pointer'}
              />
              <div className={'single-line-center-start'}>
                <IconLoader
                  iconName={'ARTHlg'}
                  iconType={'brandLogo'}
                  onClick={() => window.open('https://mahadao.com/arth', '_self')}
                />
                <div className={'tags m-l-8 m-t-4'}>
                  <TextWrapper text={'BETA'} fontSize={12} align={'center'} />
                </div>
              </div>
              <IconLoader
                iconName={'BentoMenu'}
                iconType={'misc'}
                onClick={() => {
                  setShowProjectModal(true);
                }}
              />
            </div>
          </HideOnBigScreen>
          <HideOnBigScreen>
            <MobileNav
              openMenu={showMobileMenu}
              showWarning={showWarning}
              isMainnet={true}
              onClick={() => toggleMobileMenu(!showMobileMenu)}
              onWalletClick={() => {}}
            />
          </HideOnBigScreen>
        </StyledTopBarInner>
      </StyledTopBar>
    </TopBarContainer>
  );
};

const TopBarContainer = styled.div`
  position: fixed;
  z-index: 999;
  display: flex;
  flex-direction: column;
  width: 100%;
  top: 0;
`;

const HideonPhone = styled.div`
  width: 100%;
  display: block;
  @media (max-width: 800px) {
    display: none;
  } ;
`;

const HideOnBigScreen = styled.div`
  width: 100%;
  display: none;
  @media (max-width: 800px) {
    display: block;
  } ;
`;

const StyledTopBar = styled.div`
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  background: rgba(0, 0, 0);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
`;

const StyledTopBarInner = styled.div`
  align-content: center;
  display: flex;
  height: 72px;
  justify-content: space-between;
  width: 100%;
  padding: 0 60px;
  flex-wrap: wrap;
  @media (max-width: 800px) {
    padding: 0 16px;
  }
`;

const WarningMsg = styled.div`
  display: block;
  background-color: #2a2827;
  padding: 12px 16px;
`;

export default TopBar;
