import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { useMediaQuery } from 'react-responsive';
import styled from 'styled-components';

import Grid from '@material-ui/core/Grid';

import useCore from '../../../hooks/useCore';
import { FarmingConfigurations } from '../../../protocol/config';
import { useGetActiveChainId } from '../../../state/chains/hooks';

import FarmingCard from './FarmingCard';

type apr = { [id: string]: number };
type tvl = { [id: string]: number };
type maxApr = { [id: string]: number };
type minApr = { [id: string]: number };

export interface IFarmingApiData {
  chainSpecificData: {
    [chainId: string]: {
      apr: apr;
      tvl: tvl;
      maxApr?: maxApr;
      minApr?: minApr;
    };
  };
  mahaRewardPerMinute: number;
}

export interface IFarmingApiDataState {
  isLoading: boolean;
  data: IFarmingApiData;
}

export const InitalFarmingApiDataState: IFarmingApiDataState = {
  isLoading: false,
  data: {
    chainSpecificData: {},
    mahaRewardPerMinute: 0,
  },
};

const FarmingCards = () => {
  const [farmingApiData, setFarmingApiData] = useState<IFarmingApiDataState>(
    InitalFarmingApiDataState,
  );

  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const isMobile = useMediaQuery({ maxWidth: '1280px' });

  const farmingPools: FarmingConfigurations[] = useMemo(
    () => core.getFarmingPools(activeChainId),
    [core, activeChainId],
  );

  const fetchAPY = useCallback(async () => {
    const url = `https://api.arthcoin.com/apy/loans`;
    const headers = {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET,POST,OPTIONS',
      'Access-Control-Allow-Headers':
        'access-control-allow-headers,access-control-allow-methods,access-control-allow-origin,content-type',
    };

    fetch(url, { headers })
      .then((res) => res.json())
      .then((res: IFarmingApiData) => {
        setFarmingApiData({ isLoading: false, data: res });
      })
      .catch((err) => {
        setFarmingApiData(InitalFarmingApiDataState);
      });
  }, []);

  useEffect(() => {
    fetchAPY();
  }, [fetchAPY]);

  return (
    <div className={'mo-custom-container'}>
      <Grid container className="m-t-24" spacing={2}>
        {farmingPools.map((cardData: FarmingConfigurations) => (
          <Grid item lg={4} md={6} sm={12} xs={12} key={cardData.name}>
            <FarmingCard farmingApiData={farmingApiData} cardData={cardData} />
          </Grid>
        ))}
      </Grid>
    </div>
  );

  /* return (
     <DataContainer className={'material-primary'}>
       {!isMobile && (
         <Grid container style={{ padding: '0px 32px ', marginBottom: '16px' }}>
           <Grid item lg={4}>
             <CustomTableHeading>Pair</CustomTableHeading>
           </Grid>
           <Grid item lg={2}>
             <CustomTableHeading>
               TVL
               <ToolTip
                 toolTipText={
                   'Calculated values represents the total amount of volume locked in terms of USD.'
                 }
               />
             </CustomTableHeading>
           </Grid>
           <Grid item lg={2}>
             <CustomTableHeading>
               APR
               <ToolTip
                 toolTipText={
                   'Calculated based on current rates. Rates are estimates provided for your convenience only, and may vary as per dexes multiplier.'
                 }
               />
             </CustomTableHeading>
           </Grid>
           <Grid item lg={2}>
             <CustomTableHeading>Reward</CustomTableHeading>
           </Grid>
           <Grid item lg={2} />
         </Grid>
       )}
       <div>
         {farmingPools.map((cardData: FarmingConfigurations) => (
           <FarmingCard farmingApiData={farmingApiData} cardData={cardData} />
         ))}
       </div>
     </DataContainer>
   );*/
};

const DataContainer = styled.div`
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(70px);
  border-radius: 6px;
  padding: 24px 32px;
  @media (max-width: 600px) {
    background: transparent;
    backdrop-filter: none;
    border-radius: 6px;
    padding: 32px 0;
  }
`;

const CustomTableHeading = styled.span`
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 150%;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.32);
  opacity: 0.64;
  text-align: left;
`;

export default FarmingCards;
