import { isProduction } from './analytics/Mixpanel';
import bsc from './config/bsc';
import bscTestnet from './config/bscTestnet';
import ethereum from './config/ethereum';
import polygon from './config/polygon';
import { Configuration } from './protocol/config';
import goerli from "./config/goerli";
import { chain } from 'wagmi';
import { bscChain } from "./utils/CustomChains";

let configurations: { [env: string]: Configuration } = {
  ...ethereum,
  ...bsc,
  ...goerli,
  // ...local,
  // ...bscTestnet,
  // ...polygonMumbai,
  ...polygon,
};

export let ConfigChains = [chain.mainnet, chain.polygon, bscChain, chain.goerli]

if (isProduction) {
  configurations = {
    ...ethereum,
    ...bsc,
    ...polygon,
  };
  ConfigChains = [chain.mainnet, chain.polygon, bscChain];
}

export default configurations;

export const getSupportedChains = (): number[] =>
  Object.keys(configurations).map((i) => Number(i));
