import React, { useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { useWallet } from 'use-wallet';

import Grid from '@material-ui/core/Grid';

import LoadingPage from '../../components/LoadingPage';
import useGetTrove from '../../hooks/state/troveManager/useGetTrove';
import { useGetActiveChainId } from '../../state/chains/hooks';
import Button from '../../theme/components/Button';
import IconLoader from '../../theme/components/IconLoader';
import PageHeader from '../../theme/components/PageHeader';
import TextWrapper from '../../theme/components/TextWrapper';
import { troveDetails } from '../../utils/tokenDetails';
import LoanInfo from '../LoanInfo/LoanInfo';

import OpenLoanDesc from './component/OpenLoanDesc';
import OpenedLaon from './component/OpenedLoan';

const LoanDetails = () => {
  // @ts-ignore
  const { trove, ownerAddress } = useParams();
  const chainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, chainId);
  const getTrove = useGetTrove(troveDetail.contractPrefix, ownerAddress);

  useEffect(() => {
    //ToDo: if the owner address is a valid address
  }, [ownerAddress]);

  if (getTrove.isLoading) {
    return (
      <div style={{ position: 'relative', height: 'calc(100vh - 100px)' }}>
        <LoadingPage />
      </div>
    );
  }

  if (getTrove.value.stake.lte(0)) {
    window.location.href = '/#/loans/your-loans';
    return (
      <div className="m-t-40 m-b-40 material-primary">
        <TextWrapper
          text={`You dont have any loan open for ${trove}`}
          fontSize={24}
          fontWeight={'bold'}
          className="m-b-8"
          align="center"
        />
        <TextWrapper
          text={'Open Interest free loan with leveraged position from the leverage strategies'}
          fontSize={18}
          className="m-b-8"
          Fcolor={'#FFFFFF64'}
          align={'center'}
        />
      </div>
    );
  }

  return (
    <div className="custom-container">
      <PageHeader
        title={`${trove.toUpperCase()} LOAN`}
        subTitle={`Borrow ARTH at 0% interest with ${trove.toUpperCase()} as collateral.`}
        showLearnMore={true}
        learnMoreLink={'https://docs.arthcoin.com/protocol/introduction-arth-loans'}
      />
      <Grid container style={{ marginTop: '24px' }} spacing={2}>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <OpenedLaon
            trove={trove}
            ownerAddress={ownerAddress}
            onModifyLoan={() => {
              window.open(`/#/loan/modify/${trove}`, '_self');
            }}
          />
        </Grid>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          {/* <OpenLoanDesc /> */}
          <LoanInfo trove={trove} />
        </Grid>
      </Grid>
    </div>
  );
};

export default LoanDetails;
