import { BigNumber } from 'ethers';
import React, { useMemo, useState } from 'react';

import Grid from '@material-ui/core/Grid';

import useOpenTrove from '../../../hooks/callbacks/borrowerOperations/useOpenTrove';
import useCore from '../../../hooks/useCore';
import useTokenDecimals from '../../../hooks/useTokenDecimals';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import InfoTip from '../../../theme/components/InfoTip';
import Modal from '../../../theme/components/Modal';
import SuccessModal from '../../../theme/components/SuccesModal';
import TextWrapper from '../../../theme/components/TextWrapper';
import ToolTip from '../../../theme/components/Tooltip';
import { formatToBN, getDisplayBalance } from '../../../utils/formatBalance';

import StepConfirmationModal from './StepConfirmationModal';

interface Iprops {
  selectedCollateralCoin: string;
  selectedContractPrefix: string;
  openModal: boolean;
  onClose: () => void;
  onSuccess: () => void;
  swapAmount: BigNumber;
  entryPrice: BigNumber;
  collateralValue: BigNumber;
  collateralAmount: BigNumber;
  liquidationPrice: BigNumber;
  leverage: number | string;
  collateralRatio: BigNumber;
  borrowingFee: BigNumber;
  totalDebt: BigNumber;
  liquidationReserve: BigNumber;
  borrowingFeeRate: BigNumber;
}

enum LoanTxState {
  NOT_COMPLETED,
  PENDING,
  COMPLETED,
}

const ConfirmLoanModal = (props: Iprops) => {
  const {
    selectedCollateralCoin,
    selectedContractPrefix,
    openModal,
    onClose,
    onSuccess,
    entryPrice,
    collateralValue,
    collateralAmount,
    liquidationPrice,
    leverage,
    collateralRatio,
    borrowingFee,
    totalDebt,
    liquidationReserve,
    borrowingFeeRate,
  } = props;

  const [openStepConfirmModal, setOpenStepConfirmModal] = useState<boolean>(false);
  const [openSuccessModal, setOpenSuccessModal] = useState<boolean>(false);
  const [loanTxState, setLoanTxState] = useState<LoanTxState>(LoanTxState.NOT_COMPLETED);

  const tokenDecimals = useTokenDecimals(selectedCollateralCoin);
  const arthUSDValue = {
    isLoading: false,
    value: formatToBN(2),
  };
  const openTrove = useOpenTrove(
    selectedContractPrefix,
    borrowingFeeRate,
    totalDebt.sub(liquidationReserve).sub(borrowingFee),
    collateralAmount,
  );

  const handleButtonCallback = () => {
    setLoanTxState(LoanTxState.COMPLETED);
    const handler = setTimeout(() => {
      onSuccess();
    }, 1000);

    return () => {
      clearTimeout(handler);
    };
  };

  const handleButtonClick = () => {
    setLoanTxState(LoanTxState.PENDING);
    openTrove(handleButtonCallback, () => setLoanTxState(LoanTxState.NOT_COMPLETED));
  };

  return (
    <>
      <Modal title={'Confirm Loan'} open={openModal} handleClose={() => onClose()} closeButton>
        <div>
          <div className="single-line-center-between m-b-8">
            <div className="single-line-center-start">
              <TextWrapper
                text={'Your collateral amount as supply'}
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
              />
              <ToolTip
                toolTipText={`The total amount of ${selectedCollateralCoin} you are providing to the protocol to borrow ARTH at the given Collateral Ratio.`}
              />
            </div>

            <div className="single-line-center-between">
              <TextWrapper
                text={
                  Number(getDisplayBalance(collateralAmount, 18, 3)).toLocaleString() +
                  '(' +
                  Number(getDisplayBalance(collateralValue, 18, 3)).toLocaleString() +
                  ' ARTH)'
                }
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
                className={'m-r-4'}
              />
              <div className="tags">
                <TextWrapper
                  text={selectedCollateralCoin.toUpperCase()}
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              </div>
            </div>
          </div>
          <div className="single-line-center-between">
            <div className="single-line-center-start">
              <TextWrapper text={'You will recieve'} fontWeight={600} Fcolor={'#FFFFFF88'} />
              <ToolTip
                toolTipText={`The total amount of ARTH you are able to borrow. This is the amount of ARTH you will recieve after removing borrowing fee and liquidation reserve from the total debt of your loan.`}
              />
            </div>
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(
                  getDisplayBalance(totalDebt.sub(borrowingFee).sub(liquidationReserve), 18, 3),
                ).toLocaleString()}
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
                className={'m-r-4'}
              />
              <div className="tags ">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="bottom-divider m-b-24 m-t-24" />
          <div className="single-line-center-between m-b-8">
            <div className="single-line-center-start">
              <TextWrapper text={'Entry price'} fontWeight={600} Fcolor={'#FFFFFF88'} />
              <ToolTip
                toolTipText={`The current price of GMU Value of ${selectedCollateralCoin.toUpperCase()}. This is the price at which your position on ${selectedCollateralCoin.toUpperCase()} will be opened.`}
              />
            </div>
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(getDisplayBalance(entryPrice, 18, 3)).toLocaleString('en-US', {
                  minimumFractionDigits: 3,
                })}
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
                className={'m-r-4'}
              />
              <div className="tags ">
                <TextWrapper
                  text={`${selectedCollateralCoin.toUpperCase()}/ARTH`}
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              </div>
            </div>
          </div>
          <div className="single-line-center-between m-b-2">
            <div className="single-line-center-start">
              <TextWrapper text={'Liquidation price'} fontWeight={600} Fcolor={'#FFFFFF88'} />
              <ToolTip
                toolTipText={`The GMU value of ${selectedCollateralCoin.toUpperCase()} below which your position can be Liquidated.`}
              />
            </div>
            <div className="single-line-center-between">
              <TextWrapper
                text={'~' + Number(getDisplayBalance(liquidationPrice, 18, 3)).toLocaleString()}
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
                className={'m-r-4'}
              />
              <div className="tags ">
                <TextWrapper
                  text={`ARTH/${selectedCollateralCoin.toUpperCase()}`}
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              </div>
            </div>
          </div>
          <div className="single-line-center-end">
            <div className="single-line-center-end">
              <TextWrapper
                text={
                  '~' +
                  Number(
                    getDisplayBalance(liquidationPrice.mul(arthUSDValue.value), 18 * 2, 3),
                  ).toLocaleString()
                }
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
                className={'m-r-4'}
              />
              <div className="tags ">
                <TextWrapper
                  text={`USD/${selectedCollateralCoin.toUpperCase()}`}
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              </div>
            </div>
          </div>
          <div className="bottom-divider m-b-24 m-t-24" />
          <div className="single-line-center-between m-b-8">
            <div className="single-line-center-start">
              <TextWrapper text={'Collateral Ratio'} fontWeight={600} Fcolor={'#FFFFFF88'} />
              <ToolTip
                toolTipText={`The ratio of the GMU value of the entire system collateral at the current ARTH/${selectedCollateralCoin.toUpperCase()} price, to the entire system debt.`}
              />
            </div>
            <TextWrapper
              text={Number(getDisplayBalance(collateralRatio, 18, 3)).toLocaleString() + '%'}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
          </div>
          <div className="single-line-center-between m-b-8">
            <div className="single-line-center-start">
              <TextWrapper text={'One-time Fee'} fontWeight={600} Fcolor={'#FFFFFF88'} />
              <ToolTip
                toolTipText={`The One-time Fee is a one-off fee charged as a percentage of the borrowed amount (in ARTH) and is part of a loan's debt. The fee varies between 0.5% and 5% depending on ARTH redemption volumes.`}
              />
            </div>
            <div className="single-line-center-between">
              <TextWrapper
                text={
                  Number(getDisplayBalance(borrowingFee, 18, 3)).toLocaleString() +
                  ' (' +
                  Number(getDisplayBalance(borrowingFeeRate, 16, 3)).toLocaleString() +
                  '%) '
                }
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
                className={'m-r-4'}
              />
              <div className="tags ">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="single-line-center-between">
            <div className="single-line-center-start">
              <TextWrapper text={'Liquidation Reserve'} fontWeight={600} Fcolor={'#FFFFFF88'} />
              <ToolTip
                toolTipText={
                  'An amount set aside to cover the liquidator’s gas costs if your loan needs to be liquidated. The amount increases your debt and is refunded if you close your loan by fully paying off its net debt.'
                }
              />
            </div>
            <div className="single-line-center-between">
              <TextWrapper
                text={Number(getDisplayBalance(liquidationReserve, 18, 3)).toLocaleString()}
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
                className={'m-r-4'}
              />
              <div className="tags ">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="bottom-divider m-b-24 m-t-24" />
          <div className="single-line-center-between m-b-32">
            <div className="single-line-center-start">
              <TextWrapper text={'Total Debt'} fontWeight={600} Fcolor={'#FFFFFF88'} />
              <ToolTip toolTipText={`The total amount of ARTH your loan will hold.`} />
            </div>

            <div className="single-line-center-between">
              <TextWrapper
                text={Number(getDisplayBalance(totalDebt, 18, 3)).toLocaleString()}
                fontWeight={600}
                Fcolor={'#FFFFFF88'}
                className={'m-r-4'}
              />
              <div className="tags ">
                <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
              </div>
            </div>
          </div>
          <div className="m-b-4">
            <InfoTip
              type={'Warning'}
              msg={`To break even, you should close your position at a price no less than ${Number(
                getDisplayBalance(entryPrice, tokenDecimals, 3),
              ).toLocaleString()} ARTH/${selectedCollateralCoin.toUpperCase()}`}
            />
          </div>
          <div className="m-b-32">
            <InfoTip
              type={'Warning'}
              msg={`To avoid liquidation, you should close your position at a price no less than ${Number(
                getDisplayBalance(liquidationPrice, tokenDecimals, 3),
              ).toLocaleString()} ARTH/${selectedCollateralCoin.toUpperCase()}`}
            />
          </div>
          <Grid container spacing={2}>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                variant={'transparent'}
                onClick={() => onClose()}
                tracking_id="take_loan"
                tracking_params={{
                  action: 'cancel',
                  collateral: selectedCollateralCoin,
                  amount: Number(getDisplayBalance(collateralAmount, tokenDecimals, 3)),
                }}
              >
                Back
              </Button>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                onClick={handleButtonClick}
                disabled={
                  loanTxState === LoanTxState.COMPLETED || loanTxState === LoanTxState.PENDING
                }
                loading={loanTxState === LoanTxState.PENDING}
                tracking_id="open_loan"
                tracking_params={{
                  collateral: selectedCollateralCoin,
                  amount: Number(getDisplayBalance(collateralAmount, 18, 3)),
                }}
              >
                {loanTxState === LoanTxState.COMPLETED ? 'Loan Opened' : 'Take Loan'}
              </Button>
              {/*<Button
                onClick={() => {
                  onClose();
                  setTimeout(() => {
                    setOpenStepConfirmModal(true);
                  }, 100);
                }}
                tracking_id="take_loan"
                tracking_params={{
                  action: 'confirm',
                  collateral: selectedCollateralCoin,
                  amount: Number(getDisplayBalance(collateralAmount, tokenDecimals, 3)),
                }}
              >
                Take Loan
              </Button>*/}
            </Grid>
          </Grid>
        </div>
      </Modal>
      <StepConfirmationModal
        totalDebt={totalDebt}
        selectedContractPrefix={selectedContractPrefix}
        liquidationReserve={liquidationReserve}
        borrowingFee={borrowingFee}
        borrowingFeeRate={borrowingFeeRate}
        collateralAmount={collateralAmount}
        selectedCollateralCoin={selectedCollateralCoin}
        openModal={openStepConfirmModal}
        onClose={() => setOpenStepConfirmModal(false)}
        onSuccess={() => {
          setOpenStepConfirmModal(false);
          setTimeout(() => {
            window.location.href = '/#/loans/your-loans';
            // setOpenSuccessModal(true)
          }, 100);
        }}
      />

      <SuccessModal
        modalOpen={openSuccessModal}
        setModalOpen={() => {
          onSuccess();
          setOpenSuccessModal(false);
        }}
        title={'Your transaction is being mined on blockchain'}
        buttonText={'Close'}
        buttonHref={''}
      />
    </>
  );
};

export default ConfirmLoanModal;
