import { BigNumber } from 'ethers';
import React, { useMemo, useState } from 'react';
import styled from 'styled-components';

import { Checkbox, CheckboxProps, FormControlLabel } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

import useOpenTrove from '../../../hooks/callbacks/borrowerOperations/useOpenTrove';
import useApprove, { ApprovalState } from '../../../hooks/callbacks/useApprove';
import useCore from '../../../hooks/useCore';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import IconLoader from '../../../theme/components/IconLoader';
import Modal from '../../../theme/components/Modal';
import TextWrapper from '../../../theme/components/TextWrapper';
import { getDisplayBalance } from '../../../utils/formatBalance';

const OrangeCheckBox = withStyles({
  root: {
    color: 'rgba(255, 255, 255, 0.32)',
    '&$checked': {
      color: '#FF7F57',
    },
  },
  checked: {
    color: 'white',
  },
})((props: CheckboxProps) => <Checkbox {...props} />);

interface Iprops {
  openModal: boolean;
  onClose: () => void;
  onSuccess: () => void;
  selectedCollateralCoin: string;
  selectedContractPrefix: string;
  totalDebt: BigNumber;
  liquidationReserve: BigNumber;
  borrowingFee: BigNumber;
  borrowingFeeRate: BigNumber;
  collateralAmount: BigNumber;
}

enum LoanTxState {
  NOT_COMPLETED,
  PENDING,
  COMPLETED,
}

const StepConfirmationModal = (props: Iprops) => {
  const {
    openModal,
    onClose,
    selectedCollateralCoin,
    selectedContractPrefix,
    totalDebt,
    liquidationReserve,
    borrowingFee,
    borrowingFeeRate,
    collateralAmount,
    onSuccess,
  } = props;

  const [loanTxState, setLoanTxState] = useState<LoanTxState>(LoanTxState.NOT_COMPLETED);

  const core = useCore();
  const activeChainId = useGetActiveChainId();

  const [collatApproveStatus, approveCollateral] = useApprove(
    core.tokens(selectedCollateralCoin, activeChainId),
    core.getBorrowerOperations(selectedContractPrefix, activeChainId).address,
  );

  const isCollatApproved = useMemo(() => collatApproveStatus === ApprovalState.APPROVED, [
    collatApproveStatus,
  ]);
  const isCollatApproving = useMemo(() => collatApproveStatus === ApprovalState.PENDING, [
    collatApproveStatus,
  ]);

  const openTrove = useOpenTrove(
    selectedContractPrefix,
    borrowingFeeRate,
    totalDebt.sub(liquidationReserve).sub(borrowingFee),
    collateralAmount,
  );

  const handleButtonCallback = () => {
    setLoanTxState(LoanTxState.COMPLETED);
    const handler = setTimeout(() => {
      onSuccess();
    }, 1000);

    return () => {
      clearTimeout(handler);
    };
  };

  const handleButtonClick = () => {
    setLoanTxState(LoanTxState.PENDING);
    openTrove(handleButtonCallback, () => setLoanTxState(LoanTxState.NOT_COMPLETED));
  };

  return (
    <Modal
      title={'Follow Transactions Steps'}
      open={openModal}
      handleClose={() => onClose()}
      closeButton
    >
      <div>
        <div className="m-b-32">
          <CheckboxDiv>
            <FormControlLabel
              checked={isCollatApproved}
              control={
                <OrangeCheckBox
                  icon={
                    isCollatApproved ? (
                      <IconLoader iconType="checkbox" iconName="Empty" />
                    ) : (
                      <StepNo className="tags">1</StepNo>
                    )
                  }
                  checkedIcon={<IconLoader iconType="checkbox" iconName="GreenCheck" />}
                  size={'small'}
                />
              }
              label={'Approve Transaction'}
              labelPlacement="end"
            />
          </CheckboxDiv>
          <div style={{ margin: '-12px 0px 20px 31px' }}>
            <TextWrapper
              text={'Approve the transaction using your wallet.'}
              fontSize={16}
              Fcolor={'#FFFFFF88'}
            />
          </div>
          <Button
            onClick={approveCollateral}
            disabled={isCollatApproving || isCollatApproved}
            loading={isCollatApproving}
            tracking_id="approve_collateral_loan"
            tracking_params={{ collateral: selectedCollateralCoin }}
          >
            {isCollatApproved ? 'Completed' : 'Complete Transaction'}
          </Button>
        </div>
        <div className="m-b-40">
          <CheckboxDiv>
            <FormControlLabel
              checked={loanTxState === LoanTxState.COMPLETED}
              control={
                <OrangeCheckBox
                  icon={
                    loanTxState === LoanTxState.COMPLETED ? (
                      <IconLoader iconType="checkbox" iconName="Empty" />
                    ) : (
                      <StepNo className="tags">2</StepNo>
                    )
                  }
                  checkedIcon={<IconLoader iconType="checkbox" iconName="GreenCheck" />}
                  size={'small'}
                />
              }
              label={'Open a position'}
              labelPlacement="end"
            />
          </CheckboxDiv>
          <div style={{ margin: '-12px 0px 20px 31px' }}>
            <TextWrapper
              text={'This is the final transaction to open loan.'}
              fontSize={16}
              Fcolor={'#FFFFFF88'}
            />
          </div>
          <Button
            onClick={handleButtonClick}
            disabled={
              loanTxState === LoanTxState.COMPLETED ||
              loanTxState === LoanTxState.PENDING ||
              !isCollatApproved
            }
            loading={loanTxState === LoanTxState.PENDING}
            tracking_id="open_loan"
            tracking_params={{
              collateral: selectedCollateralCoin,
              amount: Number(getDisplayBalance(collateralAmount, 18, 3)),
            }}
          >
            {loanTxState === LoanTxState.COMPLETED ? 'Completed' : 'Complete Transaction'}
          </Button>
        </div>
      </div>
    </Modal>
  );
};

export default StepConfirmationModal;

const CheckboxDiv = styled.div`
  background: transparent;
  border-radius: 6px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: rgba(255, 255, 255, 0.88);
  margin: 15px 0 0 0;
`;

const StepNo = styled.div`
  width: 24px;
  height: 24px;
  font-size: 12px;
  font-weight: 600;
`;
