import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useWallet } from 'use-wallet';

import { BNZERO } from '../../protocol/constants';
import { AppState } from '../../state';
import { useBlockNumber } from '../../state/application/hooks';
import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';
import useTokenDecimals from '../useTokenDecimals';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (token: string, contractPrefix: string) => {
  const [data, setData] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();
  const activeChainId = useGetActiveChainId();
  const tokenDecimals = useTokenDecimals(token);
  // const gmuPrice = useSelector((state: AppState) => state.analytics.gmuPrice);

  const fetchValue = useCallback(async () => {
    if (!account) {
      setData({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const contract = core.getPriceFeed(contractPrefix, activeChainId);
    const price: BigNumber = await contract.callStatic.fetchPrice();
    const calcValue = price.div(BigNumber.from(10).pow(18 - (tokenDecimals || 18)));

    if (!data.value.eq(calcValue)) {
      setData({
        isLoading: false,
        value: calcValue,
      });
    }
  }, [account, core, contractPrefix, activeChainId, tokenDecimals, data.value]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch price: ${err}`));
    }
  }, [blockNumber, core.isUnlocked, fetchValue]);

  return data;
};
