import { BigNumber } from '@ethersproject/bignumber';
import { parseUnits } from 'ethers/lib/utils';
import React, { useState } from 'react';
import { useMediaQuery } from 'react-responsive';
import styled from 'styled-components';

import Grid from '@material-ui/core/Grid';

import CustomInputContainer from '../../../components/CustomInputContainer';
import { ValidateNumber } from '../../../components/CustomInputContainer/RegexValidation';
import useBasicStakingDeposit from '../../../hooks/callbacks/farming/useBasicStakingDeposit';
import useApprove, { ApprovalState } from '../../../hooks/callbacks/useApprove';
import useCore from '../../../hooks/useCore';
import { FarmingConfigurations } from '../../../protocol/config';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import Modal from '../../../theme/components/Modal';
import { getDisplayBalance, getDisplayBalanceToken } from '../../../utils/formatBalance';

interface IProps {
  open: boolean;
  poolInfo: FarmingConfigurations;
  onCancel: () => void;
  onDeposit?: () => void;
  tokenBalance: BigNumber;
}

export default (props: IProps) => {
  const isMobile = useMediaQuery({ maxWidth: '600px' });
  const chainId = useGetActiveChainId();
  const [val, setValue] = useState<string>('0');
  const [isInputFieldError, setIsInputFieldError] = useState<boolean>(false);

  const core = useCore();
  const token = core.tokens(props.poolInfo.depositToken, chainId);
  const [approveStatus, approve] = useApprove(
    token,
    core.contracts(props.poolInfo.contract, chainId)?.address || '',
  );

  const depositAction = useBasicStakingDeposit(
    props.poolInfo.contract,
    BigNumber.from(parseUnits(val, token.decimal)),
    token,
  );

  const handleDeposit = () => {
    depositAction(() => {
      props.onCancel();
    });
  };

  const isApproved = approveStatus === ApprovalState.APPROVED;
  const isApproving = approveStatus === ApprovalState.PENDING;

  return (
    <Modal
      closeButton
      handleClose={props.onCancel}
      open={props.open}
      modalTitleStyle={{}}
      modalContainerStyle={{}}
      modalBodyStyle={{}}
      title={`Stake Your Tokens`}
    >
      <div>
        <CustomInputContainer
          ILabelValue={`How much ${props.poolInfo.depositToken} would you like to supply?`}
          IBalanceValue={getDisplayBalanceToken(props.tokenBalance, token, 6)}
          showBalance={false}
          ILabelInfoValue={''}
          DefaultValue={String(val)}
          LogoSymbol={props.poolInfo.depositToken}
          hasDropDown={false}
          SymbolText={props.poolInfo.depositToken}
          setText={(t) => {
            setValue(ValidateNumber(t) ? t : '0');
          }}
          inputMode={'decimal'}
          tagText={'MAX'}
          dontShowBackgroundContainer={true}
          multiIcons={props.poolInfo.depositToken !== 'single'}
          symbols={props.poolInfo.depositTokenSymbols}
          errorCallback={(flag: boolean) => {
            setIsInputFieldError(flag);
          }}
          tokenDecimals={token.decimal}
        />
        <OneLine>
          <div style={{ flex: 1 }} />
          <OneLine>
            <BeforeChip>
              Balance:{' '}
              {Number(
                getDisplayBalanceToken(props.tokenBalance, token, 6),
              ).toLocaleString('en-US', { minimumFractionDigits: 6 })}
            </BeforeChip>
            <TagChips>{props.poolInfo.depositToken}</TagChips>
          </OneLine>
        </OneLine>
        <Grid
          container
          spacing={2}
          direction={isMobile ? 'column-reverse' : 'row'}
          style={{ marginTop: '32px' }}
        >
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              variant={'transparent'}
              text="Cancel"
              size={'lg'}
              onClick={props.onCancel}
              tracking_id={'farming_deposit'}
              tracking_params={{
                action: 'cancel',
                collateral: props.poolInfo.depositToken,
                amount: Number(val),
              }}
            />
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            {!isApproved ? (
              <Button
                loading={isApproving}
                text={isApproving ? 'Approving' : 'Approve'}
                size={'lg'}
                onClick={approve}
                disabled={isInputFieldError || isApproved || !Number(val)}
                tracking_id={'farming_approve'}
                tracking_params={{
                  collateral: props.poolInfo.depositToken,
                }}
              />
            ) : (
              <Button
                disabled={isInputFieldError || !Number(val)}
                text={'Deposit'}
                size={'lg'}
                onClick={handleDeposit}
                tracking_id={'farming_deposit'}
                tracking_params={{
                  action: 'confirm',
                  collateral: props.poolInfo.depositToken,
                  amount: Number(val),
                }}
              />
            )}
          </Grid>
        </Grid>
      </div>
    </Modal>
  );
};

const OneLine = styled.div`
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: flex-start;
  margin: 5px 0;
`;

const BeforeChip = styled.span`
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 150%;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.64);
  margin-right: 5px;
`;

const TagChips = styled.div`
  background: rgba(255, 255, 255, 0.08);
  border-radius: 4px;
  padding: 2px 8px;
  font-family: Inter;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.64);
`;
