import * as Sentry from '@sentry/react';
import { BigNumber } from 'ethers';
import { useCallback } from 'react';

import { DECIMALS_18 } from '../../../protocol/constants';
import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import { getDisplayBalance } from '../../../utils/formatBalance';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import { findHintsForNominalCollateralRatio } from '../../state/hinthelpers/helper';
import useCore from '../../useCore';

export default function (
  symbol: string,
  feeRate: BigNumber,
  withdrawARTHAmount: BigNumber,
  debtAmount: BigNumber, // Total debt after the modification.
  collateralAmount: BigNumber, // Total collateral after the modification.
) {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const activeChainId = useGetActiveChainId();

  return useCallback(
    async (callback?: () => void): Promise<void> => {
      try {
        const contract = core.getBorrowerOperations(symbol, activeChainId);

        const [
          partialRedemptionUpperHint,
          partialRedemptionLowerHint,
        ]: string[] = await findHintsForNominalCollateralRatio(
          collateralAmount.mul(DECIMALS_18.mul(100)).div(debtAmount),
          core.getSortedTroves(symbol, activeChainId),
          core.getHintHelper(symbol, activeChainId),
          core.getTroveManager(symbol, activeChainId),
          core.myAccount,
        );

        const gasLimit = (
          await contract.estimateGas.withdrawARTH(
            feeRate,
            withdrawARTHAmount,
            partialRedemptionUpperHint,
            partialRedemptionLowerHint,
          )
        )
          .mul(120)
          .div(100);

        const response = await contract.withdrawARTH(
          feeRate,
          withdrawARTHAmount,
          partialRedemptionUpperHint,
          partialRedemptionLowerHint,
          { gasLimit },
        );

        addTransaction(response, {
          summary: `Generate ${Number(
            getDisplayBalance(withdrawARTHAmount, 18, 3),
          ).toLocaleString()} ARTH from Loan on ${symbol.toUpperCase()}`,
        });

        if (response.status !== 1) {
          Sentry.captureException({
            name: 'useWithdrawARTH',
            response,
            feeRate,
            withdrawARTHAmount,
            debtAmount,
            collateralAmount,
          });
        }

        if (callback) callback();
      } catch (e) {
        Sentry.captureException({
          name: 'useWithdrawARTH',
          ...e,
        });
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [
      symbol,
      core,
      addTransaction,
      addPopup,
      withdrawARTHAmount,
      debtAmount,
      collateralAmount,
      feeRate,
      activeChainId,
    ],
  );
}
