import { configureStore, getDefaultMiddleware } from '@reduxjs/toolkit';
import { load, save } from 'redux-localstorage-simple';
import { createLogger } from 'redux-logger';

import { isProduction } from '../analytics/Mixpanel';

import analytics from './analytics/reducer';
import application from './application/reducer';
import chains from './chains/reducer';
import slippage from './slippage/reducer';
import token from './token/reducer';
import transactions from './transactions/reducer';
import trove from './trove/reducer';

const PERSISTED_KEYS: string[] = ['transactions', 'slippage', 'chains'];

const store = configureStore({
  reducer: {
    application,
    transactions,
    trove,
    token,
    slippage,
    analytics,
    chains,
  },
  middleware: isProduction
    ? [
        ...getDefaultMiddleware({ serializableCheck: false, thunk: false }),
        save({ states: PERSISTED_KEYS }),
      ]
    : [
        ...getDefaultMiddleware({ serializableCheck: false, thunk: false }),
        save({ states: PERSISTED_KEYS }),
        // createLogger(),
      ],
  preloadedState: load({ states: PERSISTED_KEYS }),
});

export default store;

export type AppState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
