import React from 'react';
import styled from 'styled-components';
import { useWallet } from 'use-wallet';

import Grid from '@material-ui/core/Grid';

import useFaucetClaim from '../../hooks/callbacks/useFaucetClaim';
import Button from '../../theme/components/Button';
import PageHeader from '../../theme/components/PageHeader';

const Faucet = () => {
  const { account, connect } = useWallet();

  const claimTokens = useFaucetClaim();
  const isWalletConnected = !!account;

  return (
    <div className="custom-container">
      <PageHeader title={'ARTH faucet'} subTitle={'Get tokens to test ARTH V2.0'} />
      <Grid container>
        <Grid item lg={6} md={12} sm={12} xs={12}>
          <CustomInfoCard className={'material-primary'}>
            <PrimaryText>Claim your Test Tokens here</PrimaryText>

            <Grid container lg={12} style={{ marginTop: '32px' }}>
              <Grid item lg={4} md={4} sm={6} xs={6}>
                {!isWalletConnected ? (
                  <Button
                    text={'Connect Wallet'}
                    size={'lg'}
                    onClick={() =>
                      connect('injected').then(() => {
                        localStorage.removeItem('disconnectWallet');
                      })
                    }
                  />
                ) : (
                  <Button variant={'default'} text="Submit" size={'sm'} onClick={claimTokens} />
                )}
              </Grid>
            </Grid>
          </CustomInfoCard>
        </Grid>
      </Grid>
    </div>
  );
};

const CustomInfoCard = styled.div`
  @media (max-width: 600px) {
  }
`;

const PrimaryText = styled.p`
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.88);
  margin-bottom: 12px;
`;

export default Faucet;
