import { BigNumber } from 'ethers';
import { parseUnits } from 'ethers/lib/utils';
import React, { useMemo, useState } from 'react';
import Loader from 'react-spinners/BeatLoader';
import { useWallet } from 'use-wallet';

import CustomInputContainer from '../../components/CustomInputContainer';
import { ValidateNumber } from '../../components/CustomInputContainer/RegexValidation';
import useApprove, { ApprovalState } from '../../hooks/callbacks/useApprove';
import useGetBorrowingFeeRateWithDecay from '../../hooks/state/troveManager/useGetBorrowingFeeRateWithDecay';
import useGetIsRecoveryMode from '../../hooks/state/troveManager/useGetIsRecoveryMode';
import useGetTrove from '../../hooks/state/troveManager/useGetTrove';
import useCollateralPriceFeed from '../../hooks/state/useCollateralPriceFeed';
import useTokenBalance from '../../hooks/state/useTokenBalance';
import useCore from '../../hooks/useCore';
import {
  ARTH_GAS_COMPENSATION,
  BNZERO,
  DECIMALS_18,
  MIN_NET_DEBT,
} from '../../protocol/constants';
import { useGetActiveChainId } from '../../state/chains/hooks';
import theme from '../../theme';
import Button from '../../theme/components/Button';
import InfoTip from '../../theme/components/InfoTip';
import SuccessModal from '../../theme/components/SuccesModal';
import TextWrapper from '../../theme/components/TextWrapper';
import ToolTip from '../../theme/components/Tooltip';
import { crParameters, getDisplaycrParameters } from '../../utils/crParameters';
import { formatToBN, getDisplayBalance } from '../../utils/formatBalance';
import { troveDetails } from '../../utils/tokenDetails';

import ConfirmGenerateARTH from './modal/ConfirmGenerateARTH';
import ConfirmPaybackARTH from './modal/ConfirmPaybackARTH';

interface Iprops {
  trove: string;
}

const DebtModifyLoan = (props: Iprops) => {
  const { trove } = props;
  const currentSymbol = 'ARTH';

  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);

  const [paybackDebtAmount, setPaybackDebtAmount] = useState<string>('0');
  const [generateDebtAmount, setGenerateDebtAmount] = useState<string>('0');
  const [isPaybackFieldError, setIsPaybackFieldError] = useState<boolean>(false);
  const [openPaybackConfirmationModal, setOpenPaybackConfirmationModal] = useState<boolean>(
    false,
  );
  const [openGenerateConfirmationModal, setOpenGenerateConfirmationModal] = useState<boolean>(
    false,
  );
  const [openSuccessModal, setOpenSuccessModal] = useState<boolean>(false);
  const { account } = useWallet();
  const getTrove = useGetTrove(trove, account);
  const isRecoveryMode = useGetIsRecoveryMode(troveDetail.depositToken);
  const borrowRate = useGetBorrowingFeeRateWithDecay(troveDetail.depositToken);
  const collateralGMUPrice = useCollateralPriceFeed(
    troveDetail.depositToken,
    troveDetail.contractPrefix,
  );

  const [collatApproveStatus, approveCollateral] = useApprove(
    core.tokens('ARTH', activeChainId),
    core.getController(troveDetail.contractPrefix, activeChainId)?.address || undefined,
  );

  const isARTHApproved = useMemo(() => collatApproveStatus === ApprovalState.APPROVED, [
    collatApproveStatus,
  ]);
  const isARTHApproving = useMemo(() => collatApproveStatus === ApprovalState.PENDING, [
    collatApproveStatus,
  ]);

  const arthBalance = useTokenBalance(core.tokens(currentSymbol, activeChainId));

  const collateralRatio = useMemo(() => {
    if (collateralGMUPrice.isLoading || getTrove.isLoading)
      return { isLoading: true, value: BigNumber.from(0) };

    if (getTrove.value.debt.lte(0))
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };

    const ratio = getTrove.value.coll
      .mul(collateralGMUPrice.value)
      .mul(100)
      .div(getTrove.value.debt);
    return {
      isLoading: false,
      value: ratio,
    };
  }, [getTrove, collateralGMUPrice]);

  const modifiedCollatrealRatio = useMemo(() => {
    if (collateralGMUPrice.isLoading || getTrove.isLoading)
      return { isLoading: true, value: BigNumber.from(0) };

    if (getTrove.value.debt.lte(0) || getTrove.value.coll.lte(0))
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };
    const generateDebtBN = BigNumber.from(parseUnits(generateDebtAmount, 18));
    const paybackDebtBN = BigNumber.from(parseUnits(paybackDebtAmount, 18));
    if (paybackDebtBN.gt(getTrove.value.debt))
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };

    const bnDebt = getTrove.value.debt.add(generateDebtBN).sub(paybackDebtBN);
    const ratio = getTrove.value.coll.mul(collateralGMUPrice.value).mul(100).div(bnDebt);
    return {
      isLoading: false,
      value: ratio,
    };
  }, [getTrove, generateDebtAmount, paybackDebtAmount, collateralGMUPrice]);

  const isModifiedCollateralRatioBelowMin = useMemo(() => {
    if (isRecoveryMode.isLoading || modifiedCollatrealRatio.isLoading) return false;
    const crData = crParameters(trove, activeChainId);
    return !isRecoveryMode.value
      ? modifiedCollatrealRatio.value.lte(DECIMALS_18.mul(crData.mcr).div(1000))
      : modifiedCollatrealRatio.value.lte(DECIMALS_18.mul(crData.ccr).div(1000));
  }, [
    isRecoveryMode.isLoading,
    isRecoveryMode.value,
    modifiedCollatrealRatio.isLoading,
    modifiedCollatrealRatio.value,
    trove,
    activeChainId,
  ]);

  const isCollateralRatioBelowMin = useMemo(() => {
    if (isRecoveryMode.isLoading || collateralRatio.isLoading) return false;
    const crData = crParameters(trove, activeChainId);
    return !isRecoveryMode.value
      ? collateralRatio.value.lte(DECIMALS_18.mul(crData.mcr).div(1000))
      : collateralRatio.value.lte(DECIMALS_18.mul(crData.ccr).div(1000));
  }, [
    activeChainId,
    collateralRatio.isLoading,
    collateralRatio.value,
    isRecoveryMode.isLoading,
    isRecoveryMode.value,
    trove,
  ]);

  const borrowingFee = useMemo(() => {
    if (borrowRate.isLoading || isRecoveryMode.isLoading)
      return { isLoading: true, value: BigNumber.from(0) };

    if (isRecoveryMode.value)
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };

    return {
      isLoading: false,
      value: BigNumber.from(parseUnits(generateDebtAmount, 18))
        .mul(borrowRate.value)
        .div(DECIMALS_18),
    };
  }, [borrowRate, isRecoveryMode, generateDebtAmount]);

  const isModifiedDebtBelowMinDebt = useMemo(() => {
    if (getTrove.isLoading) return false;
    const bn = BigNumber.from(parseUnits(paybackDebtAmount, 18));
    if (getTrove.value.debt.lt(bn)) return true;
    return getTrove.value.debt
      .sub(BigNumber.from(parseUnits(paybackDebtAmount, 18)))
      .lt(ARTH_GAS_COMPENSATION.add(MIN_NET_DEBT));
  }, [paybackDebtAmount, getTrove]);

  const maxPaybackAmount = useMemo(() => {
    if (getTrove.isLoading)
      return {
        value: BNZERO,
        isLoading: true,
      };
    return {
      isLoading: false,
      value: getTrove.value.debt.sub(ARTH_GAS_COMPENSATION.add(MIN_NET_DEBT)),
    };
  }, [getTrove.isLoading, getTrove.value.debt]);

  const maxARTHGenerate = useMemo(() => {
    if (getTrove.isLoading || isRecoveryMode.isLoading)
      return {
        value: BNZERO,
        isLoading: true,
      };
    const crData = crParameters(trove, activeChainId);

    const cr = isRecoveryMode.value
      ? DECIMALS_18.mul(crData.ccr).div(1000)
      : DECIMALS_18.mul(crData.mcr).div(1000);

    const value = getTrove.value.coll.mul(collateralGMUPrice.value).mul(100).div(cr);

    return {
      isLoading: false,
      value: value.sub(getTrove.value.debt.add(DECIMALS_18)).lt(BNZERO)
        ? BNZERO
        : value.sub(getTrove.value.debt.add(DECIMALS_18)),
    };
  }, [
    activeChainId,
    collateralGMUPrice.value,
    getTrove.isLoading,
    getTrove.value.coll,
    getTrove.value.debt,
    isRecoveryMode.isLoading,
    isRecoveryMode.value,
    trove,
  ]);

  const onGenerateValueChange = async (val: string) => {
    if (val === '' || collateralGMUPrice.value.lte(0) || collateralGMUPrice.isLoading) {
      setGenerateDebtAmount('0');
      return;
    }

    const check: boolean = ValidateNumber(val);
    if (!check) return;
    setPaybackDebtAmount('0');
    setGenerateDebtAmount(check ? val : String(Number(val)));
  };

  const onPaybackValueChange = async (val: string) => {
    if (val === '' || collateralGMUPrice.value.lte(0) || collateralGMUPrice.isLoading) {
      setPaybackDebtAmount('0');
      return;
    }

    const check: boolean = ValidateNumber(val);
    if (!check) return;
    setGenerateDebtAmount('0');
    setPaybackDebtAmount(check ? val : String(Number(val)));
  };

  const resetFields = () => {
    setGenerateDebtAmount('0');
    setPaybackDebtAmount('0');
  };

  return (
    <div>
      <ConfirmGenerateARTH
        currentDebt={getTrove.value.debt}
        addedDebt={BigNumber.from(parseUnits(generateDebtAmount, 18))}
        borrowingFee={borrowingFee.value}
        borrowingFeeRate={borrowRate.value}
        cr={collateralRatio.value}
        newCR={modifiedCollatrealRatio.value}
        troveCollateral={getTrove.value.coll}
        trove={trove}
        openModal={openGenerateConfirmationModal}
        onClose={() => setOpenGenerateConfirmationModal(false)}
        onSuccess={() => {
          setOpenSuccessModal(true);
          setOpenGenerateConfirmationModal(false);
        }}
      />
      <ConfirmPaybackARTH
        trove={trove}
        currentDebt={getTrove.value.debt}
        removedDebt={BigNumber.from(parseUnits(paybackDebtAmount, 18))}
        cr={collateralRatio.value}
        newCR={modifiedCollatrealRatio.value}
        troveCollateral={getTrove.value.coll}
        symbol={troveDetail.depositToken}
        openModal={openPaybackConfirmationModal}
        onClose={() => setOpenPaybackConfirmationModal(false)}
        onSuccess={() => {
          setOpenSuccessModal(true);
          setOpenPaybackConfirmationModal(false);
        }}
      />
      <SuccessModal
        modalOpen={openSuccessModal}
        setModalOpen={() => {
          resetFields();
          setOpenSuccessModal(false);
        }}
        title={'Your transaction is being mined on blockchain'}
        buttonText={'Close'}
      />
      {isModifiedDebtBelowMinDebt && (
        <div className="m-b-24">
          <InfoTip
            type={'Warning'}
            msg={`You can not reduce your total debt below ${Number(
              getDisplayBalance(ARTH_GAS_COMPENSATION.add(MIN_NET_DEBT), 18, 3),
            ).toLocaleString()} ARTH.`}
          />
        </div>
      )}
      {isCollateralRatioBelowMin && (
        <div className="m-b-24">
          <InfoTip
            type={'Warning'}
            msg={
              !isRecoveryMode.value
                ? `Collateral ratio is below ${
                    getDisplaycrParameters(trove, activeChainId).mcr
                  }% and can be liquidated. Add ${troveDetail.depositToken.toUpperCase()} or decrease debt by repaying some of the borrowed ARTH.`
                : `System is currently in Recovery Mode. Collateral ratio is below ${
                    getDisplaycrParameters(trove, activeChainId).ccr
                  }% and can be liquidated. Add ${troveDetail.depositToken.toUpperCase()} or decrease debt by repaying some of the borrowed ARTH.`
            }
          />
        </div>
      )}
      {isModifiedCollateralRatioBelowMin &&
      (Number(paybackDebtAmount) || Number(generateDebtAmount)) ? (
        <div className="m-b-24">
          <InfoTip
            type={'Warning'}
            msg={
              !isRecoveryMode.value
                ? `The modified loan has Collateral ratio is below ${
                    getDisplaycrParameters(trove, activeChainId).mcr
                  }% and can be liquidated. Add ${troveDetail.depositToken.toUpperCase()} or decrease debt by repaying some of the borrowed ARTH.`
                : `System is currently in Recovery Mode. The modified loan has Collateral ratio is below ${
                    getDisplaycrParameters(trove, activeChainId).ccr
                  }% and can be liquidated. Add ${troveDetail.depositToken.toUpperCase()} or decrease debt by repaying some of the borrowed ARTH.`
            }
          />
        </div>
      ) : null}
      <div className="p-b-32 bottom-divider">
        <CustomInputContainer
          ILabelValue={'Generate'}
          IBalanceValue={`${getDisplayBalance(maxARTHGenerate.value, 18, 18)}`}
          showBalance={false}
          isBalanceLoading={maxARTHGenerate.isLoading}
          ILabelInfoValue={''}
          disabled={maxARTHGenerate.isLoading || arthBalance.isLoading}
          DefaultValue={generateDebtAmount.toString()}
          LogoSymbol={currentSymbol}
          hasDropDown={false}
          SymbolText={currentSymbol}
          inputMode={'numeric'}
          setText={(val: string) => {
            onGenerateValueChange(val);
          }}
          tagText={'MAX'}
          tokenDecimals={18}
        />
        <div className="single-line-center-between m-t-8">
          <div className="single-line-center-start">
            <TextWrapper text={`Max Generate: `} Fcolor={'#FFFFFF88'} />
            <div className="tags m-l-4">
              {maxARTHGenerate.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : (
                <TextWrapper
                  text={
                    Number(getDisplayBalance(maxARTHGenerate.value, 18, 6)).toLocaleString() +
                    ' ARTH'
                  }
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              )}
            </div>
          </div>
          <div className="single-line-center-end">
            <TextWrapper text={`ARTH Balance`} Fcolor={'#FFFFFF88'} />
            <div className="tags m-l-4">
              {arthBalance.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : (
                <TextWrapper
                  text={
                    Number(getDisplayBalance(arthBalance.value, 18, 3)).toLocaleString() +
                    ' ARTH'
                  }
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              )}
            </div>
          </div>
        </div>
        <div className="m-t-32">
          <Button
            disabled={
              !Number(generateDebtAmount) ||
              isCollateralRatioBelowMin ||
              isModifiedCollateralRatioBelowMin
            }
            onClick={() => setOpenGenerateConfirmationModal(true)}
            tracking_id="modify_loan_generate_ARTH"
            tracking_params={{
              action: 'initial',
              collateral: trove,
              amount: Number(generateDebtAmount),
            }}
          >
            Generate ARTH
          </Button>
        </div>
      </div>
      <div className="p-t-20">
        <div className="single-line-center-between m-b-8">
          <div />
        </div>
        <CustomInputContainer
          ILabelValue={'Payback'}
          showBalance={false}
          IBalanceValue={
            maxPaybackAmount.value > arthBalance.value
              ? `${getDisplayBalance(maxPaybackAmount.value, 18)}`
              : `${getDisplayBalance(arthBalance.value, 18)}`
          }
          isBalanceLoading={maxPaybackAmount.isLoading}
          DefaultValue={paybackDebtAmount.toString()}
          ILabelInfoValue={''}
          LogoSymbol={currentSymbol}
          disabled={arthBalance.isLoading || getTrove.isLoading}
          hasDropDown={false}
          SymbolText={currentSymbol}
          setText={(val: string) => {
            onPaybackValueChange(val);
          }}
          tagText={'MAX'}
          errorCallback={(flag: boolean) => setIsPaybackFieldError(flag)}
          tokenDecimals={18}
        />
        <div className="single-line-start-between m-t-8">
          <div>
            <div className="single-line-center-start m-b-4">
              <TextWrapper text={`Max Payback Amount: `} Fcolor={'#FFFFFF88'} />
              <div className="tags m-l-4">
                {maxPaybackAmount.isLoading ? (
                  <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
                ) : (
                  <TextWrapper
                    text={
                      Number(
                        getDisplayBalance(maxPaybackAmount.value, 18, 3),
                      ).toLocaleString() + ' ARTH'
                    }
                    fontSize={12}
                    Fcolor={'#FFFFFF88'}
                  />
                )}
              </div>
            </div>
            <div className="single-line-center-start">
              <TextWrapper text={`Current Debt`} Fcolor={'#FFFFFF88'} />
              <div className="tags m-l-4">
                {getTrove.isLoading ? (
                  <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
                ) : (
                  <TextWrapper
                    text={
                      Number(getDisplayBalance(getTrove.value.debt, 18, 3)).toLocaleString() +
                      ' ARTH'
                    }
                    fontSize={12}
                    Fcolor={'#FFFFFF88'}
                  />
                )}
              </div>
            </div>
          </div>
          <div className="single-line-center-start">
            <TextWrapper text={`ARTH Balance`} Fcolor={'#FFFFFF88'} />
            <div className="tags m-l-4">
              {arthBalance.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : (
                <TextWrapper
                  text={
                    Number(getDisplayBalance(arthBalance.value, 18, 3)).toLocaleString() +
                    ' ARTH'
                  }
                  fontSize={12}
                  Fcolor={'#FFFFFF88'}
                />
              )}
            </div>
          </div>
        </div>
        <div className="m-t-32">
          <InfoTip
            type={'Info'}
            msg={`You can not reduce your total debt below ${getDisplayBalance(
              ARTH_GAS_COMPENSATION.add(MIN_NET_DEBT),
              18,
            )} ARTH`}
            className={'m-b-4'}
          />
          {!isARTHApproved && (activeChainId === 137 || activeChainId === 80001) ? (
            <Button
              disabled={
                isPaybackFieldError ||
                !Number(paybackDebtAmount) ||
                isARTHApproved ||
                isARTHApproving ||
                isModifiedDebtBelowMinDebt
              }
              loading={isARTHApproving}
              onClick={approveCollateral}
              tracking_id="approve_modify_loan_payback_ARTH"
              tracking_params={{ collateral: trove }}
            >
              Approve
            </Button>
          ) : (
            <Button
              disabled={
                isPaybackFieldError || !Number(paybackDebtAmount) || isModifiedDebtBelowMinDebt
              }
              onClick={() => setOpenPaybackConfirmationModal(true)}
              tracking_id="modify_loan_payback_ARTH"
              tracking_params={{
                action: 'initial',
                collateral: trove,
                amount: Number(paybackDebtAmount),
              }}
            >
              Payback ARTH
            </Button>
          )}
        </div>
      </div>
      <div className="m-t-20">
        <div className="single-line-center-between">
          <div className="single-line-center-start">
            <TextWrapper text={'Current Collateral Ratio'} fontSize={12} />
            <ToolTip
              toolTipText={`The ratio between the ARTH value of the collateral and the debt (in ARTH) you are depositing. While the Minimum Collateral Ratio is ${
                getDisplaycrParameters(trove, activeChainId).mcr
              }% during normal operation, it is recommended to keep the Collateral Ratio always above ${
                getDisplaycrParameters(trove, activeChainId).ccr
              }% to avoid liquidation under Recovery Mode. A Collateral Ratio above 200% or 250% is recommended for additional safety.`}
            />
          </div>
          <div className="single-line-center-start">
            {collateralRatio.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={Number(getDisplayBalance(collateralRatio.value, 18, 3)).toLocaleString()}
                fontSize={12}
              />
            )}
            <div className="tags m-l-4">
              <TextWrapper text={'%'} fontSize={12} />
            </div>
          </div>
        </div>
        <div className="single-line-center-between">
          <div className="single-line-center-start">
            <TextWrapper text={'New Collateral Ratio'} fontSize={12} />
            <ToolTip
              toolTipText={`The ratio between the ARTH value of the collateral and the debt (in ARTH) you are depositing. While the Minimum Collateral Ratio is ${
                getDisplaycrParameters(trove, activeChainId).mcr
              }% during normal operation, it is recommended to keep the Collateral Ratio always above ${
                getDisplaycrParameters(trove, activeChainId).ccr
              }% to avoid liquidation under Recovery Mode. A Collateral Ratio above 200% or 250% is recommended for additional safety.`}
            />
          </div>
          <div className="single-line-center-start">
            {modifiedCollatrealRatio.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={
                  modifiedCollatrealRatio.value.eq(collateralRatio.value)
                    ? '0'
                    : Number(
                        getDisplayBalance(modifiedCollatrealRatio.value, 18, 3),
                      ).toLocaleString()
                }
                fontSize={12}
                Fcolor={isModifiedCollateralRatioBelowMin ? theme.color.red[300] : 'white'}
              />
            )}
            <div className="tags m-l-4">
              <TextWrapper text={'%'} fontSize={12} />
            </div>
          </div>
        </div>
        <div className="single-line-center-between">
          <div className="single-line-center-start">
            <TextWrapper text={'One-time fee'} fontSize={12} />
            <ToolTip
              toolTipText={
                'This amount is deducted from the borrowed amount as a one-time fee. There are no recurring fees for borrowing, which is thus interest-free.'
              }
            />
          </div>
          <div className="single-line-center-start">
            {borrowingFee.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={Number(getDisplayBalance(borrowingFee.value, 18, 3)).toLocaleString()}
                fontSize={12}
              />
            )}
            <div className="tags m-l-4">
              <TextWrapper text={'ARTH'} fontSize={12} />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DebtModifyLoan;
