import React, { useMemo } from "react";
import Loader from 'react-spinners/BeatLoader';

import { makeStyles } from '@material-ui/core/styles';
import { DataGrid, GridColDef } from '@material-ui/data-grid';

import { IFarmingPools } from '../../../config/farmingPools';
import '../../../customCss/Custom-Mahadao-Data-Table.css';
import useGetV3StakingAPR, { IAPR } from "../../../hooks/state/farming/useGetV3StakingAPR";
import useCore from '../../../hooks/useCore';
import theme from '../../../theme';
import Button from '../../../theme/components/Button';
import IconLoader from '../../../theme/components/IconLoader';
import TextButton from '../../../theme/components/TextButton';
import TextWrapper from '../../../theme/components/TextWrapper';

export function SortedDescendingIcon() {
  return <IconLoader iconName={'ArrowTailUp'} iconType="arrow" width={16} />;
}

export function SortedAscendingIcon() {
  return <IconLoader iconName={'ArrowTailDown'} iconType="arrow" width={16} />;
}

const useStyles = makeStyles({
  root: {
    '& .super-app-theme--header': {
      cursor: 'pointer',
    },
    '& .super-app.negative': {
      color: '#FA4C69',
      fontWeight: '600',
    },
    '& .super-app.positive': {
      color: '#20C974',
      fontWeight: '600',
    },
    '& .super-app.loss': {
      color: '#FA4C69',
      fontWeight: '600',
    },
    '& .super-app.profit': {
      color: '#20C974',
      fontWeight: '600',
    },
  },
  gridStyle: {
    color: 'white',
  },
});

interface IRows extends IFarmingPools{
  minApr: string;
  maxApr: string;
}

interface IProps {
  data: IRows[];
  apr: IAPR;
}

const DataTable = (props: IProps) => {
  const {data, apr} = props;
  const classes = useStyles();

  const columns: GridColDef[] = [
    {
      field: 'chain',
      headerName: 'Chain',
      flex: 0.1,
      sortable: true,
      renderCell: (params: any) => {
        return (
          <div className={'single-line-center-start'}>
            <IconLoader
              iconName={params.value}
              iconType={'chains'}
              width={24}
              className={'m-r-4'}
            />
          </div>
        );
      },
    },
    {
      field: 'poolName',
      headerName: 'Pool Name',
      flex: 0.25,
      sortable: true,
      renderCell: (params: any) => {
        return (
          <div className={'start-center'}>
            <span>{params.value}</span>
            {params.row.liquidityLink !== '' && <div className={'single-line-center-start'}>
              <TextButton
                onClick={() => window.open(`${params.row.liquidityLink}`)}
                text={`Add Liquidity`}
                fontSize={12}
              />
              <IconLoader
                iconName={'ArrowLinkColored'}
                iconType={'arrow'}
                width={16}
                height={16}
              />
            </div>}
          </div>
        );
      },
    },
    {
      field: 'platform',
      headerName: 'Platform',
      flex: 0.2,
      sortable: true,
      renderCell: (params: any) => {
        return (
          <div className={'single-line-center-start'}>
            {params.value}
          </div>
        );
      },
    },
    {
      field: 'status',
      headerName: 'Is Live',
      flex: 0.2,
      sortable: true,
      renderCell: (params: any) => {
        var text = ''
        var color = theme.color.transparent[100];
        if (params.value === 'live') {
          text = 'Live'
          color = theme.color.green[300]
        } else if (params.value === 'closed') {
          text = 'Closed'
          color = theme.color.red[300]
        } else {
          text = 'Upcoming'
          color = theme.color.yellow[300]
        }

        return (
          <div className={'start-center'}>
            <TextWrapper
              text={text}
              Fcolor={color}
            />
          </div>
        );
      },
    },
    {
      field: 'rewards',
      headerName: 'Reward Token',
      flex: 0.2,
      sortable: true,
      renderCell: (params: any) => {
        return (
          <div className={'single-line-center-start'}>
            <TextWrapper text={params.value.join()} />
          </div>
        );
      },
    },
    {
      field: 'minApr',
      headerName: 'APR',
      flex: 0.25,
      sortable: true,
      renderCell: (params: any) => {
        const minApr = params.value;
        const maxApr = params.row.maxApr;
        if (apr.isLoading) {
          return (
            <div className={'start-center'}>
              <Loader color={'#ffffff'} loading={true} size={2} margin={2} />
            </div>
          )
        }
        if (Number(minApr) && Number(maxApr)) {
          return (
            <div className={'start-center'}>
              {
                Number(minApr).toLocaleString('en-Us', { maximumFractionDigits: 2 }) +
                '%' +
                ' - ' +
                (Number(maxApr)).toLocaleString('en-Us', { maximumFractionDigits: 2 }) +
                '%'
              }
            </div>
          );
        } else if (Number(minApr)) {
          return (
            <div className={'start-center'}>
              {
                Number(minApr).toLocaleString('en-Us', { maximumFractionDigits: 2 }) + '%'
              }
            </div>
          );
        } else if (Number(maxApr)) {
          return (
            <div className={'start-center'}>
              {
                Number(maxApr).toLocaleString('en-Us', { maximumFractionDigits: 2 }) + '%'
              }
            </div>
          );
        } else {
          return (
            <div className={'start-center'}>
              -
            </div>
          )
        }

      },
    },
    {
      field: 'redirectLink',
      headerName: 'Action',
      flex: 0.2,
      sortable: false,
      renderCell: (params: any) => {
        return (
          <div style={{ marginTop: 'auto', marginBottom: 'auto', width: '150px' }}>
            <Button
              text={'Visit pool'}
              href={`${params.value}`}
              tracking_id={'visit_v3Staking_pool'}
              tracking_params={{
                collateral: `${params.row.poolName}`,
              }}
              disabled={params.row.status !== 'live'}
            />
          </div>
        );
      },
    },
  ];

  const noResultsComponent = () => {
    return (
      <div className="noResultContainer">
        <div className="internal">
          <TextWrapper
            text={'No v3 gauges found'}
            fontSize={24}
            fontWeight={'bold'}
            className="m-b-8"
            align="center"
          />
        </div>
      </div>
    );
  };

  return (
    <div className={classes.root} style={{ position: 'relative' }}>
      <DataGrid
        className={classes.gridStyle}
        getRowId={(rows) => rows.apiKey}
        rows={data}
        columns={columns}
        pageSize={10}
        hideFooterPagination={false}
        rowHeight={80}
        autoHeight={true}
        disableColumnMenu={true}
        loading={false}
        components={{
          NoRowsOverlay: noResultsComponent,
          ColumnSortedDescendingIcon: SortedDescendingIcon,
          ColumnSortedAscendingIcon: SortedAscendingIcon,
        }}
        disableColumnSelector={true}
        disableSelectionOnClick={true}
      />
    </div>
  );
};

export default DataTable;
