import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (contractPrefix: string) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();
  const chainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const contract = core.getStabilityPool(contractPrefix, chainId);
    const value = await contract.getTotalARTHDeposits();
    setValue({ isLoading: false, value: value });
  }, [core, contractPrefix, account]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch getDepositorETHGain() for ${contractPrefix}: ${err}`),
      );
    }
  }, [core.isUnlocked, account, blockNumber, fetchValue, contractPrefix]);

  return value;
};
