import React, { useMemo, useState } from 'react';
import Loader from 'react-spinners/BeatLoader';
import styled from 'styled-components';
import * as timeago from 'timeago.js';
import { useWallet } from 'use-wallet';

import useBasicStakingClaim from '../../../hooks/callbacks/farming/useBasicStakingClaim';
import useBasicStakingBalance from '../../../hooks/state/farming/useBasicStakingBalance';
import useBasicStakingReward from '../../../hooks/state/farming/useBasicStakingReward';
import useTokenBalance from '../../../hooks/state/useTokenBalance';
import useCore from '../../../hooks/useCore';
import { FarmingConfigurations } from '../../../protocol/config';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../../theme';
import ActionButton from '../../../theme/components/ActionButton';
import Button from '../../../theme/components/Button';
import DataField from '../../../theme/components/DataField';
import IconLoader from '../../../theme/components/IconLoader';
import TextButton from '../../../theme/components/TextButton';
import TextWrapper from '../../../theme/components/TextWrapper';
import ToolTip from '../../../theme/components/Tooltip';
import { getDisplayBalance } from '../../../utils/formatBalance';
import DepositModal from '../modals/DepositModal';
import WithdrawModal from '../modals/WithdrawModal';

import { IFarmingApiDataState } from './FarmingCards';
import Rewards from './Rewards';

interface IProps {
  pool: FarmingConfigurations;
  farmingApiData: IFarmingApiDataState;
}

export const MobileFarm = (props: IProps) => {
  const { farmingApiData } = props;
  const [isDepositModalOpen, setDepositModalOpen] = useState<boolean>(false);
  const [isWithdrawModalOpen, setWithdrawModalOpen] = useState<boolean>(false);

  const core = useCore();
  const chainId = useGetActiveChainId();
  const depositToken = core.tokens(props.pool.depositToken, chainId);
  const earnedBalance = useBasicStakingReward(props.pool);
  const stakingBalance = useBasicStakingBalance(props.pool);
  const tokenBalance = useTokenBalance(depositToken);
  const claimAction = useBasicStakingClaim(props.pool);

  const apr = useMemo(() => {
    // if (chainId === 56) return '-'
    if (farmingApiData.isLoading) return '-';
    else {
      let chId = chainId;
      if (chId === 80001) chId = 137;
      if (chId === 97) chId = 56;
      const chainData = farmingApiData.data.chainSpecificData[chId];
      const data = chainData?.apr[props.pool.apiKey];
      if (chainData?.maxApr && chainData?.minApr) {
        const minAPR = chainData?.minApr[props.pool.apiKey];
        const maxAPR = chainData?.maxApr[props.pool.apiKey];
        if (minAPR && maxAPR && Number(minAPR) && Number(maxAPR)) {
          return `${Number(minAPR).toLocaleString()}% - ${Number(maxAPR).toLocaleString()}%`;
        }
      }

      if (Number(data)) {
        return `${Number(data).toLocaleString()}%`;
      } else {
        return '-';
      }
    }
  }, [chainId, farmingApiData, props.pool.apiKey]);

  const tvl = useMemo(() => {
    // if (chainId === 56) return '-'
    if (farmingApiData.isLoading) return '-';
    else {
      let chId = chainId;
      if (chId === 80001) chId = 137;
      if (chId === 97) chId = 56;
      // @ts-ignore
      const data = farmingApiData.data.chainSpecificData[chId]?.tvl[props.pool.apiKey];
      if (Number(data)) {
        // @ts-ignore
        return `$${Math.floor(data).toLocaleString()}`;
      } else {
        return '-';
      }
    }
  }, [
    chainId,
    farmingApiData.data.chainSpecificData,
    farmingApiData.isLoading,
    props.pool.apiKey,
  ]);

  const hasClaimableAmount = useMemo(() => {
    if (earnedBalance.isLoading) return false;
    else {
      let returnValue = false;
      earnedBalance.value.forEach((data) => {
        if (data.gt(0)) {
          returnValue = true;
        }
      });
      return returnValue;
    }
  }, [earnedBalance.isLoading, earnedBalance.value]);

  return (
    <CustomGrid>
      <DepositModal
        poolInfo={props.pool}
        open={isDepositModalOpen}
        onCancel={() => setDepositModalOpen(false)}
        onDeposit={() => setDepositModalOpen(false)}
        tokenBalance={tokenBalance.value}
      />
      <WithdrawModal
        poolInfo={props.pool}
        open={isWithdrawModalOpen}
        stakedBalance={stakingBalance.value}
        onCancel={() => setWithdrawModalOpen(false)}
        onWithdraw={() => setWithdrawModalOpen(false)}
      />
      <PlatformDiv>
        <IconLoader
          iconType={'exchangePlatform'}
          iconName={props.pool.platform}
          width={24}
          height={24}
        />
      </PlatformDiv>
      <UpperDiv>
        <SymbolDiv>
          <div>
            {props.pool.depositTokenSymbols.map((token, index) => (
              <IconLoader
                iconName={token}
                iconType={'tokenSymbol'}
                width={44}
                className={index === 0 ? '' : 'm-l--8'}
                key={token}
              />
            ))}
          </div>
          <div className={'m-l-16'}>
            <TextWrapper text={props.pool.name} fontWeight={600} />
            <TextButton
              onClick={() => window.open(props.pool.addLiquidityLink)}
              text={`Get ${props.pool.depositToken}`}
              tracking_id={'farming-add-liquidity'}
              tracking_params={{ collateral: props.pool.name }}
              className={'m-t-4'}
              fontSize={12}
            />
            {/*{
              props.pool.depositTokenSymbols.includes('ARTH.usd') && (
                <TextButton
                  onClick={() => window.open('https://arth.mahadao.com/#/tokenization?open=ARTH.usd')}
                  text={`Get ARTH.usd`}
                  tracking_id={'farming-get-arthusd'}
                  tracking_params={{ collateral: props.pool.name }}
                  className={'m-t-4'}
                  fontSize={12}
                />
              )
            }*/}
          </div>
        </SymbolDiv>
        <DataField
          label={'TVL'}
          isValueLoading={farmingApiData.isLoading}
          labelToolTip={
            'Calculated values represents the total amount of volume locked in terms of USD'
          }
          value={`${tvl}`}
          labelFontColor={theme.color.transparent[100]}
          labelFontWeight={600}
          className={'m-b-16'}
        />
        <DataField
          label={'APR'}
          isValueLoading={farmingApiData.isLoading}
          labelToolTip={
            'Calculated based on current rates. Rates are estimates provided for your convenience only, and may vary as per dexes multiplier.'
          }
          value={`${apr}`}
          labelFontColor={theme.color.transparent[100]}
          labelFontWeight={600}
          className={'m-b-16'}
        />
        <DataField
          label={'Reward'}
          value={`${props.pool.rewardToken.join('+')}`}
          labelFontColor={theme.color.transparent[100]}
          labelFontWeight={600}
          className={'m-b-16'}
        />
        <div className={'m-b-16'}>
          <DataField
            label={'Your Locked Stake'}
            isValueLoading={stakingBalance.isLoading}
            value={
              Number(getDisplayBalance(stakingBalance.value, 18, 3)).toLocaleString() +
              ' ' +
              props.pool.depositToken
            }
            labelFontColor={theme.color.transparent[100]}
            labelFontWeight={600}
            // valueFontColor={stakingBalance.value.gt(0)? 'white': theme.color.transparent[200]}
            className={'m-b-2'}
          />
          <div className={'single-line-center-end'}>
            <TextButton
              onClick={() =>
                stakingBalance.value.gt(0) ? setWithdrawModalOpen(true) : () => {}
              }
              text={'Withdraw'}
              tracking_id={'farming-withdraw'}
              tracking_params={{
                collateral: props.pool.name,
                action: 'initial',
                amount: Number(
                  getDisplayBalance(stakingBalance.value, depositToken.decimal, 3),
                ),
              }}
              fontWeight={600}
              disabled={stakingBalance.value.lte(0)}
            />
          </div>
        </div>
        <div className={'m-b-16'}>
          <div className={'single-line-start-between'}>
            <TextWrapper
              text={'Unclaimed Rewards'}
              fontWeight={600}
              Fcolor={theme.color.transparent[100]}
            />
            <div>
              {props.pool.rewardToken.map((data, index) => {
                if (earnedBalance.isLoading) {
                  return (
                    <TextWrapper
                      text={
                        <div>
                          <Loader color={'#ffffff'} loading={true} size={4} margin={2} />
                          &nbsp;
                          {data.toUpperCase()}
                          {index !== props.pool.rewardToken.length - 1 ? ' + ' : ''}
                        </div>
                      }
                      align={'right'}
                    />
                  );
                } else {
                  return (
                    <TextWrapper
                      text={`${Number(
                        getDisplayBalance(earnedBalance.value[index], 18, 6),
                      ).toLocaleString('en-IN', {
                        minimumFractionDigits: 2,
                        maximumFractionDigits: 6,
                      })} 
                          ${data.toUpperCase()} ${
                        index !== props.pool.rewardToken.length - 1 ? '+' : ''
                      }`}
                      align={'right'}
                    />
                  );
                }
              })}
            </div>
          </div>

          <div className={'single-line-center-end'}>
            <TextButton
              onClick={() => (hasClaimableAmount ? claimAction() : () => {})}
              text={'Claim'}
              tracking_id={'farming-claim'}
              tracking_params={{
                collateral: props.pool.name,
                action: 'confirm',
                amount: Number(getDisplayBalance(earnedBalance.value[0], 18, 3)),
              }}
              fontWeight={600}
              disabled={!hasClaimableAmount}
            />
          </div>
        </div>
        <ActionButton
          text={props.pool.endDate < new Date() ? 'Closed' : 'Deposit'}
          disabled={tokenBalance.value.lte(0) || props.pool.endDate < new Date()}
          onClick={() => setDepositModalOpen(true)}
          tracking_id={`farming_deposit`}
          tracking_params={{ collateral: props.pool.name, action: 'initial' }}
        />
      </UpperDiv>
      {/*{(stakingBalance.value.gt(0) || earnedBalance.value.gt(0)) && (
        <LowerDiv>
          <div className="text-center">
            <div className="m-b-8 single-line-center-center">
              <TextWrapper text={' Your Locked stake:'} fontWeight={300} Fcolor={'#FFFFFF88'} />
              &nbsp;
              {stakingBalance.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : (
                <TextWrapper
                  text={
                    Number(getDisplayBalance(stakingBalance.value, 18, 3)).toLocaleString() +
                    ' ' +
                    props.pool.name
                  }
                  fontWeight={600}
                />
              )}
            </div>
            {stakingBalance.value.gt(0) && (
              <div className="single-line-center-center">
                <TextButton
                  onClick={() => setWithdrawModalOpen(true)}
                  text={'Withdraw'}
                  tracking_id={'farming-withdraw'}
                  tracking_params={{
                    collateral: props.pool.name,
                    action: 'initial',
                    amount: Number(
                      getDisplayBalance(stakingBalance.value, depositToken.decimal, 3),
                    ),
                  }}
                  className={'m-b-16'}
                  fontWeight={600}
                />
              </div>
            )}
            <div className="m-b-8 single-line-center-center">
              <TextWrapper text={'Unclaimed Rewards:'} fontWeight={300} Fcolor={'#FFFFFF88'} />
              &nbsp;
              {earnedBalance.isLoading ? (
                <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
              ) : farmingApiData.isLoading || true ? (
                <TextWrapper
                  text={
                    Number(getDisplayBalance(earnedBalance.value, 18, 3)).toLocaleString() +
                    ' ' +
                    props.pool.rewardToken.toUpperCase()
                  }
                  fontWeight={600}
                />
              ) : (
                <Rewards
                  earnedBalance={Number(getDisplayBalance(earnedBalance.value, 18, 3))}
                  rewardRate={farmingApiData.data.mahaRewardPerMinute / 600}
                  rewardToken={props.pool.rewardToken}
                />
              )}
            </div>
            {earnedBalance.value.gt(0) && (
              <div className="single-line-center-center">
                <TextButton
                  onClick={() => claimAction()}
                  text={'Claim'}
                  tracking_id={'farming-claim'}
                  tracking_params={{
                    collateral: props.pool.name,
                    action: 'confirm',
                    amount: Number(getDisplayBalance(earnedBalance.value, 18, 3)),
                  }}
                  fontWeight={600}
                  className={'m-b-16'}
                />
              </div>
            )}
          </div>
        </LowerDiv>
      )}*/}
      {props.pool?.willBeRestarted ? (
        <PoolStartContainer>This Pool will be restarted soon</PoolStartContainer>
      ) : (
        <div>
          {props.pool.startDate &&
            (props.pool.startDate > new Date() ? (
              <PoolStartContainer>
                Pool starts {timeago.format(props.pool.startDate)}
              </PoolStartContainer>
            ) : (
              <PoolStartContainer>
                Pool started {timeago.format(props.pool.startDate)}
              </PoolStartContainer>
            ))}
          {props.pool.endDate &&
            (props.pool.endDate > new Date() ? (
              <PoolStartContainer>
                Pool closes {timeago.format(props.pool.endDate)}
              </PoolStartContainer>
            ) : (
              <PoolStartContainer>
                Pool was closed {timeago.format(props.pool.endDate)} and will migrate to a gauge
              </PoolStartContainer>
            ))}
        </div>
      )}
    </CustomGrid>
  );
};

const PoolStartContainer = styled.div`
  background: rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  padding: 5px 15px;
  position: relative;
  color: rgb(255, 127, 87);
  font-size: 12px;
  text-align: center;
`;

const CustomGrid = styled.div`
  background: linear-gradient(180deg, #48423e 0%, #373030 100%);
  border: 1px solid rgba(255, 255, 255, 0.16);
  box-sizing: border-box;
  border-radius: 12px;
  margin-bottom: 32px;
  position: relative;
`;

const UpperDiv = styled.div`
  padding: 32px;
  //border-bottom: 1px solid #ffffff29;
  @media (max-width: 600px) {
    padding: 24px;
  }
`;

const SymbolDiv = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
`;

const PlatformDiv = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: absolute;
  top: -16px;
`;

const LowerDiv = styled.div`
  padding: 16px 32px 16px 32px;
  @media (max-width: 600px) {
    padding: 16px 24px 16px 24px;
  }
`;

export default MobileFarm;
