import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useWallet } from 'use-wallet';

import { AppState } from '../../../state';
import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { troveDetails } from '../../../utils/tokenDetails';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value: boolean;
};

export default (trove: string) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: false });

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);

  // const collateralGMUPrices = useSelector((app: AppState) => app.analytics.gmuPrice);
  // const collateralGMUPrice = collateralGMUPrices[troveDetail.depositToken];

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: false });
      return;
    }

    // if (!collateralGMUPrice) {
    //   return;
    // }

    const priceFeedContract = core.getPriceFeed(troveDetail.contractPrefix, activeChainId);
    const price = await priceFeedContract.callStatic.fetchPrice();
    const contract = core.getTroveManager(troveDetail.contractPrefix, activeChainId);
    const value = await contract.checkRecoveryMode(price);
    setValue({ isLoading: false, value: value });
  }, [core, account, activeChainId, troveDetail]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch checkRecoveryMode() for ${trove}: ${err}`),
      );
    }
  }, [core.isUnlocked, account, blockNumber, fetchValue, trove, activeChainId]);

  return value;
};
