import { Dispatch } from '@reduxjs/toolkit';
import { BigNumber } from 'ethers';

import { IMulticallInput } from '../../protocol/Mulitcall';
import { Protocol } from '../../protocol/Protocol';

import * as Actions from './actions';

export const init = (core: Protocol, dispatch: Dispatch, chainId: number) => {
  const _registerVariable = (fn: string, to: string, reduxAction: any, convertResult: any) => {
    const input: IMulticallInput = {
      key: `ANALYTICS:${fn}:${to}`,
      target: to,
      call: [fn],
      convertResult,
    };

    core.multicall[chainId].on(input.key, (val) => {
      dispatch(reduxAction(val));
    });

    return input;
  };

  const registerTroveManagerCalls = () => {
    const calls: IMulticallInput[] = [];

    const collaterals = core.getAllTroves(chainId);

    collaterals.forEach((collateral) => {
      const tm = core.getTroveManager(collateral, chainId);

      calls.push(
        _registerVariable(
          'getEntireSystemColl()(uint256)',
          tm.address,
          Actions.getEntireSystemColl,
          (val: BigNumber) => ({ val, collateral }),
        ),
        _registerVariable(
          'getEntireSystemDebt()(uint256)',
          tm.address,
          Actions.getEntireSystemDebt,
          (val: BigNumber) => ({ val, collateral }),
        ),
        _registerVariable(
          'getBorrowingRateWithDecay()(uint256)',
          tm.address,
          Actions.getBorrowingRateWithDecay,
          (val: BigNumber) => ({ val, collateral }),
        ),
        _registerVariable(
          'getRedemptionRateWithDecay()(uint256)',
          tm.address,
          Actions.getRedemptionRateWithDecay,
          (val: BigNumber) => ({ val, collateral }),
        ),
        _registerVariable(
          'getBorrowingRate()(uint256)',
          tm.address,
          Actions.getBorrowingRate,
          (val: BigNumber) => ({ val, collateral }),
        ),
        _registerVariable(
          'getRedemptionRate()(uint256)',
          tm.address,
          Actions.getRedemptionRate,
          (val: BigNumber) => ({ val, collateral }),
        ),
      );
    });

    return calls;
  };

  const calls = [...registerTroveManagerCalls()];
  core.multicall[chainId].addCalls(calls);
};
