import React, { useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useWallet } from 'use-wallet';

import Grid from '@material-ui/core/Grid';

import config from '../../config';
import useCore from '../../hooks/useCore';
import { useGetActiveChainId } from '../../state/chains/hooks';
import theme from '../../theme';
import IconLoader from '../../theme/components/IconLoader';
import PageHeader from '../../theme/components/PageHeader';
import TextWrapper from '../../theme/components/TextWrapper';
import LoanInfo from '../LoanInfo/LoanInfo';

import AddLoanSection from './components/AddLoanSection';
import LoanDesc from './components/LoanDesc';
import UnclaimedCollateral from './components/UnclaimedCollateral';

const AddLoan = () => {
  const { id } = useParams<{ id: string }>();
  const [trove, setTrove] = useState<string>('');
  const chainId = useGetActiveChainId();
  const { account } = useWallet();

  const core = useCore();
  const isTroveValid: boolean = useMemo(() => {
    const troves = core.getAllTroves(chainId, account);
    if (troves.includes(id)) {
      setTrove(id);
      return true;
    } else {
      return false;
    }
  }, [core, chainId, account, id]);

  if (!isTroveValid) {
    window.location.href = `/#/loan/add/${config[chainId].defaultCollateral}`;
    return <div />;
  }

  if (isTroveValid && trove !== '') {
    return (
      <div className="custom-container">
        <div className="mo-custom-container">
          <div className={'m-b-20 single-line-center-start'}>
            <a href={'/#/loans/overview'}>
              <TextWrapper text={'Loans overview'} Fcolor={theme.color.transparent[200]} />
            </a>
            <IconLoader iconName={'ArrowRight'} iconType={'arrow'} className={'m-r-4 m-l-4'} />
            <TextWrapper text={'New Loan'} Fcolor={theme.color.transparent[100]} />
          </div>
        </div>
        <PageHeader
          title={`Open a ${trove} Loan`}
          subTitle={`You are now going to open a loan for ARTH`}
        />
        <div className={'m-b-24'}>
          <UnclaimedCollateral contractPrefix={id} />
        </div>
        <Grid container spacing={2}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <div className={'custom-mahadao-container'}>
              <div className={'custom-mahadao-container-content'}>
                <AddLoanSection trove={trove} />
              </div>
            </div>
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            {/*<LoanDesc />*/}
            <LoanInfo trove={trove} />
          </Grid>
        </Grid>
      </div>
    );
  }
};

export default AddLoan;
