import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import styled from 'styled-components';
import { useWallet } from 'use-wallet';

import Grid from '@material-ui/core/Grid';

import LoadingPage from '../../components/LoadingPage';
import useGetTrove from '../../hooks/state/troveManager/useGetTrove';
import { useGetActiveChainId } from '../../state/chains/hooks';
import IconLoader from '../../theme/components/IconLoader';
import InfoTip from '../../theme/components/InfoTip';
import PageHeader from '../../theme/components/PageHeader';
import TextWrapper from '../../theme/components/TextWrapper';
import { troveDetails } from '../../utils/tokenDetails';
import LoanInfo from '../LoanInfo/LoanInfo';

import CollateralModifyLoan from './CollateralModifyLoan';
import DebtModifyLoan from './DebtModifyLoan';
import LoanDesc from './LoanDesc';

const ModifyLoan = () => {
  const { trove } = useParams<any>();
  const chainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, chainId);
  const { account } = useWallet();
  const getTrove = useGetTrove(troveDetail.contractPrefix, account);
  const [type, setType] = useState<'Collateral' | 'Debt'>('Collateral');

  if (getTrove.isLoading) {
    return (
      <div style={{ position: 'relative', height: 'calc(100vh - 100px)' }}>
        <LoadingPage />
      </div>
    );
  }

  if (getTrove.value.stake.lte(0)) {
    window.location.href = '/#/loans/your-loans';
    return (
      <div className="m-t-40 m-b-40 material-primary">
        <TextWrapper
          text={`You dont have any loan open for ${trove}`}
          fontSize={24}
          fontWeight={'bold'}
          className="m-b-8"
          align="center"
        />
        <TextWrapper
          text={'Open Interest free loan with leveraged position from the leverage strategies'}
          fontSize={18}
          className="m-b-8"
          Fcolor={'#FFFFFF64'}
          align={'center'}
        />
      </div>
    );
  }

  return (
    <div className="custom-container">
      <PageHeader
        title={`MODIFY ${trove.toUpperCase()} LOAN`}
        subTitle={`Modify your existing ${trove.toUpperCase()} loan with ARTH.`}
        showLearnMore={true}
        learnMoreLink={'https://docs.arthcoin.com/protocol/introduction-arth-loans'}
      />
      <Grid container style={{ marginTop: '24px' }} spacing={2}>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <LeftTopCard className={'custom-mahadao-container'}>
            <LeftTopCardHeader className={'custom-mahadao-container-header'}>
              <div className="m-r-12 single-line-center-start pointer">
                <IconLoader
                  iconName={'ArrowLeft'}
                  iconType="arrow"
                  onClick={() => window.history.back()}
                />
              </div>
              <div style={{ display: 'flex', flex: '1' }}>
                <TabContainer onClick={() => setType('Collateral')}>
                  {type === 'Collateral' && <ActiveTab />}
                  <TabText>Collateral</TabText>
                </TabContainer>
                <TabContainer onClick={() => setType('Debt')}>
                  {type === 'Debt' && <ActiveTab />}
                  <TabText>Debt</TabText>
                </TabContainer>
              </div>
            </LeftTopCardHeader>
            <div className={'custom-mahadao-container-content'}>
              {type === 'Collateral' && <CollateralModifyLoan trove={trove} />}
              {type === 'Debt' && <DebtModifyLoan trove={trove} />}
            </div>
          </LeftTopCard>
        </Grid>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <LoanDesc />
          <LoanInfo trove={trove} />
        </Grid>
      </Grid>
    </div>
  );
};

export default ModifyLoan;

const LeftTopCard = styled.div``;

const LeftTopCardHeader = styled.div`
  display: flex;
  flex-direction: row;
`;

const TabContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px 12px;
  width: 100px;
  height: 80px;
  z-index: 1;
  cursor: pointer;
  position: relative;
`;

const TabText = styled.span`
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: rgba(255, 255, 255, 0.88);
`;

const ActiveTab = styled.div`
  position: absolute;
  width: 100%;
  height: 2px;
  z-index: 0;
  background: linear-gradient(81.26deg, #f47f57 14.65%, #fd5656 83.88%);
  border-radius: 2px 2px 0 0;
  bottom: 0;
`;
