import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import config from '../../config';
import ERC20 from '../../protocol/ERC20';
import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';

const useAllowance = (token: ERC20, spender: string, pendingApproval?: boolean) => {
  const [allowance, setAllowance] = useState<BigNumber>(BigNumber.from(0));
  const activeChainId = useGetActiveChainId();
  const { account } = useWallet();
  const core = useCore();

  const fetchAllowance = useCallback(async () => {
    if (!account || spender === undefined) return;
    if (token === undefined) return;
    const allowance = await token.allowance(account, spender);
    setAllowance(allowance);
  }, [account, spender, token]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchAllowance().catch((err) =>
        console.log(
          `Failed to fetch allowance for ${token} ${account} ${spender}: ${err.stack}`,
        ),
      );

      let refreshInterval = setInterval(fetchAllowance, config[activeChainId].refreshInterval);
      return () => clearInterval(refreshInterval);
    }
  }, [account, activeChainId, core.isUnlocked, fetchAllowance, spender, token]);

  useEffect(() => {
    if (account && spender && token) {
      fetchAllowance().catch((err) => console.log(`Failed to fetch allowance: ${err.stack}`));
    }
  }, [account, spender, token, pendingApproval, fetchAllowance]);

  return allowance;
};

export default useAllowance;
