import { createReducer } from '@reduxjs/toolkit';
import { BigNumber } from 'ethers';

import * as Actions from './actions';

export interface AnalyticsState {
  gmuPrice: {
    [collateral: string]: BigNumber;
  };
  entireSystemColl: {
    [collateral: string]: BigNumber;
  };
  entireSystemDebt: {
    [collateral: string]: BigNumber;
  };
  borrowingRateWithDecay: {
    [collateral: string]: BigNumber;
  };
  borrowingRate: {
    [collateral: string]: BigNumber;
  };
  redemptionRate: {
    [collateral: string]: BigNumber;
  };
  redemptionRateWithDecay: {
    [collateral: string]: BigNumber;
  };
}

export const initialState: AnalyticsState = {
  gmuPrice: {},
  entireSystemColl: {},
  entireSystemDebt: {},
  borrowingRateWithDecay: {},
  borrowingRate: {},
  redemptionRate: {},
  redemptionRateWithDecay: {},
};

export default createReducer(initialState, (builder) =>
  builder
    .addCase(
      Actions.updateGMUPrice,
      (t, { payload }: { payload: Actions.DefaultPriceType }) => {
        t.gmuPrice = t.gmuPrice || {};
        t.gmuPrice[payload.collateral] = payload.val;
      },
    )
    .addCase(
      Actions.getEntireSystemColl,
      (t, { payload }: { payload: Actions.DefaultPriceType }) => {
        t.entireSystemColl = t.entireSystemColl || {};
        t.entireSystemColl[payload.collateral] = payload.val;
      },
    )
    .addCase(
      Actions.getEntireSystemDebt,
      (t, { payload }: { payload: Actions.DefaultPriceType }) => {
        t.entireSystemDebt = t.entireSystemDebt || {};
        t.entireSystemDebt[payload.collateral] = payload.val;
      },
    )
    .addCase(
      Actions.getBorrowingRate,
      (t, { payload }: { payload: Actions.DefaultPriceType }) => {
        t.borrowingRate = t.borrowingRate || {};
        t.borrowingRate[payload.collateral] = payload.val;
      },
    )
    .addCase(
      Actions.getBorrowingRateWithDecay,
      (t, { payload }: { payload: Actions.DefaultPriceType }) => {
        t.borrowingRateWithDecay = t.borrowingRateWithDecay || {};
        t.borrowingRateWithDecay[payload.collateral] = payload.val;
      },
    )
    .addCase(
      Actions.getRedemptionRateWithDecay,
      (t, { payload }: { payload: Actions.DefaultPriceType }) => {
        t.redemptionRateWithDecay = t.redemptionRateWithDecay || {};
        t.redemptionRateWithDecay[payload.collateral] = payload.val;
      },
    )
    .addCase(
      Actions.getRedemptionRate,
      (t, { payload }: { payload: Actions.DefaultPriceType }) => {
        t.redemptionRate = t.redemptionRate || {};
        t.redemptionRate[payload.collateral] = payload.val;
      },
    ),
);
