import { BigNumber } from '@ethersproject/bignumber';
import { parseUnits } from 'ethers/lib/utils';
import React, { useMemo, useState } from 'react';
import Loader from 'react-spinners/BeatLoader';
import styled from 'styled-components';
import { useWallet } from 'use-wallet';

import Grid from '@material-ui/core/Grid';

import CustomInputContainer from '../../../components/CustomInputContainer';
import { ValidateNumber } from '../../../components/CustomInputContainer/RegexValidation';
import DepositModal from '../../../components/DepositModal';
import useOneTroveIsAlreadyOpen from '../../../hooks/state/sortedTroves/useGetTroveIsAlreadyOpen';
import useGetBorrowingFeeRateWithDecay from '../../../hooks/state/troveManager/useGetBorrowingFeeRateWithDecay';
import useGetIsRecoveryMode from '../../../hooks/state/troveManager/useGetIsRecoveryMode';
import useCollateralPriceFeed from '../../../hooks/state/useCollateralPriceFeed';
import useGetNativeTokenBalance from '../../../hooks/state/useGetNativeTokenBalance';
import useTokenBalance from '../../../hooks/state/useTokenBalance';
import useCore from '../../../hooks/useCore';
import useTokenDecimals from '../../../hooks/useTokenDecimals';
import { DECIMALS_18 } from '../../../protocol/constants';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../../theme';
import Button from '../../../theme/components/Button';
import IconLoader from '../../../theme/components/IconLoader';
import InfoTip from '../../../theme/components/InfoTip';
import TextWrapper from '../../../theme/components/TextWrapper';
import ToolTip from '../../../theme/components/Tooltip';
import { crParameters, getDisplaycrParameters } from '../../../utils/crParameters';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';
import ConfirmLoanModal from '../modal/ConfirmLoanModal';

type Props = {
  trove: string;
};

const calculateBorrowingFee = (
  debtAmount: BigNumber,
  borrowingFeeRate: BigNumber,
): BigNumber => {
  return debtAmount.mul(borrowingFeeRate).div(DECIMALS_18);
};

const AddLoanSection = ({ trove }: Props) => {
  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);
  const [debtAmount, setdebtAmount] = useState<string>('0');
  const [checkedLevarage, setCheckedLevarage] = useState(false);
  const [depositModal, setdepositModal] = useState<boolean>(false);
  const [collateralValue, setCollateralValue] = useState<string>('0');
  const [isInputFieldError, setIsInputFieldError] = useState<boolean>(false);
  const [openConfirmationModal, setOpenConfirmationModal] = useState<boolean>(false);

  const arthBalance = useTokenBalance(core.tokens('ARTH', activeChainId));
  const tokenDecimals = useTokenDecimals(troveDetail.depositToken);
  const isRecoveryMode = useGetIsRecoveryMode(trove);
  const collateralGMUPrice = useCollateralPriceFeed(
    troveDetail.depositToken,
    troveDetail.contractPrefix,
  );
  const collateralBalance = useGetNativeTokenBalance();
  const troveIsAlreadyPresent = useOneTroveIsAlreadyOpen(troveDetail.contractPrefix);
  const borrowingFeeRate = useGetBorrowingFeeRateWithDecay(troveDetail.depositToken);
  const { account } = useWallet();

  const borrowingFee = useMemo(() => {
    if (borrowingFeeRate.isLoading || isRecoveryMode.isLoading)
      return { isLoading: true, value: BigNumber.from(0) };

    if (isRecoveryMode.value)
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };

    return {
      isLoading: false,
      value: calculateBorrowingFee(
        BigNumber.from(parseUnits(debtAmount, 18)),
        borrowingFeeRate.value,
      ),
    };
  }, [borrowingFeeRate, isRecoveryMode, debtAmount]);

  const totalDebt = useMemo(() => {
    if (borrowingFee.isLoading)
      return {
        isLoading: true,
        value: BigNumber.from(0),
      };
    if (!Number(debtAmount)) return { isLoading: false, value: BigNumber.from(0) };

    return {
      isLoading: false,
      value: BigNumber.from(parseUnits(debtAmount, 18))
        .add(troveDetail.liquidationReserve)
        .add(borrowingFee.value),
    };
  }, [borrowingFee.isLoading, borrowingFee.value, debtAmount, troveDetail.liquidationReserve]);

  const collateralRatio = useMemo(() => {
    if (collateralGMUPrice.isLoading || totalDebt.isLoading) {
      return { isLoading: true, value: BigNumber.from(0) };
    }
    if (!Number(debtAmount) || !Number(collateralValue))
      return { isLoading: false, value: BigNumber.from(0) };

    const collateralValueBN = BigNumber.from(parseUnits(collateralValue, tokenDecimals));
    const ratio = collateralValueBN.mul(collateralGMUPrice.value).mul(100).div(totalDebt.value);

    return {
      isLoading: false,
      value: ratio,
    };
  }, [debtAmount, tokenDecimals, collateralValue, collateralGMUPrice, totalDebt]);

  const isCollateralRatioBelowMin = useMemo(() => {
    if (isRecoveryMode.isLoading) return false;
    if (!Number(debtAmount) || !Number(collateralValue)) return false;
    const crData = crParameters(troveDetail.trove, activeChainId);
    return !isRecoveryMode.value
      ? collateralRatio.value.lte(DECIMALS_18.mul(crData.mcr).div(1000))
      : collateralRatio.value.lte(DECIMALS_18.mul(crData.ccr).div(1000));
  }, [
    isRecoveryMode.isLoading,
    isRecoveryMode.value,
    debtAmount,
    collateralValue,
    troveDetail.trove,
    activeChainId,
    collateralRatio.value,
  ]);

  const isDebtBelowMinNetDebt = useMemo(() => {
    const bn = BigNumber.from(parseUnits(debtAmount, 18));
    if (bn.eq(0) && BigNumber.from(parseUnits(collateralValue, 18)).eq(0)) return false;
    return bn.lt(troveDetail.minDebtAmount);
  }, [debtAmount, collateralValue, troveDetail.minDebtAmount]);

  const liquidationPrice = useMemo(() => {
    if (collateralGMUPrice.isLoading || totalDebt.isLoading)
      return { isLoading: true, value: BigNumber.from(0) };

    const collateralAmountBN = BigNumber.from(parseUnits(collateralValue, 18));
    const liquidationCR = DECIMALS_18.mul(1099999).div(10000);

    if (collateralGMUPrice.value.lte(0) || collateralAmountBN.lte(0))
      return {
        isLoading: false,
        value: BigNumber.from(0),
      };

    return {
      isLoading: false,
      value: liquidationCR.mul(totalDebt.value).div(collateralAmountBN).div(100),
    };
  }, [collateralValue, totalDebt, collateralGMUPrice]);

  const handleCheck = () => {
    setCheckedLevarage(!checkedLevarage);
  };

  const onCollateralValueChange = async (val: string) => {
    if (val === '' || collateralGMUPrice.value.lte(0) || collateralGMUPrice.isLoading) {
      setCollateralValue('0');
      return;
    }

    const check: boolean = ValidateNumber(val);
    if (!check) return;
    setCollateralValue(check ? val : String(Number(val)));
    if (Number(debtAmount)) return;
    const valueInNumber: number = Number(val);
    if (!valueInNumber) return;

    setdebtAmount(getDisplayBalance(troveDetail.minDebtAmount, 18, 18));
  };

  const onDebtAmounChange = async (val: string) => {
    if (val === '' || collateralGMUPrice.value.lte(0) || collateralGMUPrice.isLoading) {
      setdebtAmount('0');
      return;
    }

    const check: boolean = ValidateNumber(val);
    if (!check) return;
    setdebtAmount(check ? val : String(Number(val)));
  };

  const resetFields = () => {
    onCollateralValueChange('0');
    setdebtAmount('0');
  };

  return (
    <div>
      <ConfirmLoanModal
        selectedCollateralCoin={troveDetail.depositToken}
        selectedContractPrefix={troveDetail.contractPrefix}
        openModal={openConfirmationModal}
        swapAmount={BigNumber.from(0)}
        entryPrice={collateralGMUPrice.value}
        collateralValue={BigNumber.from(parseUnits(collateralValue, 18))
          .mul(collateralGMUPrice.value)
          .div(DECIMALS_18)}
        collateralAmount={BigNumber.from(parseUnits(collateralValue, tokenDecimals))}
        liquidationPrice={liquidationPrice.value}
        leverage={0}
        collateralRatio={collateralRatio.value}
        borrowingFee={borrowingFee.value}
        totalDebt={BigNumber.from(parseUnits(debtAmount, 18))
          .add(borrowingFee.value)
          .add(troveDetail.liquidationReserve)}
        liquidationReserve={troveDetail.liquidationReserve}
        borrowingFeeRate={borrowingFeeRate.value}
        onClose={() => setOpenConfirmationModal(false)}
        onSuccess={() => {
          window.location.href = '/#/loans/your-loans';
          resetFields();
        }}
      />
      {troveIsAlreadyPresent.value && (
        <div className="m-b-12 m-t-12">
          <InfoTip
            type={'Info'}
            msg={
              <div>
                You already have an open loan for {troveDetail.trove.toUpperCase()}. &nbsp;
                <a
                  className={'links'}
                  href={`/#/loan/details/${account || ''}/${troveDetail.depositToken}`}
                >
                  view loan
                </a>
              </div>
            }
          />
        </div>
      )}
      <InfoTip
        type={'Warning'}
        msg={
          <div>
            Please understand the price mechanics of ARTH before opening a loan. ARTH's price is
            stable in the short term but appreciates over the long-term. Read{' '}
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://docs.arthcoin.com/target-price-for-arth"
              className={'links bold'}
            >
              the documentation
            </a>{' '}
            for more info.
          </div>
        }
        className={'m-b-12'}
      />
      <CustomInputContainer
        ILabelValue={'Enter Collateral'}
        IBalanceValue={`${getDisplayBalance(collateralBalance.value, tokenDecimals)}`}
        isBalanceLoading={collateralBalance.isLoading}
        ILabelInfoValue={''}
        disabled={collateralBalance.isLoading || troveIsAlreadyPresent.value}
        DefaultValue={collateralValue.toString()}
        LogoSymbol={troveDetail.depositToken}
        hasDropDown={false}
        SymbolText={troveDetail.depositToken}
        inputMode={'numeric'}
        setText={(val: string) => {
          onCollateralValueChange(val);
        }}
        tagText={'MAX'}
        tokenDecimals={tokenDecimals}
        errorCallback={(flag: boolean) => {
          setIsInputFieldError(flag);
        }}
      />
      <div className="single-line-center-center m-t-12 m-b-12">
        <IconLoader iconName={'ArrowTailDown'} iconType={'arrow'} />
      </div>
      <CustomInputContainer
        ILabelValue={'Enter Debt Amount'}
        IBalanceValue={`${getDisplayBalance(arthBalance.value, 18)}`}
        isBalanceLoading={arthBalance.isLoading}
        DefaultValue={debtAmount.toString()}
        ILabelInfoValue={''}
        LogoSymbol={'ARTH'}
        disabled={arthBalance.isLoading || troveIsAlreadyPresent.value}
        hasDropDown={false}
        SymbolText={'ARTH'}
        setText={(val: string) => onDebtAmounChange(val)}
        tokenDecimals={18}
      />
      {isCollateralRatioBelowMin && (
        <div className="m-b-12 m-t-12">
          <InfoTip
            type={'Warning'}
            msg={
              !isRecoveryMode.value
                ? `Collateral ratio must be at least ${
                    getDisplaycrParameters(troveDetail.trove, activeChainId).mcr
                  }%.`
                : `Collateral ratio must be at least ${
                    getDisplaycrParameters(troveDetail.trove, activeChainId).ccr
                  }% in Recovery Mode.`
            }
          />
        </div>
      )}
      {isDebtBelowMinNetDebt && (
        <div className="m-b-12 m-t-12">
          <InfoTip
            type={'Warning'}
            msg={`Minimum debt must be at least ${Number(
              getDisplayBalance(troveDetail.minDebtAmount, 18, 3),
            ).toLocaleString()}`}
          />
        </div>
      )}
      <div className="m-b-12 m-t-12">
        <InfoTip
          type={'Warning'}
          msg={
            <div>
              Please make sure you understand the risks of opening a loan. Read the{' '}
              <a
                target="_blank"
                rel="noopener noreferrer"
                href="https://docs.arthcoin.com/protocol/risks-and-security"
                className={'links bold'}
              >
                documentation on risks
              </a>{' '}
              for more information.
            </div>
          }
        />
      </div>
      <div className="m-t-24">
        <div className="single-line-center-between m-b-8">
          <div className="single-line-center-start">
            <TextWrapper text={'Collateral Ratio'} fontSize={12} />
            <ToolTip
              toolTipText={`The ratio between the ARTH value of the collateral and the debt (in ARTH) you are depositing.
              While the Minimum Collateral Ratio is ${
                getDisplaycrParameters(troveDetail.trove, activeChainId).mcr
              }%
              during normal operation, it is recommended to keep the Collateral Ratio always above
              ${
                getDisplaycrParameters(troveDetail.trove, activeChainId).ccr
              }%% to avoid liquidation under Recovery Mode.
              A Collateral Ratio above 200% or 250% is recommended for additional safety.`}
            />
          </div>
          <div className="single-line-center-start">
            {collateralRatio.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={
                  Number(getDisplayBalance(collateralRatio.value, 18, 3)).toLocaleString() + '%'
                }
                fontSize={12}
                Fcolor={
                  Number(getDisplayBalance(collateralRatio.value, 18, 3)) >=
                  getDisplaycrParameters(troveDetail.trove, activeChainId).mcr
                    ? theme.color.green[300]
                    : theme.color.red[300]
                }
              />
            )}
          </div>
        </div>
        <div className="single-line-center-between m-b-8">
          <div className="single-line-center-start">
            <TextWrapper text={'One-time Fee'} fontSize={12} />
            <ToolTip
              toolTipText={`The One-time Fee is a one-off fee charged as a percentage of the borrowed amount (in ARTH) and is part of a loan's debt. The fee varies between 0.5% and 5% depending on ARTH redemption volumes.`}
            />
          </div>
          <div className="single-line-center-start">
            {borrowingFee.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={
                  Number(getDisplayBalance(borrowingFee.value, 18, 3)).toLocaleString() +
                  ' ARTH'
                }
                fontSize={12}
              />
            )}
          </div>
        </div>
        <div className="single-line-center-between">
          <div className="single-line-center-start">
            <TextWrapper text={'Liquidation Reserve'} fontSize={12} />
            <ToolTip
              toolTipText={
                'An amount set aside to cover the liquidator’s gas costs if your loan needs to be liquidated. The amount increases your debt and is refunded if you close your loan by fully paying off its net debt.'
              }
            />
          </div>
          <div className="single-line-center-start">
            <TextWrapper
              text={
                Number(
                  getDisplayBalance(troveDetail.liquidationReserve, 18, 3),
                ).toLocaleString() + ' ARTH'
              }
              fontSize={12}
            />
          </div>
        </div>
        <div className="single-line-center-between m-t-12">
          <div className="single-line-center-start">
            <TextWrapper text={'Total Debt'} fontSize={12} />
            <ToolTip
              toolTipText={
                'The Total Debt your loan will hold. This amount is inclusive of the amount you will recieve, borrowing fee and the liquidation reserve set aside.'
              }
            />
          </div>
          <div className="single-line-center-start">
            {totalDebt.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={
                  Number(getDisplayBalance(totalDebt.value, 18, 3)).toLocaleString() + ' ARTH'
                }
                fontSize={12}
                Fcolor={theme.color.green[300]}
              />
            )}
          </div>
        </div>
      </div>
      <div className="m-t-32">
        <InfoTip
          type={'Warning'}
          msg={`Please make sure you some ${troveDetail.trove} left over to pay for gas fees while opening a loan`}
          className={'m-b-8'}
        />
        <Button
          disabled={
            troveIsAlreadyPresent.value ||
            isInputFieldError ||
            !Number(debtAmount) ||
            !Number(collateralValue) ||
            isCollateralRatioBelowMin ||
            isDebtBelowMinNetDebt
          }
          tracking_id="take_loan"
          tracking_params={{
            collateral: troveDetail.trove,
            action: 'initial',
            amount: Number(collateralValue),
          }}
          onClick={() => setOpenConfirmationModal(true)}
        >
          Take Loan
        </Button>
      </div>
    </div>
  );
};

export default AddLoanSection;

const CheckboxDiv = styled.div`
  background: rgba(255, 255, 255, 0.08);
  border-radius: 6px;
  padding: 6px 12px 6px 12px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: rgba(255, 255, 255, 0.88);
  margin: 15px 0 0 0;
`;
