import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useWallet } from 'use-wallet';

import { AppState } from '../../../state';
import { useBlockNumber } from '../../../state/application/hooks';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (token: string) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();

  const entireSystemDebt = useSelector((app: AppState) => app.analytics.entireSystemDebt);

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    setValue({ isLoading: false, value: entireSystemDebt[token] || BigNumber.from(0) });
  }, [account, entireSystemDebt, token]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch system debt: ${err}`));
    }
  }, [core.isUnlocked, account, blockNumber, fetchValue]);

  return value;
};
