import React, { useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useWallet } from 'use-wallet';

import Grid from '@material-ui/core/Grid';

import Container from '../../components/Container';
import config from '../../config';
import useCore from '../../hooks/useCore';
import { useGetActiveChainId } from '../../state/chains/hooks';
import theme from '../../theme';
import IconLoader from '../../theme/components/IconLoader';
import PageHeader from '../../theme/components/PageHeader';
import TextWrapper from '../../theme/components/TextWrapper';
import LoanInfo from '../LoanInfo/LoanInfo';

import RedeemTabContent from './components/Redeem';
import RedeemDesc from './components/RedeemDesc';

const Redeem = () => {
  // @ts-ignore
  const { id } = useParams();
  const [trove, setTrove] = useState<string>('');
  const chainId = useGetActiveChainId();
  const { account } = useWallet();

  const core = useCore();
  const isTroveValid: boolean = useMemo(() => {
    const troves = core.getAllTroves(chainId, account);
    if (troves.includes(id)) {
      setTrove(id);
      return true;
    } else {
      return false;
    }
  }, [core, chainId, account, id]);

  if (!isTroveValid) {
    window.location.href = `/#/redeem/${config[chainId].defaultCollateral}`;
    return <div />;
  }

  return (
    <>
      <Container size="lg">
        <div className="m-b-40">
          {/*<div className={'m-b-20 single-line-center-start'}>
            <a href={'/#/redeem'}>
              <TextWrapper text={'Redeem'} Fcolor={theme.color.transparent[200]} />
            </a>
            <IconLoader iconName={'ArrowRight'} iconType={'arrow'} className={'m-r-4 m-l-4'} />
            <TextWrapper text={`${trove} Pool`} Fcolor={theme.color.transparent[100]} />
          </div>*/}
          <PageHeader
            title={`Redeem ARTH from ${trove} loans`}
            subTitle={`For every redeemption there is a fee charged in ${trove} and MAHA.`}
            showLearnMore={true}
            learnMoreLink={'https://docs.arthcoin.com/protocol/redeeming-arth#redemption-fees'}
          />
        </div>
        <Grid container className="m-t-24" spacing={2}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <RedeemTabContent trove={trove} />
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <RedeemDesc />
            <LoanInfo trove={trove} />
          </Grid>
        </Grid>
      </Container>
    </>
  );
};

export default Redeem;
