import copyText from 'copy-to-clipboard';
import { BigNumber } from 'ethers';
import React, { useEffect, useMemo, useState } from 'react';
import Loader from 'react-spinners/BeatLoader';
import styled from 'styled-components';
import { useWallet } from 'use-wallet';

import MetamaskImg from '../../../../../assets/images/Metamask.svg';
import useGetNativeTokenBalance from '../../../../../hooks/state/useGetNativeTokenBalance';
import useTokenBalance from '../../../../../hooks/state/useTokenBalance';
import useCore from '../../../../../hooks/useCore';
import ERC20 from '../../../../../protocol/ERC20';
import { useGetActiveChainId } from '../../../../../state/chains/hooks';
import theme from '../../../../../theme';
import Button from '../../../../../theme/components/Button';
import ConfirmationModal from '../../../../../theme/components/ConfirmationModal';
import IconLoader from '../../../../../theme/components/IconLoader';
import TextButton from '../../../../../theme/components/TextButton';
import TextWrapper from '../../../../../theme/components/TextWrapper';
// import { DECIMALS_18 } from '../../../../../protocol/constants';
import { truncateMiddle } from '../../../../../utils';
import { getDisplayBalance } from '../../../../../utils/formatBalance';

// import useMAHAOraclePrice from '../../../../../hooks/state/controller/useMAHAPrice';

const WalletInfo = () => {
  const [isCopied, setIsCopied] = useState<boolean>(false);
  const [showConfirmationModal, setShowConfirmationModal] = useState<boolean>(false);

  const { account } = useWallet();
  const core = useCore();
  const activeChainId = useGetActiveChainId();

  const maha = core.tokens('MAHA', activeChainId);
  const arth = core.tokens('ARTH', activeChainId);
  const arthusd = core.tokens('ARTH.usd', activeChainId);
  const mahaBal = useTokenBalance(maha);
  const arthBal = useTokenBalance(arth);
  const nativeTokenBal = useGetNativeTokenBalance();

  useEffect(() => {
    if (isCopied) {
      const hide = setTimeout(() => {
        setIsCopied(false);
      }, 500);

      return () => {
        clearTimeout(hide);
      };
    }
  }, [isCopied, setIsCopied]);

  const addTokenToMetaMask = (data: ERC20) => {
    const params = {
      type: 'ERC20',
      options: {
        address: data.address,
        symbol: data.symbol,
        decimals: data.decimal,
      },
    };

    // @ts-ignore
    window.ethereum
      .request({ method: 'wallet_watchAsset', params })
      .then(() => console.log('added'))
      .catch((error: Error) => console.log('error', error));
  };

  return (
    <MainContainer>
      <ConfirmationModal
        modalOpen={showConfirmationModal}
        onClose={() => setShowConfirmationModal(false)}
        modalTitle={'Disconnect Wallet'}
        title={`Are you sure you want to disconnect ${truncateMiddle(account || '', 15)}?`}
        subtitle={`${account || ''}`}
        yesText={'Disconnect'}
        noText={'Cancel'}
        yesAction={() => {
          setShowConfirmationModal(false);
          localStorage.setItem('disconnectWallet', '1');
          window.location.reload();
        }}
        noAction={() => setShowConfirmationModal(false)}
      />
      <WalletHeader className="single-line-center-between bottom-divider">
        <TextWrapper text={'Your Account'} fontWeight={600} fontSize={16} />
        <div className="single-line-center-start">
          <img src={MetamaskImg} height={32} className="m-r-8" alt="metamask" />
          <TextWrapper
            text={`${truncateMiddle(account || '', 12, '...')}`}
            fontWeight={600}
            fontSize={16}
            className="m-r-8"
          />
          {isCopied ? (
            <IconLoader iconName={'Copied'} />
          ) : (
            <IconLoader
              iconName={'Copy'}
              onClick={() => {
                const didCopy = copyText(account?.toString() || '');
                setIsCopied(didCopy);
              }}
            />
          )}
        </div>
      </WalletHeader>
      <WalletBody>
        <div className="single-line-center-between m-b-20">
          <div className="single-line-center-start">
            <IconLoader
              iconName={`${core.config('blockchainToken', activeChainId)}`}
              iconType="tokenSymbol"
              className="m-r-12"
              height={44}
              width={44}
            />
            {arthBal.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={`${Number(
                  getDisplayBalance(nativeTokenBal.value),
                ).toLocaleString()} ${core.config('blockchainToken', activeChainId)}`}
                fontWeight={600}
              />
            )}
          </div>
        </div>
        <div className="single-line-center-between m-b-20">
          <div className="single-line-center-start">
            <IconLoader
              iconName={'MAHA'}
              iconType="tokenSymbol"
              className="m-r-12"
              height={44}
              width={44}
            />
            {mahaBal.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={`${Number(
                  getDisplayBalance(mahaBal.value, 18, 3),
                ).toLocaleString()} MAHA`}
                fontWeight={600}
              />
            )}
          </div>
          <TextButton onClick={() => addTokenToMetaMask(maha)} text={'Add to metamask'} />
        </div>
        <div className="single-line-center-between m-b-20">
          <div className="single-line-center-start">
            <IconLoader
              iconName={'ARTH'}
              iconType="tokenSymbol"
              className="m-r-12"
              height={44}
              width={44}
            />
            {arthBal.isLoading ? (
              <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
            ) : (
              <TextWrapper
                text={`${Number(getDisplayBalance(arthBal.value)).toLocaleString()} ARTH`}
                fontWeight={600}
              />
            )}
          </div>
          <TextButton onClick={() => addTokenToMetaMask(arth)} text={'Add to metamask'} />
        </div>
        <Button variant={'transparent'} onClick={() => setShowConfirmationModal(true)}>
          Disconnect
        </Button>
      </WalletBody>
    </MainContainer>
  );
};

export default WalletInfo;

const MainContainer = styled.div`
  width: 100%;
`;

const WalletHeader = styled.div`
  padding-bottom: 16px;
`;

const WalletBody = styled.div`
  padding: 24px 0 0 0;
`;
