import moment from 'moment';
import React, { useMemo } from 'react';
import Loader from 'react-spinners/BeatLoader';

import useUpdatePrice from '../../../hooks/callbacks/updateOracle/useUpdatePrice';
import useGetNextUpdateTime from '../../../hooks/state/updateOracle/useGetNextUpdateTime';
import theme from '../../../theme';
import Button from '../../../theme/components/Button';
import ProgressBar from '../../../theme/components/ProgressBar';
import TextWrapper from '../../../theme/components/TextWrapper';

const UpdateOraclePrice = () => {
  const time = useGetNextUpdateTime();

  const daysUntil = (dateToCheckAgainst: number) => {
    return moment().to(moment(dateToCheckAgainst));
  };

  const updateAction = useUpdatePrice();

  const handleUpdate = () => {
    updateAction(() => {});
  };

  const percPassed = useMemo(() => {
    const nextTime = time.value;
    const currentTime = new Date().getTime();
    const difference = nextTime - currentTime;
    const hoursLeft = difference / (1000 * 60 * 60);
    const hoursPassedAway = 24 - hoursLeft;
    return (hoursPassedAway / 24) * 100;
  }, [time.value]);

  return (
    <div className={'material-primary'}>
      <TextWrapper
        text={'The pricefeed runs on a 24 hour epoch. Advancing the epoch will award 10 MAHA.'}
        className={'m-b-24'}
      />

      <div style={{}}>
        <Button
          disabled={new Date().getTime() < time.value || time.isLoading}
          text={'Advance Epoch'}
          onClick={handleUpdate}
        />
      </div>
      <div className={'m-t-32'}>
        <ProgressBar
          barColor={theme.color.green[300]}
          percent={percPassed}
          width={'100%'}
          className={'m-b-8'}
        />
        <div className={'single-line-center-end'}>
          {time.isLoading ? (
            <Loader color={'#ffffff'} loading={true} size={2} margin={2} />
          ) : (
            <TextWrapper
              text={`Next epoch in ${daysUntil(time.value)}`}
              fontSize={12}
              Fcolor={theme.color.transparent[100]}
            />
          )}
        </div>
      </div>
    </div>
  );
};

export default UpdateOraclePrice;
