import * as Sentry from '@sentry/react';
import { BigNumber } from 'ethers';
import { useCallback } from 'react';

import { REDEEM_MAX_ITERATIONS } from '../../../protocol/constants';
import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import { Decimal, decimalify } from '../../../utils/Decimal';
import { getFees } from '../../../utils/fees';
import { getDisplayBalance } from '../../../utils/formatBalance';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import { troveDetails } from '../../../utils/tokenDetails';
import { findRedemptionHints } from '../../state/hinthelpers/helper';
import useCore from '../../useCore';

export default function (trove: string, arthAmount: BigNumber, redemptionRate: BigNumber) {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);

  return useCallback(
    async (callback: () => void, errorHandler: () => void): Promise<void> => {
      try {
        const priceFeed = core.getPriceFeed(troveDetail.contractPrefix, activeChainId);
        const hintHelpers = core.getHintHelper(troveDetail.contractPrefix, activeChainId);

        const troveManager = core.getTroveManager(troveDetail.contractPrefix, activeChainId);
        const sortedTroves = core.getSortedTroves(troveDetail.contractPrefix, activeChainId);
        const price = await priceFeed.callStatic.fetchPrice();

        const [
          fees,
          total,
          [truncatedAmount, firstRedemptionHint, ...partialHints],
        ] = await Promise.all([
          getFees(troveManager, core, troveDetail.depositToken, price, activeChainId),
          core.getTotal(troveDetail.contractPrefix, activeChainId),
          findRedemptionHints(
            arthAmount,
            sortedTroves,
            hintHelpers,
            troveManager,
            priceFeed,
            core.myAccount,
          ),
        ]);

        if (truncatedAmount.isZero()) {
          addPopup({
            error: {
              message: `Amount too low to redeem`,
              stack: '',
            },
          });
          return;
        }

        const defaultRedemptionRateSlippageTolerance = Decimal.from(0.001); // 0.1%

        const defaultMaxRedemptionRate = (amount: Decimal) =>
          Decimal.min(
            fees
              .redemptionRate(amount.div(total.debt))
              .add(defaultRedemptionRateSlippageTolerance),
            Decimal.ONE,
          );

        // const maxRedemptionRate = decimalify(redemptionRate)
        const maxRedemptionRateOrDefault = defaultMaxRedemptionRate(
          decimalify(truncatedAmount),
        );

        const response = await troveManager.redeemCollateral(
          truncatedAmount,
          firstRedemptionHint,
          ...partialHints.map((d) => d.toString()),
          REDEEM_MAX_ITERATIONS,
          maxRedemptionRateOrDefault.hex,
        );

        if (response.status !== 1) {
          errorHandler();
          Sentry.captureException({
            ...partialHints.map((d) => d.toString()),
            response,
            truncatedAmount,
            firstRedemptionHint,
            REDEEM_MAX_ITERATIONS,
          });
        }

        addTransaction(response, {
          summary: `Redeem ${Number(
            getDisplayBalance(truncatedAmount, 18, 3),
          ).toLocaleString()} ARTH for ${trove.toUpperCase()}`,
        });

        if (response.status !== 1) {
          Sentry.captureException({
            name: 'useRedeemCollateral',
            response,
            trove,
            arthAmount,
          });
        }

        const tx = await response.wait();
        if (errorHandler && tx.status !== 1) errorHandler();
        if (callback && tx.status === 1) callback();
      } catch (e) {
        Sentry.captureException({
          name: 'useRedeemCollateral',
          ...e,
        });
        if (errorHandler) errorHandler();

        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [
      activeChainId,
      addPopup,
      addTransaction,
      arthAmount,
      core,
      trove,
      troveDetail.contractPrefix,
      troveDetail.depositToken,
    ],
  );
}
