import * as Sentry from '@sentry/react';
import { BigNumber, Contract } from 'ethers';
import Numeral from 'numeral';
import { useCallback } from 'react';

import ERC20 from '../../../protocol/ERC20';
import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import { getDisplayBalance } from '../../../utils/formatBalance';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import useCore from '../../useCore';

export default function (wrappedTokenName: string, amount: BigNumber, depositToken: ERC20) {
  const core = useCore();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();
  const chainId = useGetActiveChainId();

  const action = useCallback(
    async (callback?: () => void): Promise<void> => {
      const contract = core.contracts(wrappedTokenName, chainId);

      try {
        const response = await contract.deposit(amount);

        addTransaction(response, {
          summary: `Deposit ${Numeral(
            getDisplayBalance(amount, depositToken.decimal, 3),
          ).format(
            '0,0.00a',
          )} ${depositToken.symbol.toUpperCase()} into ${wrappedTokenName.toUpperCase()}`,
        });

        if (response.status !== 1) {
          Sentry.captureException({
            name: 'useTokenizationDeposit',
            response,
            wrappedTokenName,
            amount,
            depositToken,
          });
        }

        if (callback) callback();
      } catch (e) {
        Sentry.captureException({
          name: 'useTokenizationDeposit',
          ...e,
        });
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e.stack,
          },
        });
      }
    },
    [core, wrappedTokenName, chainId, amount, addTransaction, depositToken, addPopup],
  );

  return action;
}
