import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import BasisCash from '../../protocol';
import { DECIMALS_18, PAGINATION_PAGE_SIZE } from '../../protocol/constants';
import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: Array<any>;
};

const fetchForCollaterals = async (
  core: BasisCash,
  pageNo: number = 0,
  collaterals: string[],
  activeChainId: number,
) => {
  const finalTroves = [];

  const index = Number(pageNo);

  const pageIndex = index === 0 ? -1 : -1 - index * PAGINATION_PAGE_SIZE;
  /*
    index 0 pageindex -1
    index 1 pageindex -5
    index 2 pageindes -10
   */
  for (let i = 0; i < collaterals.length; i++) {
    const collateral = collaterals[i];
    const troveContract = core.getTroveManager(collateral, activeChainId);

    const multiTroveGetterContract = core.getMultiTroveGetter(collateral, activeChainId);
    const troves = await multiTroveGetterContract.getMultipleSortedTroves(
      pageIndex,
      PAGINATION_PAGE_SIZE,
    );

    const collateralPriceFeedContract = core.getPriceFeed(collateral, activeChainId);
    const collateralPrice = await collateralPriceFeedContract.callStatic.fetchPrice();

    const recoveryMode = await troveContract.checkRecoveryMode(collateralPrice);
    const tcr = await troveContract.getTCR(collateralPrice);

    const spContract = core.getStabilityPool(collateral, activeChainId);
    const arthInSp = await spContract.getTotalARTHDeposits();

    for (const trove of troves) {
      if (trove.coll.lte(0) && trove.debt.lte(0)) continue;

      const pendingETHReward: any =
        (await troveContract.getPendingETHReward(trove.owner)) || BigNumber.from(0);
      const pendingARTHDebtReward: any =
        (await troveContract.getPendingARTHDebtReward(trove.owner)) || BigNumber.from(0);

      let coll: any = trove.coll;
      let debt: any = trove.debt;
      if (pendingETHReward && pendingETHReward.gt(0)) coll = coll.add(pendingETHReward);
      if (pendingARTHDebtReward && pendingARTHDebtReward.gt(0))
        debt = debt.add(pendingARTHDebtReward);

      const cr = debt.lte(0)
        ? BigNumber.from(0)
        : coll.mul(collateralPrice).mul(100).div(debt).div(DECIMALS_18);

      finalTroves.push({
        id: `${Date.now() + Math.random()}-${trove.owner}`, // Reduces the chance of getting same id.
        token: collateral,
        owner: trove.owner,
        deposit: coll,
        debt: debt,
        isLiquidateable: recoveryMode
          ? cr.lt(tcr.mul(100).div(DECIMALS_18)) && arthInSp.gte(debt)
          : cr.lt(110),
        collateralRatio: cr,
      });
    }
  }

  return finalTroves;
};

export default (token: string, pageNo: number = 5, doRefresh: boolean) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: [] });

  const core = useCore();
  const { account } = useWallet();
  const activeChainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: [] });
      return;
    }

    const collateralTroveMapping = await fetchForCollaterals(
      core,
      pageNo,
      [token],
      activeChainId,
    );
    setValue({ isLoading: false, value: collateralTroveMapping });
  }, [account, token, pageNo, core, activeChainId]);

  useEffect(() => {
    setValue({ isLoading: true, value: [] });
  }, [token, pageNo, doRefresh]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch all loans: ${err.message}`, err),
      );
    }
  }, [
    core.isUnlocked,
    doRefresh,
    account,
    token,
    pageNo,
    core.myAccount,
    fetchValue,
    activeChainId,
  ]);

  return value;
};
