import mixpanel from 'mixpanel-browser';

//ToDo: add 'REACT_APP_ISPROD=true' in script for app.arthcoin.com in jenkins
export const isProduction = process.env.REACT_APP_ISPROD === 'true';

if (isProduction) {
  mixpanel.init('b646039d2dd5e1e09deebb592cd041b1');
} else {
  mixpanel.init('84c19ce0c48dff4b147960872628919a');
}

let env_check = true;

let actions = {
  identify: (id) => {
    if (env_check) {
      mixpanel.identify(id);
    }
  },
  alias: (id) => {
    if (env_check) mixpanel.alias(id);
  },
  track: (name, props) => {
    if (env_check) {
      mixpanel.track(name, props);
    }
  },
  people: {
    set: (props) => {
      if (env_check) mixpanel.people.set(props);
    },
  },
};

export let Mixpanel = actions;
