import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import styled from 'styled-components';

import { Grid } from '@material-ui/core';

import Container from '../../components/Container';
import useCore from '../../hooks/useCore';
import { TokeniseStaticInfo } from '../../protocol/config';
import { useGetActiveChainId } from '../../state/chains/hooks';
import theme from '../../theme';
import IconLoader from '../../theme/components/IconLoader';
import PageHeader from '../../theme/components/PageHeader';
import TextWrapper from '../../theme/components/TextWrapper';
import { VfatLink } from '../Farming';

import TokenisationCard from './components/TokenisationCard';
import Deposit from './modals/Deposit';
import Withdraw from './modals/Withdraw';

const Tokenization = () => {
  const core = useCore();
  const chainId = useGetActiveChainId();
  const tokenziseInfo = core.getAllTokeniseInfo(chainId);

  // parse the URL to automatically open any popup
  const { search } = useLocation();
  useEffect(() => {
    const query = new URLSearchParams(search);
    const toOpen = query.get('open');
    const found = tokenziseInfo.find((r) => r.tokeniseToken === toOpen);
    if (found) onDepositPress(found);
  }, [search, tokenziseInfo]);

  const [openDeposit, setOpenDeposit] = useState<boolean>(false);
  const [openWithdraw, setOpenWithdraw] = useState<boolean>(false);

  const [selectedData, setSelectedData] = useState<TokeniseStaticInfo | null>(null);

  const onDepositPress = (data: TokeniseStaticInfo) => {
    setSelectedData(data);
    setOpenDeposit(true);
  };

  const onWithdrawPress = (data: TokeniseStaticInfo) => {
    setSelectedData(data);
    setOpenWithdraw(true);
  };

  const onDepositClose = () => {
    setSelectedData(null);
    setOpenDeposit(false);
  };

  const onWithdrawClose = () => {
    setSelectedData(null);
    setOpenWithdraw(false);
  };

  return (
    <Container size="lg">
      <PageHeader
        title={'TOKENIZE'}
        subTitle={'Tokenize ARTH, Staking pools, farms, vaults into fungible tokens.'}
        showLearnMore={true}
        learnMoreLink={'https://docs.arthcoin.com/protocol/tokenized-pools'}
      />
      <div className={'mo-custom-container'}>
        <Grid container className="m-t-24" spacing={2}>
          {tokenziseInfo.map((data) => {
            return (
              <Grid item lg={4} md={4} sm={12} xs={12}>
                <TokenisationCard
                  tokenziseInfo={data}
                  onDeposit={() => onDepositPress(data)}
                  onWithdraw={() => onWithdrawPress(data)}
                />
              </Grid>
            );
          })}
        </Grid>
      </div>

      {selectedData !== null && (
        <Deposit open={openDeposit} onClose={onDepositClose} tokenziseInfo={selectedData} />
      )}
      {selectedData !== null && (
        <Withdraw open={openWithdraw} onClose={onWithdrawClose} tokenziseInfo={selectedData} />
      )}
    </Container>
  );
};

export default Tokenization;
