import { BigNumber } from 'ethers';
import React, { useMemo } from 'react';

import Grid from '@material-ui/core/Grid';

import useCloseTrove from '../../../hooks/callbacks/borrowerOperations/useCloseTrove';
import useApprove, { ApprovalState } from '../../../hooks/callbacks/useApprove';
import useTokenBalance from '../../../hooks/state/useTokenBalance';
import useCore from '../../../hooks/useCore';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import InfoTip from '../../../theme/components/InfoTip';
import Modal from '../../../theme/components/Modal';
import TextWrapper from '../../../theme/components/TextWrapper';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { troveDetails } from '../../../utils/tokenDetails';

interface Iprops {
  getTrove: any;
  trove: string;
  openModal: boolean;
  onClose: () => void;
  onSuccess: () => void;
}

const ConfirmCloseLoanModal = (props: Iprops) => {
  const { getTrove, openModal, onClose, onSuccess, trove } = props;

  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);
  const arthBalance = useTokenBalance(core.tokens('ARTH', activeChainId));

  const [arthApprovalStatus, approveARTH] = useApprove(
    core.tokens('ARTH', activeChainId),
    core.getController(troveDetail.contractPrefix, activeChainId)?.address || undefined,
  );

  const closeTrove = useCloseTrove(troveDetail.contractPrefix.toUpperCase());
  const isARTHApproved = useMemo(() => arthApprovalStatus === ApprovalState.APPROVED, [
    arthApprovalStatus,
  ]);
  const isARTHApproving = useMemo(() => arthApprovalStatus === ApprovalState.PENDING, [
    arthApprovalStatus,
  ]);

  const balanceNeededAmount = useMemo(() => {
    if (arthBalance.isLoading)
      return { isLoading: true, value: BigNumber.from(0), amountNeededMore: BigNumber.from(0) };
    if (getTrove.debt.lte(0))
      return {
        isLoading: false,
        value: BigNumber.from(0),
        amountNeededMore: BigNumber.from(0),
      };

    return {
      isLoading: false,
      value: getTrove.debt.sub(troveDetail.liquidationReserve),
      amountNeededMore: arthBalance.value.lt(getTrove.debt.sub(troveDetail.liquidationReserve))
        ? getTrove.debt.sub(troveDetail.liquidationReserve).sub(arthBalance.value)
        : BigNumber.from(0),
    };
  }, [arthBalance.isLoading, arthBalance.value, getTrove.debt, troveDetail.liquidationReserve]);

  const handleCloseTrove = () => {
    closeTrove(() => {
      onSuccess();
      onClose();
    });
  };

  return (
    <Modal
      title={'Confirm Close Loan'}
      open={openModal}
      handleClose={() => onClose()}
      closeButton
    >
      <div>
        <div className="single-line-center-between m-b-12">
          <TextWrapper text={'Your collateral amount'} fontWeight={600} Fcolor={'#FFFFFF88'} />
          <div className="single-line-center-start">
            <TextWrapper
              text={Number(getDisplayBalance(getTrove.coll, 18, 3)).toLocaleString()}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="tags m-l-4">
              <TextWrapper
                text={troveDetail.depositToken.toUpperCase()}
                fontSize={12}
                Fcolor={'#FFFFFF88'}
              />
            </div>
          </div>
        </div>
        <div className="single-line-center-between m-b-12">
          <TextWrapper text={'Your total debt amount'} fontWeight={600} Fcolor={'#FFFFFF88'} />
          <div className="single-line-center-start">
            <TextWrapper
              text={Number(getDisplayBalance(getTrove.debt, 18, 3)).toLocaleString()}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="tags m-l-4">
              <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
            </div>
          </div>
        </div>
        <div className="single-line-center-between m-b-16">
          <div className="single-line-center-start">
            <TextWrapper text={'Liquidation Reserve'} fontWeight={600} Fcolor={'#FFFFFF88'} />
          </div>
          <div className="single-line-center-start">
            <TextWrapper
              text={Number(
                getDisplayBalance(troveDetail.liquidationReserve, 18, 3),
              ).toLocaleString()}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="tags m-l-4">
              <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
            </div>
          </div>
        </div>
        <div className="single-line-center-between m-b-12">
          <TextWrapper
            text={'Your payable debt amount'}
            fontWeight={600}
            Fcolor={'#FFFFFF88'}
          />
          <div className="single-line-center-start">
            <TextWrapper
              text={Number(
                getDisplayBalance(getTrove.debt.sub(troveDetail.liquidationReserve), 18, 3),
              ).toLocaleString()}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="tags m-l-4">
              <TextWrapper text={'ARTH'} fontSize={12} Fcolor={'#FFFFFF88'} />
            </div>
          </div>
        </div>
        <div className="custom-divider" />
        <div className="single-line-center-between m-b-12">
          <TextWrapper text={'You will receive'} fontWeight={600} Fcolor={'#FFFFFF88'} />
          <div className="single-line-center-start">
            <TextWrapper
              text={Number(getDisplayBalance(getTrove.coll, 18, 3)).toLocaleString()}
              fontWeight={600}
              Fcolor={'#FFFFFF88'}
            />
            <div className="tags m-l-4">
              <TextWrapper
                text={troveDetail.depositToken.toUpperCase()}
                fontSize={12}
                Fcolor={'#FFFFFF88'}
              />
            </div>
          </div>
        </div>
        <br />
        {balanceNeededAmount.amountNeededMore.gt(0) && (
          <>
            <div className="m-b-24">
              <InfoTip
                type={'Warning'}
                msg={`You need at least ${Number(
                  getDisplayBalance(balanceNeededAmount.value, 18, 3),
                ).toLocaleString()} ARTH(${Number(
                  getDisplayBalance(balanceNeededAmount.amountNeededMore, 18, 3),
                ).toLocaleString()} ARTH more) to close your loan.`}
              />
            </div>
            <br />
          </>
        )}
        <Grid container spacing={2} className="m-b-32">
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              variant={'transparent'}
              onClick={() => onClose()}
              tracking_id="close_loan"
              tracking_params={{
                collateral: trove,
                action: 'cancel',
                amount: Number(getDisplayBalance(getTrove.coll, 18, 3)),
                other: {
                  debt: Number(getDisplayBalance(getTrove.debt, 18, 3)),
                },
              }}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            {!isARTHApproved && activeChainId === 137 ? (
              <Button
                disabled={balanceNeededAmount.amountNeededMore.gt(0)}
                loading={balanceNeededAmount.isLoading || isARTHApproving}
                onClick={approveARTH}
                tracking_id="approve_ARTH_close_loan"
                tracking_params={{
                  collateral: trove,
                }}
              >
                Approve ARTH
              </Button>
            ) : (
              <Button
                disabled={balanceNeededAmount.amountNeededMore.gt(0)}
                loading={balanceNeededAmount.isLoading}
                onClick={handleCloseTrove}
                tracking_id="close_loan"
                tracking_params={{
                  collateral: trove,
                  action: 'confirm',
                  amount: Number(getDisplayBalance(getTrove.coll, 18, 3)),
                  other: {
                    debt: Number(getDisplayBalance(getTrove.debt, 18, 3)),
                  },
                }}
              >
                Confirm Close Loan
              </Button>
            )}
          </Grid>
        </Grid>
      </div>
    </Modal>
  );
};

export default ConfirmCloseLoanModal;
