import React from 'react';
import styled from 'styled-components';

import StakedBanner from '../../components/StakedBanner';
import config from '../../config';
import { useGetActiveChainId } from '../../state/chains/hooks';
import theme from '../../theme';
import Button from '../../theme/components/Button';
import IconLoader from '../../theme/components/IconLoader';
import InfoTip from '../../theme/components/InfoTip';
import PageHeader from '../../theme/components/PageHeader';
import TextWrapper from '../../theme/components/TextWrapper';
import { switchMetamaskChain } from '../../utils/chains/function';
import AllLoans from '../AllLoans';
import { VfatLink } from '../Farming';

import GlobalData from './components/GlobalData';

type Tab = {
  index: number;
  key: string;
  text: string;
};

const Loan = () => {
  const chainId = useGetActiveChainId();

  return (
    <div className="custom-container">
      {chainId === 1 && (
        <VfatLink onClick={() => (window.location.href = '/#/stability-pool/ETH')}>
          <TextWrapper
            text={'LIVE'}
            Fcolor={theme.color.primary[300]}
            fontWeight={600}
            className={'m-r-12'}
          />
          <TextWrapper text={'Stability pool is live, visit here'} className={'m-r-12'} />
          <IconLoader iconName={'ArrowTailRight'} iconType={'arrow'} />
        </VfatLink>
      )}
      <div className="single-line-center-between mo-single-line-column">
        <PageHeader
          title={'LOANS'}
          subTitle={`Borrow ARTH at 0% interest using ${config[chainId].defaultCollateral} as collateral.`}
          showLearnMore={true}
          learnMoreLink={'https://docs.arthcoin.com/protocol/introduction-arth-loans'}
        />
        <div className="single-line-center-start">
          <ButtonContainer>
            <Button
              to={`/loan/add/${config[chainId].defaultCollateral}`}
              tracking_id={'open_loan'}
              tracking_params={{ position: 'header' }}
              disabled={chainId === 56}
            >
              <IconLoader iconName={'Add'} className="m-r-4" width={20} />
              Take Loan
            </Button>
          </ButtonContainer>
        </div>
      </div>
      {chainId === 56 && (
        <InfoTip
          type={'Warning'}
          msg={
            <div>
              Binance Smart Chain loans will be depreciated by September 27th 2022. We encourage
              users to close their loans and move their collateral to the{' '}
              <a onClick={() => switchMetamaskChain(1)} className={'links pointer'}>
                Ethereum Chain
              </a>
              . If you'd like to get ARTH, simply mint on the Ethereum chain and bridge to BSC
              using Anyswap.
            </div>
          }
          className={'m-b-12'}
        />
      )}
      <StakedBanner />
      <GlobalData />
      <AllLoans />
    </div>
  );
};

export default Loan;

const ButtonContainer = styled.div`
  margin-bottom: 40px;
  @media (max-width: 600px) {
    max-width: max-content;
    padding-left: 16px;
    margin-top: -20px;
  }
`;
